import React, { Component } from 'react';
import MenuCollection from '../../../../component/menuCollection';
// import { animations } from 'react-animation';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import { formatCurrency } from '../../../../../../commons/utils';
import { SingleDatePicker } from 'react-dates';
import { UI } from '../../../../../../components';
import { history } from '../../../../../../redux/router';
import BoxInfoRight from '../box_info_right';

import './styles.scss';
import '../flightInformation/styles.scss';
// import _ from 'lodash';

// const style = {
//   animation: animations.fadeInUp
// };

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  textField: {
    padding: '6px 12px',
    margin: '0px',
    width: '100%'
  },
  menu: {
    width: 200
  }
}));

const listSignedLuggage = [
  { id: 1, weigh: '15kg', price: 200000 },
  { id: 2, weigh: '20kg', price: 300000 },
  { id: 3, weigh: '25kg', price: 500000 }
];
const listInsurrance = [
  { id: 1, title: 'StarCare', price: 120000 },
  { id: 2, title: 'Liberty TravelCare', price: 150000 }
];

class PeopleInfomation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: '',
      signedLuggageFirst: '',
      signedLuggageLast: '',
      priceLuggageFirst: 0,
      priceLuggageLast: 0,
      insurrance: '',
      priceInsurrance: 0
    };
  }

  _onHandleChangeLuggageFirst = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    this.setState(
      {
        [name]: value
      },
      () => {
        let priceLuggageFirst = listSignedLuggage.filter(item => {
          return item.id === this.state.signedLuggageFirst;
        });
        this.setState(
          {
            priceLuggageFirst: priceLuggageFirst[0].price
          },
          () => {
            console.log('price', this.state.priceLuggageFirst);
          }
        );
      }
    );
  };
  _onHandleChange = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    this.setState(
      {
        [name]: value
      },
      () => {
        let insurrance = listInsurrance.filter(item => {
          return item.id === this.state.insurrance;
        });
        this.setState(
          {
            priceInsurrance: insurrance[0].price
          },
          () => {
            console.log('price', this.state.priceInsurrance);
          }
        );
      }
    );
  };
  _onHandleChangeLuggageLast = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    this.setState(
      {
        [name]: value
      },
      () => {
        let priceLuggageLast = listSignedLuggage.filter(item => {
          return item.id === this.state.signedLuggageLast;
        });
        this.setState(
          {
            priceLuggageLast: priceLuggageLast[0].price
          },
          () => {
            console.log('price', this.state.priceLuggageLast);
          }
        );
      }
    );
  };

  renderDropDownSignedLuggageFirst = () => {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="signedLuggageFirst"
        select
        label="Hành lý ký gửi lượt đi:"
        className={classes.textField}
        value={this.state.signedLuggageFirst}
        onChange={this._onHandleChangeLuggageFirst}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listSignedLuggage.map(option => (
          <MenuItem key={option.id} value={option.id}>
            {option.weigh} <span style={{ color: '#999' }}>- {formatCurrency(option.price)}</span>
          </MenuItem>
        ))}
      </TextField>
    );
  };
  renderDropDownSignedLuggageLast = () => {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="signedLuggageLast"
        select
        label="Hành lý ký gửi lượt về:"
        className={classes.textField}
        value={this.state.signedLuggageLast}
        onChange={this._onHandleChangeLuggageLast}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listSignedLuggage.map(option => (
          <MenuItem key={option.id} value={option.id}>
            {option.weigh} <span style={{ color: '#999' }}>- {formatCurrency(option.price)}</span>
          </MenuItem>
        ))}
      </TextField>
    );
  };
  renderDropDownInsurrance = () => {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="insurrance"
        select
        label="Bảo hiểm du lịch:"
        className={classes.textField}
        value={this.state.insurrance}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listInsurrance.map(option => (
          <MenuItem key={option.id} value={option.id} className="item_insurrance">
            {option.title}{' '}
            <span className="price" style={{ position: 'absolute', right: '26px' }}>
              {formatCurrency(option.price)}/Hành khách
            </span>
          </MenuItem>
        ))}
      </TextField>
    );
  };
  onHandleSubmit = values => {
    let priceLuggageFirst = listSignedLuggage.filter(item => {
      return item.id === this.state.signedLuggageFirst;
    });
    let priceLuggageLast = listSignedLuggage.filter(item => {
      return item.id === this.state.signedLuggageLast;
    });
    let insurrance = listInsurrance.filter(item => {
      return item.id === this.state.insurrance;
    });
    let birthday = new Date(this.state.date).getTime();

    let data = {
      birthday: birthday,
      priceLuggageFirst: priceLuggageFirst,
      priceLuggageLast: priceLuggageLast,
      insurrance: insurrance,
      amountBabies: this.props.location.state.amountBabies,
      amountBeliever: this.props.location.state.amountBeliever,
      amountChildren: this.props.location.state.amountChildren,
      itemFligh: this.props.location.state.itemFligh,
      itemFlightDestination: this.props.location.state.itemFlightDestination,
      startDate: this.props.location.state.startDate,
      endDate: this.props.location.state.endDate,
      codeAirportdeparture: this.props.location.state.codeAirportdeparture,
      codeDestination: this.props.location.state.codeDestination,
      name: values.first_name + ' ' + values.last_name
    };
    history.push('/reservation-information', data);
    console.log('onHandleSubmit', data);
  };
  render() {
    let data = this.props.location.state;
    console.log(data);
    return (
      <div className="flight_information">
        <MenuCollection />
        <div className="header_air_ticket">
          <span>Thông tin hành khách</span>
        </div>
        <hr className="line-header" />

        <div className="box_info">
          <div className="box_info_left">
            <div className="title_people">
              <span>Hành khách 1</span>
            </div>
            <UI.Formik
              initialValues={{
                first_name: '',
                last_name: '',
                contact_name: '',
                contact_phone: '',
                contact_email: ''
              }}
              validationSchema={UI.yup.object().shape({
                first_name: UI.yup.string().required('not null'),
                last_name: UI.yup.string().required('not null'),
                contact_name: UI.yup.string().required('not null'),
                contact_phone: UI.yup.string().required('not null'),
                contact_email: UI.yup.string().required('not null')
              })}
              onSubmit={this.onHandleSubmit}
              render={props => (
                <form onSubmit={props.handleSubmit}>
                  <div className="info_person">
                    <div className="box_input">
                      <div className="title">Họ:</div>
                      <div className="card-cover">
                        <UI.Field
                          {...props}
                          name="first_name"
                          type="text"
                          placeholder="Họ"
                          value={props.values.first_name}
                          onChange={props.handleChange}
                          autoComplete="off"
                        />
                      </div>
                    </div>
                    <div className="box_input">
                      <div className="title">Tên đệm và tên:</div>
                      <div className="card-cover">
                        <UI.Field
                          {...props}
                          name="last_name"
                          type="text"
                          placeholder="Tên đệm và tên"
                          value={props.values.last_name}
                          onChange={props.handleChange}
                          autoComplete="off"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="box_input">
                    <div className="title">Năm sinh:</div>
                    <SingleDatePicker
                      date={this.state.date}
                      onDateChange={date =>
                        this.setState({ date }, () => {
                          console.log('date', this.state.date);
                        })
                      }
                      placeholder="Năm sinh"
                      focused={this.state.focused}
                      onFocusChange={({ focused }) => this.setState({ focused })}
                      id="your_unique_id"
                    />
                  </div>
                  <div className="signed_luggage">
                    <div className="box_input item_signed_luggage">
                      <div className="card-cover">{this.renderDropDownSignedLuggageFirst()}</div>
                      {/* <div className="err-caterory">
                    {(this.state && this.state.showErrorComboboxRole) || ''}
                  </div> */}
                    </div>
                    <div className="box_input item_signed_luggage">
                      <div className="card-cover">{this.renderDropDownSignedLuggageLast()}</div>
                      {/* <div className="err-caterory">
                    {(this.state && this.state.showErrorComboboxRole) || ''}
                  </div> */}
                    </div>
                  </div>
                  <div className="box_input insurrance">
                    <div className="card-cover">{this.renderDropDownInsurrance()}</div>
                    {/* <div className="err-caterory">
                    {(this.state && this.state.showErrorComboboxRole) || ''}
                  </div> */}
                  </div>
                  <div className="title_people contact">
                    <span>Thông tin liên hệ</span>
                    <hr className="line-header" />
                  </div>
                  <div className="people_contact">
                    <div className="box_input">
                      <div className="title">Họ tên người liên hệ:</div>
                      <div className="card-cover">
                        <UI.Field
                          {...props}
                          name="contact_name"
                          type="text"
                          placeholder="Họ tên người liên hệ"
                          value={props.values.contact_name}
                          onChange={props.handleChange}
                          autoComplete="off"
                        />
                      </div>
                    </div>
                    <div className="box_input">
                      <div className="title">Số điện thoại:</div>
                      <div className="card-cover">
                        <UI.Field
                          {...props}
                          name="contact_phone"
                          type="text"
                          placeholder="Số điện thoại"
                          value={props.values.contact_phone}
                          onChange={props.handleChange}
                          autoComplete="off"
                        />
                      </div>
                    </div>
                    <div className="box_input">
                      <div className="title">Email:</div>
                      <div className="card-cover">
                        <UI.Field
                          {...props}
                          name="contact_email"
                          type="text"
                          placeholder="Email"
                          value={props.values.contact_email}
                          onChange={props.handleChange}
                          autoComplete="off"
                        />
                      </div>
                    </div>
                  </div>
                  <hr className="line-header" style={{ marginTop: '30px' }} />
                  <UI.Button type="submit" name="Tiếp tục" className="btn-info" />
                </form>
              )}
            />
          </div>
          <BoxInfoRight
            itemFlight={data.itemFligh}
            itemFlightDestination={data.itemFlightDestination}
            startDate={data.startDate}
            amountBeliever={data.amountBeliever}
            amountChildren={data.amountChildren}
            amountBabies={data.amountBabies}
            display={data.display}
            priceLuggageFirst={this.state.priceLuggageFirst}
            priceLuggageLast={this.state.priceLuggageLast}
            priceInsurrance={this.state.priceInsurrance}
          />
        </div>
      </div>
    );
  }
}
export default PeopleInfomation;
