import React, { Component } from 'react';
import SubMenu from '../component/menuSell';
import { icons } from '../../../assets';
import './styles/qrCode.scss';
import QRcode from 'qrcode.react';
import _ from 'lodash';

import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { accountActions, accountSelectors } from '../../../redux/account';
import { orderSelectors, orderActions } from '../../../redux/order';
import { AppState } from '../../../redux/state';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  // login: (payload: any) => dispatch(authActions.login(payload))
  getAccount: () => dispatch(accountActions.accountMe()),
  getStaticQRCode: () => dispatch(orderActions.getStaticQRCode())
});

const mapStateToProps = (state: AppState) => ({
  account: accountSelectors(state).account,
  staticQRCode: orderSelectors(state).staticQRCode
});

class DetailStore extends Component {
  componentDidMount() {
    this.props.getAccount();
    this.props.getStaticQRCode();
  }
  render() {
    const { account, staticQRCode } = this.props;
    const { store } = account;

    return (
      <div className="qr-code ">
        <SubMenu />
        <div className="header-qrcode">
          <span>Mã QR tĩnh</span>
        </div>
        <div className="content-qr-code">
          <div className="info-qr-code">
            <div className="info-qr-code-content">
              <div className="img-responsive my-div-qrcode">
                <QRcode
                  value={staticQRCode === '' ? 'noName' : staticQRCode.qrCode}
                  className="img-responsive my-qrcode"
                />
              </div>

              <div className="profile-info">
                <div className="detail-my-qrcode ">
                  <img src={icons.icon_user} class="img-responsive" alt="Avatar profile" />
                  <div className="name-merchant">{_.isEmpty(store) ? '' : store.storeName}</div>
                  <div className="address-merchant">
                    {_.isEmpty(store) ? '' : store.storeAddress},{' '}
                    {_.isEmpty(store) ? '' : store.storeDistrict},{' '}
                    {_.isEmpty(store) ? '' : store.storeCity}
                  </div>
                  <div className="number-phone-merchant">
                    {_.isEmpty(store) ? '' : store.phoneNumber}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DetailStore);
