import { of, from } from 'rxjs';
import { ofType, ActionsObservable } from 'redux-observable';
import { takeUntil, catchError, switchMap } from 'rxjs/operators';
import { combineEpics } from 'redux-observable';
import * as localStorage from '../../commons/localStorage';
import { StorageKey } from '../../commons/constants/storageKeys';
// import { Action } from 'c-redux';
import { types, actions } from './actions';
import { accountActions } from '../account';
import { authAPIs } from '../../services';
import { apisauceInstance } from '../../services';
// import { history } from '../router';
// import { showDialog } from '../../emitter';
// import { t } from '../../i18n';

interface Action {
  type: string;
  payload?: any;
}

const loginEpic = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.LOGIN),
    switchMap(action => {
      return from(authAPIs.login(action.payload)).pipe(
        switchMap((data: any) => {
          console.log('data', data);

          apisauceInstance.setHeader('Authorization', `${data.token_type} ${data.access_token}`);

          localStorage.setStorageKey(
            StorageKey.USER_TOKEN,
            {
              token: `${data.token_type} ${data.access_token}`,
              expires_in: data.expires_in
            }
            // `${data.token_type} ${data.access_token}`
          );

          // const access_token = data.access_token;
          // localStorage.saveToken(access_token);
          // dispatch(mainActions.authencate(true));
          // history.push(`/overview`);

          return of(accountActions.preloadingAfterLogin(), actions.loginSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.LOGIN_CANCEL))),
        catchError(error => {
          return of(actions.loginFailure(error.message));
        })
      );
    })
  );
};

export default combineEpics(loginEpic);
