class EmployeeManager extends Component {
  componentDidMount() {
    this.fetchData();
  }

  fetchData() {
    this.props.getAccount();
  }

  renderProduct() {
    let result = '';
    let role = '';
    const { listAccount } = this.props;
    result = listAccount.map((item, index) => {
      switch (item.role) {
        case 'manager':
          role = 'Quản lý';
          break;

        default:
          role = 'Nhân viên';
          break;
      }
      return (
        <div className="box-category" key={index}>
          <div
            className="content-box-category"
            style={{ backgroundColor: index % 2 === 0 ? '#fbfcff' : 'white' }}
          >
            <div className="item-detail-caterory">{item.fullName}</div>
            <div className="item-detail-caterory img-product">{item.store.storeName}</div>
            <div className="item-detail-caterory name-product">{item.phoneNumber}</div>
            <div className="item-detail-caterory unit-price">
              {item.store.storeAddress +
                ', ' +
                item.store.storeDistrict +
                ', ' +
                item.store.storeCity}
            </div>
            <div className="item-detail-caterory amount-product">{role}</div>
            <div className="item-detail-caterory statusproduct">
              {item && item.active ? 'Đang làm việc' : 'Đã nghỉ việc'}
            </div>
            <div className="item-detail-caterory">
              <Link
                className="icon"
                to="#"
                onClick={() => {
                  this.onEdit(item);
                }}
              >
                <i className="fa fa-edit"></i>
              </Link>
              <Link
                className="icon"
                to="#"
                onClick={() => {
                  this.onDel(item);
                }}
              >
                <i className="fa fa-trash"></i>
              </Link>
            </div>
          </div>
        </div>
      );
    });
    return result;
  }
  onDel = item => {
    console.log('item', item);
  };
  onEdit = item => {
    console.log('onEdit', item);
  };
  render() {
    return (
      <div className="employee-manager">
        <SubMenu />
        <div className="content-employee-manager">
          <div className="header-employee-manager">
            <div className="header-employee-manager-left">
              <span>Quản lý nhân viên</span>
            </div>
            <div className="header-employee-manager-right">
              <Link to="/add-employee" className="add-category">
                <span className="title-add">Thêm nhân viên mới</span>
                <div className="icon-add">
                  <i className="fa fa-plus icon_plus"></i>
                </div>
              </Link>
            </div>
          </div>
          <hr className="line-header" />
          <div className="caterory-products">
            <div className="title-caterory-products">
              <div className="item-title-caterory">Tên nhân viên</div>
              <div className="item-title-caterory img-product">Chi nhánh</div>
              <div className="item-title-caterory name-product">Số điện thoại</div>
              <div className="item-title-caterory unit-price">Địa chỉ chi nhánh</div>
              <div className="item-title-caterory amount-product">Phân quyền</div>
              <div className="item-title-caterory statusproduct">Trạng thái</div>
              <div className="item-title-caterory">Tuỳ chỉnh</div>
            </div>
          </div>
          <div className="list-category">{this.renderProduct()}</div>
        </div>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EmployeeManager);
