import { status } from '../helper';

export const types = {
  GET_CATEGORY: `${status.start}GET_CATEGORY`,
  GET_CATEGORY_SUCCESS: `${status.success}GET_CATEGORY`,
  GET_CATEGORY_FAILURE: `${status.failure}GET_CATEGORY`,
  GET_CATEGORY_CANCEL: `${status.cancel}GET_CATEGORY`,

  CREATE_CATEGORY: `${status.start}CREATE_CATEGORY`,
  CREATE_CATEGORY_SUCCESS: `${status.success}CREATE_CATEGORY`,
  CREATE_CATEGORY_FAILURE: `${status.failure}CREATE_CATEGORY`,
  CREATE_CATEGORY_CANCEL: `${status.cancel}CREATE_CATEGORY`,

  UPDATE_CATEGORY: `${status.start}UPDATE_CATEGORY`,
  UPDATE_CATEGORY_SUCCESS: `${status.success}UPDATE_CATEGORY`,
  UPDATE_CATEGORY_FAILURE: `${status.failure}UPDATE_CATEGORY`,
  UPDATE_CATEGORY_CANCEL: `${status.cancel}UPDATE_CATEGORY`,

  DELETE_CATEGORY: `${status.start}DELETE_CATEGORY`,
  DELETE_CATEGORY_SUCCESS: `${status.success}DELETE_CATEGORY`,
  DELETE_CATEGORY_FAILURE: `${status.failure}DELETE_CATEGORY`,
  DELETE_CATEGORY_CANCEL: `${status.cancel}DELETE_CATEGORY`,

  GET_CATEGORY_ID: `${status.start}GET_CATEGORY_ID`,
  GET_CATEGORY_ID_SUCCESS: `${status.success}GET_CATEGORY_ID`,
  GET_CATEGORY_ID_FAILURE: `${status.failure}GET_CATEGORY_ID`,
  GET_CATEGORY_ID_CANCEL: `${status.cancel}GET_CATEGORY_ID`
};

export const actions = {
  getCategory: (payload?: any) => ({ type: types.GET_CATEGORY, payload }),
  getCategorySuccess: (payload?: any) => ({ type: types.GET_CATEGORY_SUCCESS, payload }),
  getCategoryFailure: (payload?: any) => ({ type: types.GET_CATEGORY_FAILURE, payload }),
  getCategoryCancel: () => ({ type: types.GET_CATEGORY_CANCEL }),

  createCategory: (payload?: any) => ({ type: types.CREATE_CATEGORY, payload }),
  createCategorySuccess: (payload?: any) => ({ type: types.CREATE_CATEGORY_SUCCESS, payload }),
  createCategoryFailure: (payload?: any) => ({ type: types.CREATE_CATEGORY_FAILURE, payload }),
  createCategoryCancel: () => ({ type: types.CREATE_CATEGORY_CANCEL }),

  updateCategory: (payload?: any) => ({ type: types.UPDATE_CATEGORY, payload }),
  updateCategorySuccess: (payload?: any) => ({ type: types.UPDATE_CATEGORY_SUCCESS, payload }),
  updateCategoryFailure: (payload?: any) => ({ type: types.UPDATE_CATEGORY_FAILURE, payload }),
  updateCategoryCancel: () => ({ type: types.UPDATE_CATEGORY_CANCEL }),

  deleteCategory: (payload?: any) => ({ type: types.DELETE_CATEGORY, payload }),
  deleteCategorySuccess: (payload?: any) => ({ type: types.DELETE_CATEGORY_SUCCESS, payload }),
  deleteCategoryFailure: (payload?: any) => ({ type: types.DELETE_CATEGORY_FAILURE, payload }),
  deleteCategoryCancel: () => ({ type: types.DELETE_CATEGORY_CANCEL }),

  getCategoryId: (payload?: any) => ({ type: types.GET_CATEGORY_ID, payload }),
  getCategoryIdSuccess: (payload?: any) => ({ type: types.GET_CATEGORY_ID_SUCCESS, payload }),
  getCategoryIdFailure: (payload?: any) => ({ type: types.GET_CATEGORY_ID_FAILURE, payload }),
  getCategoryIdCancel: () => ({ type: types.GET_CATEGORY_ID_CANCEL })
};
