import React, { Component } from 'react';
import { UI } from '..';
import { colors } from '../../commons/constants';
import './styles.scss';
// import { icons } from '../../assets';
import PropTypes from 'prop-types';

class index extends Component {
  state = {};
  render() {
    // const Divider = props => {
    //   return (
    //     <UI.Div className={props.active ? 'div-process-active' : 'div-process'}>
    //       <UI.Image src={props.icon} />
    //     </UI.Div>
    //   );
    // };
    const { data } = this.props;
    const maxLeft = data.length - 1;
    const active = data.filter(s => s.active).length;

    console.log('maxLeft', maxLeft, 'active', active);

    return (
      <UI.Div className="container-process">
        {data.map((item, index) => (
          <React.Fragment>
            <UI.Div
              className={
                item.active && index === 0
                  ? 'item-process-active-first'
                  : item.active && index !== 0
                  ? 'item-process-active'
                  : maxLeft === index
                  ? 'item-process-end'
                  : 'item-process'
              }
            >
              <UI.Text textAlign="center" className="text-process" color={colors.white}>
                {item.name}
              </UI.Text>
            </UI.Div>
          </React.Fragment>
        ))}
      </UI.Div>
    );
  }
}
index.defaultProps = {
  data: [
    { name: '1. Xác nhận thông tin', active: true },
    { name: '2. Tình trạng giao dịch', active: false }
  ]
};
index.propTypes = {
  data: PropTypes.array
};

export default index;
