import { of, from } from 'rxjs';
import { ofType, ActionsObservable } from 'redux-observable';
import { takeUntil, catchError, switchMap } from 'rxjs/operators';
import { combineEpics } from 'redux-observable';
// import { Action } from 'c-redux';
import { types, actions } from './actions';
import { BillAPIs } from '../../services';

interface Action {
  type: string;
  payload?: any;
}

const getBillAll = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_BILL_ALL),
    switchMap(action => {
      return from(BillAPIs.getBillAll()).pipe(
        switchMap((data: any) => {
          return of(actions.getBillAllSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_BILL_ALL_CANCEL))),
        catchError(error => {
          return of(actions.getBillAllFailure(error.message));
        })
      );
    })
  );
};

const getBillPage = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_BILL_PAGE),
    switchMap(action => {
      const payload = action.payload;
      return from(BillAPIs.getBillPage(payload)).pipe(
        switchMap((data: any) => {
          return of(actions.getBillPageSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_BILL_PAGE_CANCEL))),
        catchError(error => {
          return of(actions.getBillPageFailure(error.message));
        })
      );
    })
  );
};

const getBillSearch = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_BILL_SEARCH),
    switchMap(action => {
      const payload = action.payload;
      return from(BillAPIs.getBillSearch(payload)).pipe(
        switchMap((data: any) => {
          return of(actions.getBillSearchSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_BILL_SEARCH_CANCEL))),
        catchError(error => {
          return of(actions.getBillSearchFailure(error.message));
        })
      );
    })
  );
};

const getBillCode = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_BILL_CODE),
    switchMap(action => {
      const payload = action.payload;
      const { code, onSuccess } = payload;
      return from(BillAPIs.getBillCode(code)).pipe(
        switchMap((data: any) => {
          if (onSuccess) {
            onSuccess(data);
          }
          return of(actions.getBillCodeSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_BILL_CODE_CANCEL))),
        catchError(error => {
          return of(actions.getBillCodeFailure(error.message));
        })
      );
    })
  );
};

const getBillById = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_BILL_ID),
    switchMap(action => {
      const payload = action.payload;

      return from(BillAPIs.getBillById(payload)).pipe(
        switchMap((data: any) => {
          console.log('getBillById', data);

          return of(actions.getBillIdSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_Bill_ID_CANCEL))),
        catchError(error => {
          return of(actions.getBillIdFailure(error.message));
        })
      );
    })
  );
};

const createBill = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.POST_BILL_ID),
    switchMap(action => {
      const payload = action.payload;
      return from(BillAPIs.createBill(payload)).pipe(
        switchMap((data: any) => {
          return of(actions.postBillIdSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.POST_BILL_ID_CANCEL))),
        catchError(error => {
          return of(actions.postBillIdFailure(error.message));
        })
      );
    })
  );
};

export default combineEpics(
  getBillAll,
  getBillPage,
  getBillSearch,
  getBillCode,
  getBillById,
  createBill
);
