import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { icons } from '../../assets';
import { CustomLink } from '../../components/CustomLink/customLink';
import './styles.scss';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import Modal from 'react-modal';
import { accountSelectors } from '../../redux/account';
import { AppState } from '../../redux/state';
import { StorageKey } from '../../commons/constants/storageKeys';
import CustomizedMenus from './menuDropDown';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  // login: (payload: any) => dispatch(authActions.login(payload))
});

const mapStateToProps = (state: AppState) => ({
  account: accountSelectors(state).account
});

const customStyles = {
  content: {
    top: '53%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    padding: '0px'
  }
};

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalIsOpen: false
    };
  }
  openModal = () => {
    this.setState({
      modalIsOpen: true
    });
  };
  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };
  logout = () => {
    this.setState({ modalIsOpen: false });
    localStorage.clear();
    window.location.reload();
  };

  contentLogout = () => {
    return (
      <div className="box_logout">
        <div className="off_box">
          <img src={icons.icon_close_popup} className="img-responsive" alt="img_btn_logout" />
        </div>
        <div className="content_box_logout">
          <p>Bạn có chắc muốn kết thúc phiên làm việc không?</p>
        </div>

        <div className="btn_gr_logout">
          <button type="button" className="btn btn-default btn_logout" onClick={this.logout}>
            Đăng xuất
          </button>
          <button
            type="button"
            className="btn btn-default btn_cancel_logout"
            onClick={this.closeModal}
          >
            Huỷ
          </button>
        </div>
      </div>
    );
  };

  renderMenu() {
    let fullName = JSON.parse(localStorage.getItem(StorageKey.FULL_NAME));
    const account = this.props.account;
    if (account || fullName) {
      return (
        <nav className="navbar navbar-expand-lg navbar-light bg-light menu">
          <div className="view-logo">
            <img src={icons.icon_logo_white} className="img-responsive logo" alt="logo" />
          </div>

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              <CustomLink
                to="/overview"
                label="Tổng quan"
                className="link-menu"
                imgAlt={icons.icon_tongquan_inactive}
              />
              <CustomLink
                to="/bill-electric"
                muiltipleTo="(/bill-electric|/info-bill-electric|/bill-water|/info-bill-water|/bill-television|/info-bill-television|/tuition|/search-tution|/info-tuition|/air-ticket|/form-booking|/flight-information|/people-infomation|/reservation-information|/booking-history|/detail-my-booking|/my-customer|/ticket-purchase-history)"
                label="Thu hộ"
                className="link-menu"
                imgAlt={icons.icon_thu_ho_inactive}
              />
              <CustomLink
                to="/tao-don-hang"
                muiltipleTo="(/tao-don-hang|/detail-order|/ma-qr|/waiting-pay|/history-order|/category-management|/product-management|/employee-management|/store-manager|/order-success|/order-false|/add-product|/add-employee|/add-store)"
                label="Bán hàng"
                className="link-menu"
                imgAlt={icons.icon_banhang_inactive}
              />
              <CustomLink
                to="/report"
                label="Báo cáo"
                className="link-menu"
                imgAlt={icons.icon_baocao_inactive}
              />
            </ul>
            <ul className="navbar-nav my-2 my-lg-0 menu-right">
              <li className="nav-item">
                <img src={icons.icon_bg_menu_right} className="img-responsive" alt="bg-menu" />
              </li>
              <li className="nav-item">
                <Link className="nav-link name-account" to="#">
                  Xin chào: {account.fullName || fullName}
                </Link>
              </li>
              <li className="nav-item avatar">
                <img
                  src={icons.icon_profile_white}
                  className="img-responsive profile-avatar"
                  alt="bg-menu"
                />
              </li>
              <li className="nav-item avatar">
                <CustomizedMenus />
                {/* <UI.CustomizedMenus icon={icons.icon_setting_white} /> */}
                <img
                  src={icons.icon_setting_white}
                  className="img-responsive profile-avatar"
                  alt="bg-menu"
                />
              </li>
              <li className="nav-item avatar">
                <Link to="#" onClick={this.openModal}>
                  <img
                    src={icons.icon_logout}
                    className="img-responsive profile-avatar icon_logout"
                    alt="bg-menu"
                  />
                </Link>
              </li>
            </ul>
          </div>
          <Modal
            isOpen={this.state.modalIsOpen}
            onRequestClose={this.closeModal}
            style={customStyles}
            contentLabel="Example Modal"
            ariaHideApp={false}
          >
            {this.contentLogout()}
          </Modal>
        </nav>
      );
    }
  }
  render() {
    return <React.Fragment>{this.renderMenu()}</React.Fragment>;
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);
