import { fetch } from '.';
const accountAPIs = {
  getAccountMe: () => {
    return fetch('Account/me', 'get');
  },
  getAccount: () => {
    return fetch('Account', 'get');
  },
  getAccountId: (payload: any) => {
    return fetch(`Account/${payload}`, 'get');
  },

  getAccountReset: (payload: any) => {
    return fetch(`Account/reset/${payload}`, 'get');
  },

  accountChangePassword: (payload: any) => {
    return fetch('Account/changePassword', 'post', payload);
  },

  createAccount: (payload: any) => {
    return fetch('Account', 'post', payload);
  },

  editAccount: (payload: any) => {
    return fetch(`Account?id=${payload.id}`, 'put', payload);
  },

  accountReset: (payload: any) => {
    return fetch(`Accountreset?phone=${payload}`, 'get');
  }
};

export { accountAPIs };
