import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { icons } from '../../../../assets';
import { formatCurrency } from '../../../../commons/utils';

import SubMenu from '../../component/menuSell';
import '../styles/productManagement.scss';

import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { productActions, productSelectors } from '../../../../redux/product';
import { AppState } from '../../../../redux/state';

import { history } from '../../../../redux/router';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  // login: (payload: any) => dispatch(authActions.login(payload))
  getProduct: () => dispatch(productActions.getProduct()),
  getProductId: (payload: any) => dispatch(productActions.getProductId(payload)),
  // createProduct: (payload: any) => dispatch(productActions.createProduct(payload)),
  // editProduct: (payload: any) => dispatch(productActions.updateProduct(payload)),
  deleteProduct: (payload: any) => dispatch(productActions.deleteProduct(payload))
});

const mapStateToProps = (state: AppState) => ({
  // account: accountSelectors(state).account
  listProduct: productSelectors(state).listProduct,
  productById: productSelectors(state).productById
});
class ProductManagement extends Component {
  componentDidMount() {
    this.fetchData();
  }

  fetchData() {
    this.props.getProduct();
  }

  renderProduct() {
    let result = '';
    const { listProduct } = this.props;
    result = listProduct.map((item, index) => {
      return (
        <div className="box-category" key={index}>
          <div
            className="content-box-category"
            style={{ backgroundColor: index % 2 === 0 ? '#fbfcff' : 'white' }}
          >
            <div className="item-detail-caterory sku">{item.sku}</div>
            <img
              src={item.thumbnail}
              className="img-responsive img-product"
              alt={item.productName}
            />
            {/* <div className="item-detail-caterory img-product"></div> */}
            <div className="item-detail-caterory name-product">{item.productName}</div>
            <div className="item-detail-caterory unit-price">{formatCurrency(item.price)}</div>
            <div className="item-detail-caterory amount-product">{(item && item.unit) || 0}</div>
            <div className="item-detail-caterory statusproduct">Còn hàng</div>
            <div className="item-detail-caterory">
              <Link
                to="#"
                onClick={() => {
                  this.onEdit(item);
                }}
              >
                <img
                  src={icons.icon_edit}
                  class="img-responsive"
                  alt="icon_edit"
                  style={{ height: '35px' }}
                />
              </Link>
              <Link
                to="#"
                onClick={() => {
                  this.onDel(item);
                }}
              >
                <img
                  src={icons.icon_del}
                  class="img-responsive"
                  alt="icon_del"
                  style={{ height: '35px' }}
                />
              </Link>
            </div>
          </div>
        </div>
      );
    });
    return result;
  }
  onDel = item => {
    console.log('item', item);
    const onSuccess = (data: any) => {
      this.fetchData();
    };
    this.props.deleteProduct({ data: item.id, onSuccess });
  };
  onEdit = (data: any) => {
    console.log('onEdit', data);
    // history.push('/order-success', { item: data });
    history.push('/add-product', data);
  };
  render() {
    console.log(this.props);
    return (
      <div className="product-management">
        <SubMenu />
        <div className="content-product-management">
          <div className="header-category-management">
            <div className="header-category-management-left">
              <span>Quản lý sản phẩm</span>
            </div>
            <div className="header-category-management-right">
              <Link to="/add-product" className="add-category">
                <span className="title-add">Thêm sản phẩm mới</span>
                <div className="icon-add">
                  <i className="fa fa-plus icon_plus"></i>
                </div>
              </Link>
            </div>
          </div>
          <hr className="line-header" />
          <div className="caterory-products">
            <div className="title-caterory-products">
              <div className="item-title-caterory">Mã SP</div>
              <div className="item-title-caterory img-product">Hình ảnh SP</div>
              <div className="item-title-caterory name-product">Tên sản phẩm</div>
              <div className="item-title-caterory unit-price">Đơn giá</div>
              <div className="item-title-caterory amount-product">Số lượng</div>
              <div className="item-title-caterory statusproduct">Trạng thái</div>
              <div className="item-title-caterory">Tuỳ chỉnh</div>
            </div>
          </div>
          <div className="list-category">{this.renderProduct()}</div>
        </div>
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ProductManagement);
