import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import SubMenu from '../../component/menuSell';
import Modal from 'react-modal';
import { images, icons } from '../../../../assets';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import PopUpPay from './popupPay';
import { withStyles } from '@material-ui/core/styles';
import { formatCurrency } from '../../../../commons/utils';
import '../styles/detail-order.scss';
import { history } from '../../../../redux/router';
import { StorageKey } from '../../../../commons/constants/storageKeys';
import _ from 'lodash';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { orderActions, orderSelectors } from '../../../../redux/order';
import { billActions } from '../../../../redux/bill';
import { AppState } from '../../../../redux/state';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  createOrder: (payload: any) => dispatch(orderActions.createOrder(payload)),
  deleteOrderCode: (payload: any) => dispatch(orderActions.deleteOrderCode(payload)),
  getBillCode: (payload: any) => dispatch(billActions.getBillCode(payload))
});

const mapStateToProps = (state: AppState) => ({
  // account: accountSelectors(state).account
  // listOrder: orderSelectors(state).listOrder,
  createSuccess: orderSelectors(state).createSuccess
});

const IOSSwitch = withStyles(theme => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1)
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: theme.palette.common.white,
      '& + $track': {
        backgroundColor: '#52d869',
        opacity: 1,
        border: 'none'
      }
    },
    '&$focusVisible $thumb': {
      color: '#52d869',
      border: '6px solid #fff'
    }
  },
  thumb: {
    width: 24,
    height: 24
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border'])
  },
  checked: {},
  focusVisible: {}
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked
      }}
      {...props}
    />
  );
});

const customStyles = {
  content: {
    top: '53%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    padding: '0px'
  }
};
const listBtnPay = [
  {
    title: 'Thanh toán bằng QR',
    icon: icons.icon_banhang_maqr_white,
    status: 'qrcode'
  },
  {
    title: 'Thanh toán tiền mặt',
    icon: icons.icon_paybycash,
    status: 'cash'
  }
];

class DetailOrder extends Component {
  constructor(props) {
    super(props);
    let sumShoppingCarts = JSON.parse(localStorage.getItem(StorageKey.SHOPPING_CARTS));
    let amount = 0;
    let promoCode = '';
    let shoppingCart = {};
    if (!_.isEmpty(sumShoppingCarts)) {
      amount = sumShoppingCarts.amount;
      promoCode = sumShoppingCarts.promoCode;
      shoppingCart = sumShoppingCarts;
    }
    this.state = {
      checkedB: false,
      modalIsOpen: false,
      amount: amount,
      promoCode: promoCode,
      promo: 0,
      shoppingCart: shoppingCart
    };
  }

  getDataShoppingCarts = async () => {
    let shoppingCarts = {};
    shoppingCarts = await localStorage.getStorageKey(StorageKey.SHOPPING_CARTS);
    return shoppingCarts;
  };

  Add = async (item: any) => {
    // console.log('add item ', item);

    try {
      let shoppingCarts = {};
      let fullName = '';
      shoppingCarts = await JSON.parse(localStorage.getItem(StorageKey.SHOPPING_CARTS));
      fullName = await JSON.parse(localStorage.getItem(StorageKey.FULL_NAME));
      // console.log('shoppingCarts', shoppingCarts);
      if (_.isEmpty(shoppingCarts)) {
        // console.log('shoppingCarts', typeof shoppingCarts);
        shoppingCarts = {
          promoCode: '',
          name: fullName,
          amount: item.price,
          orderItems: [
            {
              productId: item.id,
              quantity: 1,
              productName: item.productName,
              price: item.price,
              thumbnail: images.Budweiser
              // thumbnail:item.thumbnail
            }
          ]
        };
      } else {
        // console.log('shoppingCarts else', shoppingCarts);
        shoppingCarts.amount = shoppingCarts.amount + item.price;
        let loop = false;
        if (!_.isEmpty(shoppingCarts.orderItems)) {
          shoppingCarts.orderItems.forEach(element => {
            if (element.productId === item.productId) {
              element.quantity = element.quantity + 1;
              loop = true;
              return;
            }
          });
        }

        if (!loop) {
          shoppingCarts.orderItems.push({
            productId: item.productId,
            quantity: 1,
            productName: item.productName,
            price: item.price,
            thumbnail: images.Tiger
            // thumbnail:item.thumbnail
          });
        }
      }

      localStorage.setItem(StorageKey.SHOPPING_CARTS, JSON.stringify(shoppingCarts));
      this.setState({ amount: shoppingCarts.amount, shoppingCart: shoppingCarts });
    } catch (error) {
      console.log('add shopping cart', error);
    }
  };

  Sub = async (item: any) => {
    // console.log('sub item ', item);

    try {
      let shoppingCarts = {};
      shoppingCarts = await JSON.parse(localStorage.getItem(StorageKey.SHOPPING_CARTS));
      // console.log('shoppingCarts', shoppingCarts);
      if (_.isEmpty(shoppingCarts)) {
        return;
      } else {
        if (!_.isEmpty(shoppingCarts.orderItems)) {
          shoppingCarts.orderItems.forEach(element => {
            if (element.productId === item.productId && element.quantity > 1) {
              element.quantity = element.quantity - 1;
              if (shoppingCarts.amount >= item.price) {
                shoppingCarts.amount = shoppingCarts.amount - item.price;
              }
              return;
            }
          });
        }
      }

      localStorage.setItem(StorageKey.SHOPPING_CARTS, JSON.stringify(shoppingCarts));
      this.setState({ amount: shoppingCarts.amount, shoppingCart: shoppingCarts });
    } catch (error) {
      console.log('sub shopping cart', error);
    }
  };

  Delete = async (item: any, index: Number) => {
    console.log('delete item ', item);

    try {
      let shoppingCarts = {};
      shoppingCarts = await JSON.parse(localStorage.getItem(StorageKey.SHOPPING_CARTS));
      // console.log('shoppingCarts', shoppingCarts);
      if (_.isEmpty(shoppingCarts)) {
        // return;
      } else {
        // console.log('shoppingCarts else', shoppingCarts);

        if (!_.isEmpty(shoppingCarts.orderItems)) {
          if (index !== -1 && item.productId === shoppingCarts.orderItems[index].productId) {
            shoppingCarts.amount =
              shoppingCarts.amount - item.price * shoppingCarts.orderItems[index].quantity;
            shoppingCarts.orderItems.splice(index, 1);
          }
        }
      }

      localStorage.setItem(StorageKey.SHOPPING_CARTS, JSON.stringify(shoppingCarts));
      this.setState({ amount: shoppingCarts.amount, shoppingCart: shoppingCarts });
    } catch (error) {
      console.log('add shopping cart', error);
    }
  };

  renderProduct = () => {
    let result = '';
    let shoppingCarts = {};
    shoppingCarts = JSON.parse(localStorage.getItem(StorageKey.SHOPPING_CARTS));
    // console.log('shopingcar', shoppingCarts);

    if (_.isEmpty(shoppingCarts)) {
      return <div></div>;
    } else {
      // console.log('shopingcar.orderItems', shoppingCarts.orderItems);
      result = shoppingCarts.orderItems.map((item, index) => {
        return (
          <div className="row product" key={index}>
            <div className="col-xs-1 col-sm-1 col-md-1 col-lg-1 btn-del">
              <Link onClick={() => this.Delete(item, index)}>
                <i className="fa fa-times-circle"></i>
              </Link>
            </div>

            <div className="col-xs-1 col-sm-1 col-md-1 col-lg-1 viewImgProduct">
              <img
                src={item.thumbnail}
                className="img-responsive img-product"
                alt={item.productName}
              />
            </div>

            <div className="col-xs-7 col-sm-7 col-md-7 col-lg-7 view-name-price">
              <div className="name-price">
                <span className="name-product">{item.productName}</span>
                <br />
                <span className="price-product">{formatCurrency(item.price)}</span>
              </div>
            </div>

            <div className="col-xs-3 col-sm-3 col-md-3 col-lg-3 view-btn-add">
              <div className="btn-add">
                <Link onClick={() => this.Sub(item)}>
                  <i className="fa fa-minus"></i>
                </Link>

                <button type="button" className="btn btn-default">
                  {item.quantity}
                </button>

                <Link onClick={() => this.Add(item)}>
                  <i className="fa fa-plus"></i>
                </Link>
              </div>
            </div>
          </div>
        );
      });
      return result;
    }
  };
  handleChange = name => event => {
    console.log('handleChange');
    this.setState({ checkedB: !this.state.checkedB });
  };
  renderVocher() {
    if (this.state.checkedB) {
      return (
        <div className="vocher">
          <div className="box-vocher">
            <form action="#" className="form-check">
              <div className="item-check">
                <div className="content-item-check">
                  <input type="radio" id="test1" name="radio-group" />
                  <label htmlFor="test1">Mã giảm giá</label>
                </div>
              </div>
              <div className="item-check">
                <div className="content-item-check">
                  <input type="radio" id="test2" name="radio-group" />
                  <label htmlFor="test2">Giảm theo %</label>
                </div>
              </div>
              <div className="item-check">
                <div className="content-item-check">
                  <input type="radio" id="test3" name="radio-group" />
                  <label htmlFor="test3">Giảm trực tiếp</label>
                </div>
              </div>
            </form>
          </div>
        </div>
      );
    } else return null;
  }

  openModal = () => {
    this.setState({
      modalIsOpen: true
    });
  };
  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };

  createOrder = (item: any) => {
    // console.log('create Order ', item);

    const { shoppingCart } = this.state;
    const onSuccess = (data: any) => {
      if (data.status === 1) {
        history.push('/order-success', { item: data });
      } else {
        this.setState({ item: item, viewData: data }, () => this.openModal());
      }
    };
    if (_.isEmpty(shoppingCart)) {
      //thong bao shopping cart rong
    } else {
      if (item.status === 'cash') {
        let data = Object.assign({}, shoppingCart, { status: 1 });
        this.props.createOrder({ data: data, onSuccess });
      } else {
        this.props.createOrder({ data: shoppingCart, onSuccess });
      }
    }
  };

  // onClick={() => this.openModal(item)}
  renderBtnPay() {
    let result = '';
    result = listBtnPay.map((item, index) => {
      return (
        <li className="item-btn">
          <button type="button" className="btn btn-default" onClick={() => this.createOrder(item)}>
            <img src={item.icon} className="img-responsive" alt={item.title} />
            {item.title}
          </button>
        </li>
      );
    });
    return result;
  }
  renderRealAmount() {
    return (
      <div className="box-total-amount">
        <span className="title-box-total-amount">Số tiền cần thanh toán</span>
        <br />
        <span className="number-amount">
          {formatCurrency(this.state.amount - this.state.promo)}
        </span>
      </div>
    );
  }

  AddOtherProduct = () => {
    console.log('AddOtherProduct');

    history.push(`/tao-don-hang`);
  };

  onDeleteOrder = () => {
    const { viewData } = this.state;
    console.log('onDeleteOrder :', viewData);
    const onSuccess = (data: any) => {
      console.log('onDeleteOrder', data);
      this.closeModal();
    };
    this.props.deleteOrderCode({ code: viewData.code, onSuccess });
  };

  onCheckStatusBill = () => {
    const { viewData } = this.state;
    console.log('onCheckStatusBill :', viewData);
    const onSuccess = (data: any) => {
      console.log('onCheckStatusBill', data);
      if (data.status === 1) {
        history.push('/order-success', { item: data });
      } else {
        alert('Chưa thay thanh toán');
      }
    };
    this.props.getBillCode({ code: viewData.code, onSuccess: onSuccess });
  };

  render() {
    return (
      <div className="detail-order">
        <SubMenu />
        <div className="content-detail-order">
          <div className="row">
            <div className="col-xs-9 col-sm-9 col-md-9 col-lg-9 order">
              <div className="titile-order">
                <span>Thông tin đơn hàng</span>
              </div>
              <div className="list-product-order">{this.renderProduct()}</div>

              <button
                type="button"
                className="btn btn-default add-product"
                onClick={() => this.AddOtherProduct()}
              >
                Thêm sản phẩm
              </button>
            </div>

            <div className="col-xs-3 col-sm-3 col-md-3 col-lg-3 right-order">
              <div className="number-provisional">
                <span className="title-provisional">Tạm tính</span>
                <span className="provisional-amount">{formatCurrency(this.state.amount)}</span>
              </div>
              <div className="number-provisional">
                <div className="title-provisional">
                  {' '}
                  <span>Khuyến mãi</span>
                </div>
                <FormControlLabel
                  control={
                    <IOSSwitch
                      checked={this.state.checkedB}
                      onChange={this.handleChange('checkedB')}
                      value="checkedB"
                    />
                  }
                />
              </div>
              {this.renderVocher()}
              {this.renderRealAmount()}

              <ul className="btn-pay">{this.renderBtnPay()}</ul>
            </div>
          </div>
        </div>
        <Modal
          isOpen={this.state.modalIsOpen}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Example Modal"
          ariaHideApp={false}
        >
          <PopUpPay
            closeModal={this.closeModal}
            viewData={this.state.viewData}
            onDeleteOrder={this.onDeleteOrder}
            onCheckStatusBill={this.onCheckStatusBill}
          />
        </Modal>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DetailOrder);
