import Immutable from 'seamless-immutable';
import { AppState } from '../state';

import { types } from './actions';

const initialState = Immutable({
  fetchingCategory: 0,
  listCategory: [],
  categoryById: {},
  fetchingCategoryById: 0,
  actionFetching: 0,
  createSuccess: {},
  editSuccess: {},
  deleteSuccess: {}
});

export const reducer = (
  state = initialState,
  { type, payload }: { type: string, payload?: any }
) => {
  switch (type) {
    case types.GET_CATEGORY:
      return state.set('fetchingCategory', 1);
    case types.GET_CATEGORY_SUCCESS:
      return state.merge({ fetchingCategory: 0, listCategory: payload });
    case types.GET_CATEGORY_FAILURE:
      return state.set('fetchingCategory', -1);
    case types.GET_CATEGORY_CANCEL:
      return state.set('fetchingCategory', 0);

    case types.GET_CATEGORY_ID:
      return state.set('fetchingCategoryById', 1);
    case types.GET_CATEGORY_ID_SUCCESS:
      return state.merge({ fetchingCategory: 0, categoryById: payload });
    case types.GET_CATEGORY_ID_FAILURE:
      return state.set('fetchingCategoryById', -1);
    case types.GET_CATEGORY_ID_CANCEL:
      return state.set('fetchingCategoryById', 0);

    case types.CREATE_CATEGORY:
      return state.set('actionFetching', 1);
    case types.CREATE_CATEGORY_SUCCESS:
      return state.merge({ actionFetching: 0, createSuccess: payload });
    case types.CREATE_CATEGORY_FAILURE:
      return state.set('actionFetching', -1);
    case types.CREATE_CATEGORY_CANCEL:
      return state.set('actionFetching', 0);

    case types.UPDATE_CATEGORY:
      return state.set('actionFetching', 1);
    case types.UPDATE_CATEGORY_SUCCESS:
      return state.merge({ actionFetching: 0, editSuccess: payload });
    case types.UPDATE_CATEGORY_FAILURE:
      return state.set('actionFetching', -1);
    case types.UPDATE_CATEGORY_CANCEL:
      return state.set('actionFetching', 0);

    case types.DELETE_CATEGORY:
      return state.set('actionFetching', 1);
    case types.DELETE_CATEGORY_SUCCESS:
      return state.merge({ actionFetching: 0, deleteSuccess: payload });
    case types.DELETE_CATEGORY_FAILURE:
      return state.set('actionFetching', -1);
    case types.DELETE_CATEGORY_CANCEL:
      return state.set('actionFetching', 0);

    case 'RESET':
      return initialState;

    default:
      return state;
  }
};

export const selectors = (state: AppState) => ({
  fetchingCategory: state.category.getIn(['fetchingCategory']),
  fetchingCategoryById: state.category.getIn(['fetchingCategoryById']),
  listCategory: state.category.getIn(['listCategory']),
  categoryById: state.category.getIn(['categoryById']),
  actionFetching: state.category.getIn(['actionFetching']),
  editSuccess: state.category.getIn(['editSuccess']),
  createSuccess: state.category.getIn(['createSuccess']),
  deleteSuccess: state.category.getIn(['deleteSuccess'])
});
