import { StorageKey } from '../constants/storageKeys';
import Cookies from 'universal-cookie';

function getStorageKey(key) {
  return new Promise(async (resolve, reject) => {
    try {
      const data = JSON.parse(localStorage.getItem(key));
      if (data) {
        resolve(data);
      } else {
        resolve({});
      }
    } catch (error) {
      console.log(`Get ${key} error is ${error}`);
      reject({});
    }
  });
}

function setStorageKey(key, data) {
  try {
    localStorage.setItem(key, JSON.stringify(data));
    // console.log(`Set ${key} successfull`);
  } catch (error) {
    console.log(`Set ${key} error is ${error}`);
  }
}

function removeStorageKey(key) {
  try {
    localStorage.removeItem(key);
    // console.log(`Remove ${key} successfull`);
  } catch (error) {
    console.log(`Remove ${key} error is ${error}`);
  }
}

function removeAll() {
  try {
    localStorage.clear();
  } catch (error) {
    console.log(`Remove all clear error is ${error}`);
  }
}

const saveToken = async (token, oldAuthen) => {
  try {
    const cookies = new Cookies();
    cookies.set(StorageKey.USER_TOKEN, token);
    cookies.set(StorageKey.SMS, oldAuthen);
    return true;
  } catch (error) {
    return false;
  }
};

const clearToken = async () => {
  await localStorage.removeItem(...[StorageKey.USER_PROFILE, StorageKey.AVATAR_INFO]);
  const cookies = await new Cookies();
  cookies.remove(StorageKey.USER_TOKEN);
};

const clearCookie = async key => {
  const cookies = await new Cookies();
  cookies.remove(key);
};

const setCookie = async (key, data) => {
  const cookies = new Cookies();
  cookies.set(key, data);
};

const getToken = () => {
  try {
    const cookies = new Cookies();
    return cookies.get(StorageKey.USER_TOKEN);
  } catch (error) {
    return '';
  }
};

export {
  getStorageKey,
  setStorageKey,
  removeStorageKey,
  saveToken,
  clearToken,
  getToken,
  clearCookie,
  setCookie,
  removeAll
};
