import { fetch } from '.';
const CategoryAPIs = {
  getCategory: () => {
    return fetch('ProductCategory', 'get');
  },
  createCategory: (payload: any) => {
    return fetch('ProductCategory', 'post', payload);
  },
  editCategory: (payload: any) => {
    return fetch(`ProductCategory?id=${payload.id}`, 'put', payload);
  },
  deleteCategory: (payload: any) => {
    return fetch(`ProductCategory?id=${payload}`, 'delete', payload);
  },

  getCategoryById: (payload: any) => {
    return fetch(`ProductCategory/${payload}`, 'get');
  }
};

export { CategoryAPIs };
