import React, { Component } from 'react';
import { history } from '../../../../redux/router';
import MenuCollection from '../../component/menuCollection';
import './search_tution.scss';
const listBill = [
  {
    id: 0,
    amount: 360150,
    term: '02/2019',
    fromDate: '07/01/2019 00:00:00',
    toDate: '09/02/2019 00:00:00'
  },
  {
    id: 1,
    amount: 400150,
    term: '03/2019',
    fromDate: '07/02/2019 00:00:00',
    toDate: '09/03/2019 00:00:00'
  }
];
class ListSearchTution extends Component {
  renderItemlist() {
    let result = '';
    if (this.props.location.state && this.props.location.state) {
      let list = this.props.location.state;
      result = list.map((item, index) => {
        return (
          <div
            className="detail-litst-order"
            key={index}
            style={{ backgroundColor: index % 2 === 0 ? '#fbfcff' : 'white' }}
          >
            <div className="item_search">{item.name}</div>

            <div className="item_search nameSchool">{item.nameSchool}</div>
            <div className="item_search class">{item.class}</div>
            <div className="item_search birth">{item.birth}</div>

            <button
              type="button"
              className="btn btn-default item_search studentCode"
              onClick={() => this.getInfoStudent(item)}
            >
              {item.studentCode}
            </button>
          </div>
        );
      });
      return result;
    }
  }
  getInfoStudent = item => {
    console.log('getInfoStudent', item);
    let data = {
      infoStudent: item,
      dataBill: listBill
    };
    history.push('/info-tuition', data);
  };
  render() {
    console.log(this.props.location.state);
    return (
      <div className="list_search_tution">
        <MenuCollection />
        <div className="header_tuition">
          <span>Thanh toán học phí SSC</span>
        </div>
        <hr className="line-header" />
        <div className="list-order-waiting">
          <div className="title-litst-order">
            <div className="item-title">Họ tên</div>
            <div className="item-title">Trường</div>
            <div className="item-title">Lớp</div>
            <div className="item-title">Năm sinh</div>
            <div className="item-title">Mã HS/Sinh viên</div>
          </div>
          {this.renderItemlist()}
        </div>
      </div>
    );
  }
}
export default ListSearchTution;
