import React from 'react';
import './styles.scss';
import Loading from '../Loading';
import PropTypes from 'prop-types';
// import { lang, withTranslation } from '../../locales';

const Button = props => {
  const { error, messageError, className, loading, name, type, disabled, primary, icon, classIcon, onClick, style } = props;
  return (
    <React.Fragment>
      {error === false ? <span className="error">{messageError}</span> : null}
      <button type={type} className={primary === true ? 'btn btnGreen' : className} disabled={disabled} onClick={onClick} style={style}>
        {icon && loading === false ? <img className={classIcon} src={icon} alt="icon" /> : null}
        {loading === true ? (
          <center>
            <Loading className="loading" />
          </center>
        ) : (
          name
        )}
      </button>
    </React.Fragment>
  );
};
Button.defaultProps = {
  className: 'btn btnGold',
  disabled: false,
  loading: false,
  error: false,
  // messageError: 'msgConnectionError',
  name: 'btnNext',
  type: 'submit',
  primary: false,
  icon: '',
  classIcon: 'classIcon'
};

Button.propTypes = {
  error: PropTypes.bool,
  messageError: PropTypes.string,
  className: PropTypes.string,
  loading: PropTypes.bool,
  name: PropTypes.string,
  type: PropTypes.string,
  disabled: PropTypes.bool,
  primary: PropTypes.bool,
  icon: PropTypes.string,
  classIcon: PropTypes.string,
  onClick: PropTypes.func,
  style: PropTypes.object
};

export default Button;
