import React, { Component } from 'react';
import { icons } from '../../../../assets';
import QRCode from 'qrcode.react';
import { Link } from 'react-router-dom';
import { formatCurrency } from '../../../../commons/utils';
import '../styles/pop_up_pay.scss';

class PopUpPay extends Component {
  renderListOrder() {
    let result = '';
    if (this.props.viewData && this.props.viewData.orderItems) {
      result = this.props.viewData.orderItems.map((item, index) => {
        return (
          <div className="item-product-popup" key={index}>
            <div className="name-product-pop-up ">{item.productName}</div>
            <div className="number-product-popup">{item.quantity}</div>
            <div className="amount-product-popup">{formatCurrency(item.price * item.quantity)}</div>
          </div>
        );
      });
      return result;
    }
  }

  renderTotalAmount() {
    if (this.props.viewData) {
      const itemOrder = this.props.viewData;
      return (
        <div className="view-total-amount">
          <div className="item-product-popup">
            <div className="name-product-pop-up ">Tạm tính</div>
            <div className="number-product-popup"></div>
            <div className="amount-product-popup">{formatCurrency(itemOrder.amount)}</div>
          </div>
          <div className="item-product-popup">
            <div className="name-product-pop-up ">Giảm giá</div>
            <div className="number-product-popup" style={{ color: '#b3b3b3' }}>
              {itemOrder.promoCode}
            </div>
            <div className="amount-product-popup"></div>
          </div>
          <hr className="line-name-column" />
          <div className="item-product-popup">
            <div className="name-product-pop-up ">Số tiền cần thanh toán</div>
            <div className="number-product-popup"></div>
            <div
              className="amount-product-popup"
              style={{ color: '#f25366', fontSize: '1.2rem', fontWeight: 'bold' }}
            >
              {formatCurrency(itemOrder.amount)}
            </div>
          </div>
        </div>
      );
    }
  }

  DeleteOrder = () => {
    console.log('DeleteOrder');

    this.props.onDeleteOrder();
  };

  testActionOrder = () => {
    this.props.onCheckStatusBill();
  };

  renderDetailOrder() {
    const { viewData } = this.props;
    let status = '';
    if (typeof viewData.status !== 'undefined') {
      if (viewData.status === 1) {
        status = 'Đã thanh toán';
      } else {
        if (viewData.status === 0) {
          status = 'Chưa thanh toán';
        } else {
          status = 'Quá hạn xử lý';
        }
      }
    } else {
      status = 'Chưa thanh toán';
    }

    return (
      <div className="pop_up_pay" style={{ minWidth: '500px', position: 'relative' }}>
        <div
          className="detail-order-pop-up"
          style={{
            width: viewData.qrCode !== '' ? '70%' : '100%'
          }}
        >
          <div className="name-order-popup">
            <span>Đơn hàng #{viewData && viewData.billCode}</span>
            <span
              className="status-order"
              style={{ marginRight: viewData.qrCode !== '' ? '0px' : '30px' }}
            >
              {status}
            </span>
          </div>
          <div className="name-column">
            <div className="name-product-pop-up ">Sản phẩm</div>
            <div className="number-product-popup">Số lượng</div>
            <div className="amount-product-popup">Số tiền</div>
          </div>
          <hr className="line-name-column" />
          {this.renderListOrder()}
          <hr className="line-name-column" />
          {this.renderTotalAmount()}
          {viewData.status === 0 ? (
            <ul className="action-order">
              <li className="item">
                <Link onClick={() => this.testActionOrder()} className="btn btn-default">
                  Kiểm tra lại
                </Link>
              </li>
              <li className="item">
                <Link className="btn btn-default" onClick={() => this.DeleteOrder()}>
                  Huỷ đơn hàng
                </Link>
              </li>
            </ul>
          ) : null}
        </div>
        {viewData.qrCode !== '' ? (
          <div className="qr-code-pop-up">
            <p>Thanh toán bằng mã QR bên dưới</p>
            <QRCode value={viewData && viewData.qrCode} />
          </div>
        ) : null}
      </div>
    );
  }

  render() {
    // console.log(this.props.viewData);
    return (
      <div className="content-pop-up">
        <div className="viewClose">
          <div className="icon-close">
            <img src={icons.icon_close} alt="" onClick={() => this.props.closeModal()} />
          </div>
        </div>

        {this.renderDetailOrder()}
      </div>
    );
  }
}
export default PopUpPay;
