import { status } from '../helper';

export const types = {
  GET_DISTRICT: `${status.start}GET_DISTRICT`,
  GET_DISTRICT_SUCCESS: `${status.success}GET_DISTRICT`,
  GET_DISTRICT_FAILURE: `${status.failure}GET_DISTRICT`,
  GET_DISTRICT_CANCEL: `${status.cancel}GET_DISTRICT`
};

export const actions = {
  getDistrict: (payload?: any) => ({ type: types.GET_DISTRICT, payload }),
  getDistrictSuccess: (payload?: any) => ({ type: types.GET_DISTRICT_SUCCESS, payload }),
  getDistrictFailure: (payload?: any) => ({ type: types.GET_DISTRICT_FAILURE, payload }),
  getDistrictCancel: () => ({ type: types.GET_DISTRICT_CANCEL })
};
