import { of, from } from 'rxjs';
import { ofType, ActionsObservable } from 'redux-observable';
import { takeUntil, catchError, switchMap } from 'rxjs/operators';
import { combineEpics } from 'redux-observable';
// import _ from 'lodash';
import { types, actions } from './actions';
import { OrderAPIs } from '../../services';

interface Action {
  type: string;
  payload?: any;
}

const getOrder = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_ORDER),
    switchMap(action => {
      return from(OrderAPIs.getOrder()).pipe(
        switchMap((data: any) => {
          return of(actions.getOrderSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_ORDER_CANCEL))),
        catchError(error => {
          return of(actions.getOrderFailure(error.message));
        })
      );
    })
  );
};

const getOrderById = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_ORDER_ID),
    switchMap(action => {
      const payload = action.payload;
      const onSuccess = payload;
      const orderId = payload.data;
      // console.log('ac', payload);
      return from(OrderAPIs.getOrderById(orderId)).pipe(
        switchMap((data: any) => {
          // console.log('getOrderById', data);
          // let newData = Object.assign({}, data, {
          //   orderItems: data.merchantOrderItems,
          //   qrCode: _.isEmpty(data.merchantBills) ? '' : data.merchantBills[0].qrcode
          // });
          if (onSuccess) {
            payload.onSuccess(data);
          }
          return of(actions.getOrderIdSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_ORDER_ID_CANCEL))),
        catchError(error => {
          return of(actions.getOrderIdFailure(error.message));
        })
      );
    })
  );
};

const createOrder = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.CREATE_ORDER),
    switchMap(action => {
      const actionPayload = action.payload;
      const payload = actionPayload.data;
      const onSuccess = actionPayload.onSuccess;
      return from(OrderAPIs.createOrder(payload)).pipe(
        switchMap((data: any) => {
          // console.log('data', data);
          let newData = Object.assign({}, payload, data);
          if (onSuccess) {
            onSuccess(newData);
          }
          return of(actions.createOrderSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.CREATE_ORDER_CANCEL))),
        catchError(error => {
          return of(actions.createOrderFailure(error.message));
        })
      );
    })
  );
};

const updateOrder = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.UPDATE_ORDER),
    switchMap(action => {
      const payload = action.payload;

      return from(OrderAPIs.editOrder(payload)).pipe(
        switchMap((data: any) => {
          console.log('data', data);

          return of(actions.updateOrderSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.UPDATE_ORDER_CANCEL))),
        catchError(error => {
          return of(actions.updateOrderFailure(error.message));
        })
      );
    })
  );
};

const deleteOrderCode = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.DELETE_ORDER_CODE),
    switchMap(action => {
      const payload = action.payload;
      const { code, onSuccess } = payload;
      return from(OrderAPIs.deleteOrderCode(code)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }

          return of(actions.deleteOrderCodeSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.DELETE_ORDER_CODE_CANCEL))),
        catchError(error => {
          return of(actions.deleteOrderCodeFailure(error.message));
        })
      );
    })
  );
};

const getStaticQrCode = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_STATIC_QRCODE),
    switchMap(action => {
      return from(OrderAPIs.getStaticQrCode()).pipe(
        switchMap((data: any) => {
          return of(actions.getStaticQRCodeSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_STATIC_QRCODE_CANCEL))),
        catchError(error => {
          return of(actions.getStaticQRCodeFailure(error.message));
        })
      );
    })
  );
};

export default combineEpics(
  getOrder,
  getOrderById,
  createOrder,
  updateOrder,
  deleteOrderCode,
  getStaticQrCode
);
