import { fetch } from '.';
const BillAPIs = {
  getBillAll: () => {
    return fetch('Bill', 'get');
  },

  getBillPage: (payload: any) => {
    return fetch(`Bill/page/${payload}`, 'get');
  },

  getBillSearch: (payload: any) => {
    return fetch(
      `Bill/search/${payload.code}/${payload.from}/${payload.to}/${payload.page}/${payload.limit}`,
      'get'
    );
  },

  getBillCode: (payload: any) => {
    return fetch(`Bill/code/${payload}`, 'get');
  },

  createBill: (payload: any) => {
    return fetch('Bill', 'post', payload);
  },

  getBillById: (payload: any) => {
    return fetch(`Bill/id/${payload}`, 'get');
  }
};

export { BillAPIs };
