import { create, ApisauceConfig, ApiResponse } from 'apisauce';
import { baseURL, TIMEOUT } from '../constants';
import { authAPIs } from './auth';
import { accountAPIs } from './account';
import { history } from '../redux/router';
import { productAPIs } from './product';
import { CategoryAPIs } from './category';
import { OrderAPIs } from './order';
import { BillAPIs } from './bill';
import { DistrictAPIs } from './district';
import * as localStorage from '../commons/localStorage';

interface ErrorResponse {
  code: number | string | undefined;
  message: string;
  scope: 'HTTP_ERROR' | 'PHUTRAN_HTTP_ERROR' | 'CORE_HTTP_ERROR' | 'CORE_LOGIC_ERROR';
}

const apisauceInstance = create({ baseURL, timeout: TIMEOUT });

const fetch = (
  url: string,
  method: 'get' | 'post' | 'put' | 'delete',
  params?: {} | undefined,
  config?: ApisauceConfig | undefined
): Promise<any> => {
  const request = Object(apisauceInstance)[method](url, params, config);

  return new Promise((resolve, reject) => {
    request
      .then((res: ApiResponse<any>) => {
        console.log(':::::RESPONSE:::::', res);

        const _data = res.data;
        const _status = res.status;

        if (_status === 200) {
          resolve(_data);
        } else {
          const errorMessage = res.data;
          throw { code: _status, message: errorMessage };
        }
      })
      .catch((error: ErrorResponse) => {
        console.log('servicers', error);

        if (error.code === 401) {
          localStorage.removeAll();
          reject(error);
          return history.push(`/`);
        }
        reject(error);
      });
  });
};

export {
  apisauceInstance,
  fetch,
  authAPIs,
  accountAPIs,
  productAPIs,
  CategoryAPIs,
  OrderAPIs,
  BillAPIs,
  DistrictAPIs
};
