import Immutable from 'seamless-immutable';
import { AppState } from '../state';

import { types } from './actions';

const initialState = Immutable({
  fetchingOrder: 0,
  listOrder: [],
  orderById: {},
  fetchingOrderById: 0,
  actionFetching: 0,
  createSuccess: {},
  staticQRCode: ''
});

export const reducer = (
  state = initialState,
  { type, payload }: { type: string, payload?: any }
) => {
  switch (type) {
    case types.GET_ORDER:
      return state.set('fetchingOrder', 1);
    case types.GET_ORDER_SUCCESS:
      return state.merge({ fetchingOrder: 0, listOrder: payload });
    case types.GET_ORDER_FAILURE:
      return state.set('fetchingOrder', -1);
    case types.GET_ORDER_CANCEL:
      return state.set('fetchingOrder', 0);

    case types.GET_ORDER_ID:
      return state.set('fetchingOrderById', 1);
    case types.GET_ORDER_ID_SUCCESS:
      return state.merge({ fetchingOrderById: 0, orderById: payload });
    case types.GET_ORDER_ID_FAILURE:
      return state.set('fetchingOrderById', -1);
    case types.GET_ORDER_ID_CANCEL:
      return state.set('fetchingOrderById', 0);

    case types.CREATE_ORDER:
      return state.set('actionFetching', 1);
    case types.CREATE_ORDER_SUCCESS:
      return state.merge({ actionFetching: 0, createSuccess: payload });
    case types.CREATE_ORDER_FAILURE:
      return state.set('actionFetching', -1);
    case types.CREATE_ORDER_CANCEL:
      return state.set('actionFetching', 0);

    case types.DELETE_ORDER_CODE:
      return state.set('actionFetching', 1);
    case types.DELETE_ORDER_CODE_SUCCESS:
      return state.merge({ actionFetching: 0 });
    case types.DELETE_ORDER_CODE_FAILURE:
      return state.set('actionFetching', -1);
    case types.DELETE_ORDER_CODE_CANCEL:
      return state.set('actionFetching', 0);

    case types.GET_STATIC_QRCODE:
      return state.set('actionFetching', 1);
    case types.GET_STATIC_QRCODE_SUCCESS:
      return state.merge({ actionFetching: 0, staticQRCode: payload });
    case types.GET_STATIC_QRCODE_FAILURE:
      return state.set('actionFetching', -1);
    case types.GET_STATIC_QRCODE_CANCEL:
      return state.set('actionFetching', 0);

    case 'RESET':
      return initialState;

    default:
      return state;
  }
};

export const selectors = (state: AppState) => ({
  fetchingOrder: state.order.getIn(['fetchingOrder']),
  fetchingOrderById: state.order.getIn(['fetchingOrderById']),
  actionFetching: state.order.getIn(['actionFetching']),
  listOrder: state.order.getIn(['listOrder']),
  orderById: state.order.getIn(['orderById']),
  createSuccess: state.order.getIn(['createSuccess']),
  staticQRCode: state.order.getIn(['staticQRCode'])
});
