import React, { Component } from 'react';
import './change_password.scss';
import { UI } from '../../components';

class ChangePinCode extends Component {
  render() {
    return (
      <div className="change_pin_code">
        <div className="change_password">
          <div className="title_change_password">
            <div className="line_title">
              <span>Đổi mã Pin</span>
            </div>
            <hr className="line-header" />
          </div>
          <div className="box_chage_pass">
            <UI.Formik
              initialValues={{ oldPinCode: '', newPinCode: '', reNewPinCode: '' }}
              validationSchema={UI.yup.object().shape({
                oldPinCode: UI.yup.string().required('not null'),
                newPinCode: UI.yup.string().required('not null'),
                reNewPinCode: UI.yup.string().required('not null')
              })}
              onSubmit={this.onHandleSubmit}
              render={props => (
                <form onSubmit={props.handleSubmit}>
                  <div className="box-input">
                    <div className="title-product-code">Mã pin cũ</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="oldPinCode"
                        type="oldPinCode"
                        placeholder="Mã pin cũ"
                        value={props.values.oldPinCode}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Mã Pin mới</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="newPinCode"
                        type="newPinCode"
                        placeholder="Mã Pin mới"
                        value={props.values.newPinCode}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Nhập lại mã Pin mới</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="reNewPinCode"
                        type="reNewPinCode"
                        placeholder="Nhập lại mã Pin mới"
                        value={props.values.reNewPinCode}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="group-btn-pop-up">
                    <UI.Button type="submit" name="Lưu" className="btn-pop-up" />
                    <UI.Button type="button" name="Huỷ" className="btn-pop-up" />
                  </div>
                </form>
              )}
            />
          </div>
        </div>
      </div>
    );
  }
}
export default ChangePinCode;
