import React, { Component } from 'react';
import { icons } from '../../../../assets';
import { UI } from '../../../../components';
import './addCaterory.scss';
import { Formik } from 'formik';

interface Form {
  namecaterory: string;
}
class AddCaterory extends Component {
  myRef: React.RefObject<Formik<Form>>;
  constructor(props) {
    super(props);

    this.myRef = React.createRef();
  }
  onHandleSubmit = values => {
    const { namecaterory } = values;
    const { type, itemEdit, onEditCategory, onCreateCategory } = this.props;
    console.log('onHandleSubmit', namecaterory, itemEdit);
    if (type) {
      if (onEditCategory) {
        onEditCategory({ id: itemEdit.id, categoryName: namecaterory });
      }
    } else {
      if (onCreateCategory) {
        onCreateCategory(namecaterory);
      }
    }
  };

  componentDidMount() {
    this.genDateEdit();
  }
  // formRef = React.createRef();
  genDateEdit() {
    const { type, itemEdit } = this.props;

    if (type) {
      console.log('genDateEdit', type, itemEdit, this.myRef);
      if (this.myRef.current) {
        // this.myRef.current.setFieldValue('namecaterory', itemEdit.categoryName);
        console.log('item', itemEdit);
      }
    }
  }
  render() {
    const { type } = this.props;
    return (
      <div className="add-caterory">
        <div className="titie-add-caterory">
          <div className="content-titie-add-caterory">
            <div className="titie-add-caterory-left">
              <span>Tạo danh mục mới</span>
            </div>
            <div className="titie-add-caterory-right">
              <img
                src={icons.icon_close_popup}
                className="img-responsive"
                alt="Icon close"
                onClick={() => {
                  this.props.closeModal();
                }}
              />
            </div>
          </div>
        </div>
        <Formik
          ref={this.myRef}
          initialValues={{ namecaterory: '' }}
          validationSchema={UI.yup.object().shape({
            namecaterory: UI.yup.string().required('not null')
          })}
          onSubmit={this.onHandleSubmit}
          render={props => (
            <form onSubmit={props.handleSubmit}>
              <div className="box-add-caterory">
                <div className="descriptionCaterory">
                  <div className="title-catalog-description">Tên danh mục</div>
                  <div className="card-cover">
                    <UI.Field
                      {...props}
                      ref={this.myRef}
                      name="namecaterory"
                      type="text"
                      placeholder="Tên danh mục"
                      value={props.values.namecaterory}
                      onChange={props.handleChange}
                      autoComplete="off"
                    />
                  </div>
                </div>
              </div>
              <div className="group-btn-pop-up">
                <UI.Button type="submit" name={type ? 'Cập nhật' : 'Tạo'} className="btn-pop-up" />
                <UI.Button
                  name="Huỷ"
                  className="btn-pop-up"
                  onClick={() => {
                    this.props.closeModal();
                  }}
                />
              </div>
            </form>
          )}
        />
      </div>
    );
  }
}
export default AddCaterory;
