import React, { Component } from 'react';
import { connect } from 'react-redux';
import { authActions } from '../../../redux/auth';
import { Dispatch } from 'redux';
import './styles/signIn.scss';
import { icons } from '../../../assets';
import FormLogin from './formLogin';
import FormRegister from './formRegister';
// import { lang, withTranslation } from '../../../locales';

class SignIn extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  showPassword() {
    this.setState({ visiblePass: !this.state.visiblePass });
  }

  rederTabbar = () => {
    return (
      <div>
        <ul className="nav nav-tabs nav-tabs-login" id="myTab" role="tablist">
          <li className="nav-item login-tabs">
            <a
              className="nav-link tab-link active"
              id="home-tab"
              data-toggle="tab"
              href="#home"
              role="tab"
              aria-controls="home"
              aria-selected="true"
            >
              Đăng nhập
            </a>
          </li>
          <li className="nav-item login-tabs">
            <a
              className="nav-link tab-link"
              id="profile-tab"
              data-toggle="tab"
              href="#profile"
              role="tab"
              aria-controls="profile"
              aria-selected="false"
            >
              Đăng ký
            </a>
          </li>
        </ul>
        <div className="tab-content" id="myTabContent">
          <div
            className="tab-pane fade show active"
            id="home"
            role="tabpanel"
            aria-labelledby="home-tab"
          >
            <FormLogin />
          </div>
          <div className="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab">
            <FormRegister />
          </div>
        </div>
      </div>
    );
  };

  onHandleSubmit = values => {
    const { username, password } = values;
    // this.props.dispatchSignIn(`username=${username}&password=${password}&grant_type=password`);
    // const payload = { Username: username, Password: password, grant_type: 'Password' };
    // var payload = new FormData();

    // payload.append('username', username);
    // payload.append('Password', password);
    // payload.append('grant_type', 'Password');

    const payload = `username=${username}&password=${password}&grant_type=password`;

    this.props.login(payload);
  };

  render() {
    return (
      <div className="signin">
        <div className="content-signin">
          <div className="viewLogo">
            <img src={icons.logo_ecpay} className="img-responsive logo-ecpay" alt="logo" />
          </div>
          {this.rederTabbar()}
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
  login: (payload: any) => dispatch(authActions.login(payload))
});

const mapStateToProps = state => ({
  // signInFalse: state.auth.signInFalse
});

export default connect(mapStateToProps, mapDispatchToProps)(SignIn);
