import Immutable from 'seamless-immutable';
import { AppState } from '../state';

import { types } from './actions';

const initialState = Immutable({
  fetchingProduct: 0,
  listProduct: [],
  productById: {},
  fetchingProductById: 0
});

export const reducer = (
  state = initialState,
  { type, payload }: { type: string, payload?: any }
) => {
  switch (type) {
    case types.GET_PRODUCT:
      return state.set('fetchingProduct', 1);
    case types.GET_PRODUCT_SUCCESS:
      return state.merge({ fetchingProduct: 0, listProduct: payload });
    case types.GET_PRODUCT_FAILURE:
      return state.set('fetchingProduct', -1);
    case types.GET_PRODUCT_CANCEL:
      return state.set('fetchingProduct', 0);

    case types.GET_PRODUCT_ID:
      return state.set('fetchingProductById', 1);
    case types.GET_PRODUCT_ID_SUCCESS:
      return state.merge({ fetchingProduct: 0, productById: payload });
    case types.GET_PRODUCT_ID_FAILURE:
      return state.set('fetchingProductById', -1);
    case types.GET_PRODUCT_ID_CANCEL:
      return state.set('fetchingProductById', 0);

    case 'RESET':
      return initialState;

    default:
      return state;
  }
};

export const selectors = (state: AppState) => ({
  fetchingProduct: state.product.getIn(['fetchingProduct']),
  fetchingProductById: state.product.getIn(['fetchingProductById']),
  listProduct: state.product.getIn(['listProduct']),
  productById: state.product.getIn(['productById'])
});
