import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import { StorageKey } from '../../commons/constants/storageKeys';

import { apisauceInstance } from '../../services';

if (localStorage.getItem(StorageKey.USER_TOKEN)) {
  let token = JSON.parse(localStorage.getItem(StorageKey.USER_TOKEN));
  apisauceInstance.setHeader('Authorization', token.token);
}

export const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      localStorage.getItem(StorageKey.USER_TOKEN) ? (
        <Component {...props} {...rest} />
      ) : (
        <Redirect to={{ pathname: '/', state: { from: props.location } }} />
      )
    }
  />
);
