import React, { Component } from 'react';
import MenuCollection from '../../../../component/menuCollection';
import { history } from '../../../../../../redux/router';
import { DateRangePicker } from 'react-dates';
import Modal from 'react-modal';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import { UI } from '../../../../../../components';
import AmountPerson from '../popupChooseAmount';
import 'moment/locale/vi';
import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import './form_booking.scss';

const customStyles = {
  content: {
    top: '53%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    padding: '0px'
  }
};

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  textField: {
    padding: '6px 12px',
    margin: '0px',
    width: '100%'
  },
  menu: {
    width: 200
  }
}));
const listBtn = [
  { id: 1, title: 'Một chiều' },
  { id: 2, title: 'Khứ hồi' }
];
const listDeparture = [
  {
    value: 1,
    codeAirport: 'DAD',
    city: 'Đà Nẵng',
    country: 'Việt Nam'
  },
  {
    value: 2,
    codeAirport: 'SGN',
    city: 'TP HCM',
    country: 'Việt Nam'
  }
];
const listTicketType = [
  { id: 1, label: 'Phổ thông' },
  { id: 2, label: 'Thương gia' },
  { id: 3, label: 'Hạng nhất' }
];
class FormBooking extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectItem: listBtn[0].id,
      departure: '',
      destination: '',
      startDate: '',
      endDate: '',
      amountBeliever: 1,
      amountChildren: 0,
      amountBabies: 0,
      modalIsOpen: false,
      ticketType: ''
    };
  }
  toggleClass = id => {
    if (this.state.selectItem === id) {
      return;
    }
    this.setState({
      selectItem: id,
      display: !this.state.display
    });
  };
  openModal = () => {
    this.setState({
      modalIsOpen: true
    });
  };

  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };

  renderBtn() {
    let result = '';
    result = listBtn.map((item, index) => {
      return (
        <Button
          key={index}
          className={
            this.state.selectItem === item.id
              ? `btn btn-link ${item.className} active`
              : `btn btn-link ${item.className}`
          }
          onClick={this.toggleClass.bind(this, item.id)}
        >
          {item.title}
        </Button>
      );
    });
    return result;
  }
  _onHandleChange = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    this.setState(
      {
        [name]: value
      },
      () => {
        console.log('name', this.state.role);
      }
    );
  };
  _setStatesFocusedInput = focusedInput => {
    this.setState(
      {
        focusedInput: focusedInput
      },
      () => {
        console.log(this.state.focusedInput);
      }
    );
  };
  renderDropDownDeparture() {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="departure"
        select
        label="Chọn điểm khởi hành"
        className={classes.textField}
        value={this.state.departure}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listDeparture.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.city} ({option.codeAirport}) - {option.country}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  renderDropDownTicketType() {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="ticketType"
        select
        label="Hạng ghế"
        className={classes.textField}
        value={this.state.ticketType}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listTicketType.map(option => (
          <MenuItem key={option.id} value={option.id}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  renderDropDownDestination() {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="destination"
        select
        label="Chọn điểm đến"
        className={classes.textField}
        value={this.state.destination}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listDeparture.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.city} ({option.codeAirport}) - {option.country}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  btnAmountPerson = (data: {}) => {
    console.log('list tra ve', data);
    this.setState({
      amountBeliever: data ? data[0].quantity : this.state.amountBeliever,
      amountChildren: data ? data[1].quantity : this.state.amountChildren,
      amountBabies: data ? data[2].quantity : this.state.amountBabies
    });
  };

  onHandleSubmit = () => {
    let startDate = new Date(this.state.startDate).getTime();
    let endDate = new Date(this.state.endDate).getTime();

    let ticketType = listTicketType.filter(item => {
      return item.id === this.state.ticketType;
    });
    let departure = listDeparture.filter(item => {
      return item.value === this.state.departure;
    });
    let destination = listDeparture.filter(item => {
      return item.value === this.state.destination;
    });
    let selectItem = listBtn.filter(item => {
      return item.id === this.state.selectItem;
    });
    let data = {
      departure: departure,
      ticketType: ticketType,
      destination: destination,
      startDate: startDate,
      endDate: endDate,
      amountBeliever: this.state.amountBeliever,
      amountChildren: this.state.amountChildren,
      amountBabies: this.state.amountBabies,
      status: selectItem
    };
    console.log('onHandleSubmit', data);
    history.push('/flight-information', data);
  };
  render() {
    return (
      <div className="form_booking">
        <MenuCollection />
        <div className="header_air_ticket">
          <span>Mua vé máy bay</span>
        </div>
        <hr className="line-header" />
        <div className="content_form">
          <ButtonGroup fullWidth aria-label="full width outlined button group">
            {this.renderBtn()}
          </ButtonGroup>
          <UI.Formik
            initialValues={{}}
            validationSchema={UI.yup.object().shape({})}
            onSubmit={this.onHandleSubmit}
            render={props => (
              <form onSubmit={props.handleSubmit}>
                <div className="box-input">
                  <div className="card-cover">{this.renderDropDownDeparture()}</div>
                  {/* <div className="err-caterory">
                    {(this.state && this.state.showErrorComboboxRole) || ''}
                  </div> */}
                </div>
                <div className="box-input">
                  <div className="card-cover">{this.renderDropDownDestination()}</div>
                  {/* <div className="err-caterory">
                    {(this.state && this.state.showErrorComboboxRole) || ''}
                  </div> */}
                </div>
                <DateRangePicker
                  startDate={this.state.startDate}
                  endDate={this.state.endDate} // momentPropTypes.momentObj or null,
                  startDatePlaceholderText="Ngày đi"
                  endDatePlaceholderText="Ngày về"
                  onDatesChange={({ startDate, endDate }) => this.setState({ startDate, endDate })}
                  isOutsideRange={() => false}
                  focusedInput={this.state.focusedInput}
                  onFocusChange={focusedInput => this.setState({ focusedInput })}
                />

                <button
                  type="button"
                  class="btn btn-default amount_person"
                  onClick={this.openModal}
                >
                  <span>
                    {this.state.amountBeliever} Người Lớn, {this.state.amountChildren} Trẻ Em,{' '}
                    {this.state.amountBabies} Trẻ Sơ Sinh
                  </span>
                  <i className="fa fa-caret-down"></i>
                </button>
                <div className="box-input">
                  <div className="card-cover">{this.renderDropDownTicketType()}</div>
                  {/* <div className="err-caterory">
                    {(this.state && this.state.showErrorComboboxRole) || ''}
                  </div> */}
                </div>

                <UI.Button type="submit" name="Lưu" className="btn_add" />
              </form>
            )}
          />
        </div>
        <Modal
          isOpen={this.state.modalIsOpen}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Example Modal"
          ariaHideApp={false}
        >
          <AmountPerson
            closeModal={this.closeModal}
            item={this.state.item}
            addAmountPerson={this.btnAmountPerson}
          />
        </Modal>
      </div>
    );
  }
}
export default FormBooking;
