class HistoryOrder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalIsOpen: false,
      activePage: 1
    };
  }

  componentDidMount() {
    this.props.getBillPage(1);
  }

  handlePageChange = pageNumber => {
    console.log(`active page is ${pageNumber}`);
    this.setState({ activePage: pageNumber });
  };
  openModal = item => {
    this.setState({
      modalIsOpen: true,
      item: item
    });
  };
  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };

  changeOrder = (data: any) => {
    console.log('data', data);

    let order = {};
    order = Object.assign({}, data, {
      orderItems: data.merchantOrderItems,
      qrCode: _.isEmpty(data.merchantBills) ? '' : data.merchantBills[0].qrcode,
      billCode: data.code
    });
    return order;
  };

  getOrder = (item: any) => {
    const onSuccess = (data: any) => {
      this.setState({ viewData: this.changeOrder(data) }, () => this.openModal());
    };
    if (_.isEmpty(item)) {
      //thong bao shopping cart rong
    } else {
      this.props.getOrderId({ data: item.orderId, onSuccess });
    }
  };

  renderItemlist() {
    let result = '';
    if (this.props.listBill) {
      const { listBill } = this.props;
      result = listBill.map((item, index) => {
        let status = '';
        switch (item.status) {
          case 1:
            status = 'Đã thanh toán';
            break;
          case 2:
            status = 'Quá hạn xử lý';
            break;
          default:
            status = 'Chưa thanh toán';
            break;
        }
        return (
          <div
            className="detail-litst-order"
            key={index}
            style={{ backgroundColor: index % 2 === 0 ? '#fbfcff' : 'white' }}
          >
            <Link className="item-history code" to="#" onClick={() => this.getOrder(item)}>
              #{item.code}
            </Link>
            <div className="item-history amount">{formatCurrency(item.amount)}</div>
            <div className="item-history qrcode">{item.qrcode !== '' ? 'QRCode' : 'Tiền mặt'}</div>
            <div className="item-history status">{status}</div>
            <div className="item-history">{formatDateTime(item.createdTime)}</div>
          </div>
        );
      });
      return result;
    }
  }
  render() {
    return (
      <div className="page-history-order">
        <SubMenu />
        <div className="header-history-order">
          <span>Lịch sử thanh toán</span>
        </div>
        <div className="list-order-waiting">
          <div className="title-litst-order">
            <div className="item-title">Đơn hàng</div>
            <div className="item-title">Số tiền</div>
            <div className="item-title">Hình thức thanh toán</div>
            <div className="item-title">Trạng thái</div>
            <div className="item-title">Thời gian</div>
          </div>
          {this.renderItemlist()}
          {/* <Pagination
            activePage={this.state.activePage}
            itemsCountPerPage={10}
            totalItemsCount={450}
            pageRangeDisplayed={5}
            onChange={this.handlePageChange}
          /> */}
          <Pagination
            hideFirstLastPages
            pageRangeDisplayed={10}
            activePage={this.state.activePage}
            itemsCountPerPage={10}
            totalItemsCount={50}
            onChange={this.handlePageChange}
          />
          <Modal
            isOpen={this.state.modalIsOpen}
            onRequestClose={this.closeModal}
            style={customStyles}
            contentLabel="Example Modal"
            ariaHideApp={false}
          >
            <PopUpPay closeModal={this.closeModal} viewData={this.state.viewData} />
          </Modal>
        </div>
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(HistoryOrder);
