class OrderWaiting extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalIsOpen: false
    };
  }
  ondel = item => {
    console.log('ondel', item);
  };
  renderItemlist() {
    const listBill = this.props.listBill;
    let result = '';
    result = listBill.map((item, index) => {
      return (
        <div
          className="detail-litst-order"
          key={index}
          style={{ backgroundColor: index % 2 === 0 ? '#fbfcff' : 'white' }}
        >
          <Link className="order-code" to="#" onClick={() => this.openModal(item)}>
            #{item.code}
          </Link>
          <div className="order-amount">{formatCurrency(item.amount)}</div>
          <div className="order-time">{item.createdTime}</div>
          <Link to="#" className="icon-trash" onClick={this.ondel(item)}>
            <img
              src={icons.icon_del}
              class="img-responsive"
              alt="icon_del"
              style={{ height: '25px', marginRight: '5px' }}
            />
          </Link>
        </div>
      );
    });
    return result;
  }
  openModal = item => {
    this.setState({
      modalIsOpen: true,
      item: item
    });
  };
  closeModal = () => {
    this.setState({ modalIsOpen: false }, () => {
      console.log('closeModal');
    });
  };
  render() {
    return (
      <div className="list-order-waiting">
        <div className="title-litst-order">
          <div className="title-code">Đơn hàng</div>
          <div className="title-amount">Số tiền</div>
          <div className="title-time">Thời gian</div>
        </div>
        {this.renderItemlist()}
        <Modal
          isOpen={this.state.modalIsOpen}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Example Modal"
          ariaHideApp={false}
        >
          <PopUpPay closeModal={this.closeModal} itemOrder={itemOrder} item={this.state.item} />
        </Modal>
      </div>
    );
  }
}
export default OrderWaiting;
