class BillTelevision extends Component {
  constructor(props) {
    super(props);
    this.state = {
      providerCode: listProviderEVN[0].providerCode,
      customerCode: '',
      checkSaveBill: false,
      selectItemProvider: {}
    };
  }

  toggleClass = (id, item) => {
    this.setState({
      providerCode: id,
      selectItemProvider: item ? item : listProviderEVN[0]
    });
  };
  renderListProvider() {
    let result = '';
    result = listProviderEVN.map((item, index) => {
      return (
        <Link
          to="#"
          onClick={() => this.toggleClass(item.providerCode, item)}
          className={
            this.state.providerCode === item.providerCode ? 'item-bill active' : 'item-bill'
          }
          key={index}
        >
          <img src={item.icon} className="img-responsive icon-service" alt={item.name} />

          <div style={{ textAlign: 'center' }}>
            <p>{item.name}</p>
          </div>
        </Link>
      );
    });
    return result;
  }
  _handleChange = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    this.setState(
      {
        [name]: value
      },
      () => {
        console.log('name', this.state.customerCode);
      }
    );
  };
  onHandleSubmit = values => {
    console.log('onHandleSubmit', values, this.state);
    let data = {
      dataBill: dataBill,
      infoProvider: this.state.selectItemProvider,
      customerCode: values.keyword
    };
    history.push('/info-bill-television', data);
  };
  handleChangeCheck = name => event => {
    console.log('handleChange');
    this.setState({ checkSaveBill: !this.state.checkSaveBill }, () => {
      console.log('checkB', this.state.checkSaveBill);
    });
  };

  render() {
    return (
      <div className="bill_electric">
        <MenuCollection />
        <div className="header_bill_electric">
          <span>Thanh toán hoá đơn truyền hình</span>
        </div>
        <hr className="line-header" />
        <div className="title-provider">Chọn nhà cung cấp</div>
        <div className="list_provider">{this.renderListProvider()}</div>
        <div className="title-provider">Nhập mã khách hàng hoặc số hợp đồng</div>
        <UI.Formik
          initialValues={{ keyword: '', saveBill: '' }}
          validationSchema={UI.yup.object().shape({
            keyword: UI.yup.string().required('not null')
          })}
          onSubmit={this.onHandleSubmit}
          render={props => (
            <form onSubmit={props.handleSubmit}>
              <div className="card-cover">
                <UI.Field
                  {...props}
                  name="keyword"
                  type="text"
                  placeholder="Nhập mã khách hàng hoặc số hợp đồng"
                  value={props.values.keyword}
                  onChange={props.handleChange}
                  autoComplete="off"
                />
              </div>
              <Link to="# " className="text-guide">
                Hướng dẫn xem mã khách hàng
              </Link>
              <p>
                <FormControlLabel
                  control={
                    <Checkbox
                      onChange={this.handleChangeCheck('checkSaveBill')}
                      value="checkSaveBill"
                      color="primary"
                      checked={this.state.checkSaveBill}
                    />
                  }
                  label="Lưu lại hoá đơn này"
                />
              </p>

              <UI.Button
                type="submit"
                name="Tiếp tục"
                className="btn-bill-electric"
                // loading={loading}
                // error={signInFalse.success}
                // messageError={messageError ? lang('signIn.' + messageError) : null}
                // disabled={loading}
              />
            </form>
          )}
        />
      </div>
    );
  }
}
export default BillTelevision;
