import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import { images } from '../../../../assets';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import { UI } from '../../../../components';
import SubMenu from '../../component/menuSell';
import './add_product.scss';

import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { productActions } from '../../../../redux/product';
import { categoryActions, categorySelectors } from '../../../../redux/category';
import { AppState } from '../../../../redux/state';
import { history } from '../../../../redux/router';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  // login: (payload: any) => dispatch(authActions.login(payload))
  createProduct: (payload: any) => dispatch(productActions.createProduct(payload)),
  editProduct: (payload: any) => dispatch(productActions.updateProduct(payload)),
  deleteProduct: (payload: any) => dispatch(productActions.deleteProduct(payload)),
  getCategory: (payload: any) => dispatch(categoryActions.getCategory(payload))
});

const mapStateToProps = (state: AppState) => ({
  // account: accountSelectors(state).account
  listCategory: categorySelectors(state).listCategory
});

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200
  },
  menu: {
    width: 200
  }
}));
const IOSSwitch = withStyles(theme => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1)
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: theme.palette.common.white,
      '& + $track': {
        backgroundColor: '#52d869',
        opacity: 1,
        border: 'none'
      }
    },
    '&$focusVisible $thumb': {
      color: '#52d869',
      border: '6px solid #fff'
    }
  },
  thumb: {
    width: 24,
    height: 24
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border'])
  },
  checked: {},
  focusVisible: {}
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked
      }}
      {...props}
    />
  );
});

class AddProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      namecaterory: '',
      checkedB: true,
      logo: images.none_image
    };
  }

  componentDidMount() {
    this.fetchData();
  }

  fetchData = () => {
    this.props.getCategory();
  };
  onSetState = (data: any) => {
    this.setState({ logo: data });
  };
  _fileChangedHandler = e => {
    e.stopPropagation();
    e.preventDefault();

    // FileList object.
    var files = e.target.files;

    var file = files[0];

    var fileReader = new FileReader();

    fileReader.onload = progressEvent => {
      var url = fileReader.result;

      // Something like: data:image/png;base64,iVBORw...Ym57Ad6m6uHj96js
      this.onSetState(url);

      var myImg = document.getElementById('myimage');
      myImg.src = url ? url : images.none_image;
    };

    // Read file asynchronously.
    fileReader.readAsDataURL(file); // fileReader.result -> URL.
  };
  changeCategoryList = (list: any) => {
    let data = [];

    list.forEach(element => {
      data.push({
        value: element.id,
        label: element.categoryName
      });
    });
    return data;
  };
  _onHandleChange = e => {
    const target = e.target;
    const name = target.name;
    const value = target.value;
    this.setState({
      [name]: value
    });
  };
  handleChangeCheck = name => event => {
    this.setState({ checkedB: !this.state.checkedB }, () => {
      console.log('checkB', this.state.checkedB);
    });
  };
  renderDropDown() {
    const classes = useStyles();
    const { listCategory } = this.props;
    const data = this.changeCategoryList(listCategory);
    return (
      <TextField
        id="standard-select-currency"
        name="namecaterory"
        select
        label=""
        className={classes.textField}
        value={this.state.namecaterory}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {data.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }

  imageBase64 = (base: string) => {
    let _imageBase64 = '';
    _imageBase64 = base.slice(base.indexOf(',') + 1);
    return _imageBase64;
  };

  onHandleSubmit = values => {
    // const { namecaterory, catalogCode, descriptionCaterory } = values;
    console.log('onHandleSubmit', values, this.imageBase64(this.state.logo));
    const { nameproduct, productCode, productPrice } = values;
    if (this.state && this.state.namecaterory !== '') {
      this.setState({ showErrorCombobox: '0' });
      console.log('namecategory', this.state.namecaterory);
      const data = {
        active: true,
        productName: nameproduct,
        sku: productCode,
        type: 'Product',
        thumbnail: this.state.logo === images.none_image ? '' : this.imageBase64(this.state.logo),
        description: '',
        price: productPrice,
        unit: 'chai',
        categoryId: this.state.namecaterory ? this.state.namecaterory : -1
      };
      const onSuccess = (data: any) => {
        history.push('/product-management');
      };

      this.props.createProduct({ data: data, onSuccess });
    } else {
      this.setState({ showErrorCombobox: 'Vui lòng chọn danh mục' });
    }
  };

  render() {
    console.log('render', this.props);
    let item,
      type = undefined;
    if (this.props.location.state && this.props.location.state.item) {
      item = this.props.location.state.item;
      type = this.props.location.state.item.type;
    }

    return (
      <div className="add_product">
        <SubMenu />
        <div className="header-add-product">
          <span>Tạo sản phẩm mới</span>
        </div>
        <hr className="line-header" />
        <UI.Formik
          initialValues={{ nameproduct: '', productCode: '', productPrice: '' }}
          validationSchema={UI.yup.object().shape({
            nameproduct: UI.yup.string().required('not null'),
            productCode: UI.yup.string().required('not null'),
            productPrice: UI.yup.string().required('not null')
          })}
          onSubmit={this.onHandleSubmit}
          render={props => (
            <form onSubmit={props.handleSubmit}>
              <div className="box-add-product">
                <div className="box-left">
                  <div className="box-input">
                    <div className="title-product-code">Tên sản phẩm</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="nameproduct"
                        type="text"
                        placeholder="Tên sản phẩm"
                        value={props.values.nameproduct || (item && item.productName)}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Mã sản phẩm</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="productCode"
                        type="text"
                        placeholder="Mã sản phẩm"
                        value={props.values.productCode || (item && item.sku)}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Giá sản phẩm</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="productPrice"
                        type="text"
                        placeholder="Giá sản phẩm"
                        value={props.values.productPrice || (item && item.price)}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Chọn danh mục:</div>
                    <div className="card-cover">{this.renderDropDown()}</div>
                    <div className="err-caterory">
                      {(this.state && this.state.showErrorCombobox) || ''}
                    </div>
                  </div>
                  <div className="condition">
                    <span className="title">Tình trạng hoạt động</span>
                    <FormControlLabel
                      control={
                        <IOSSwitch
                          checked={this.state.checkedB || (item && item.active)}
                          onChange={this.handleChangeCheck('checkedB')}
                          value="checkedB"
                        />
                      }
                    />
                  </div>
                </div>
                <div className="box-right">
                  <div className="content-box-right">
                    <p>Hình ảnh sản phẩm</p>

                    <div className="box-image">
                      <input
                        name="logo"
                        id="logo"
                        type="file"
                        style={{ display: 'none' }}
                        ref={ref => (this.myInput = ref)}
                        onChange={this._fileChangedHandler}
                      />
                      <div className="media-photo media-round" aria-hidden="true">
                        <img
                          id="myimage"
                          src={images.none_image}
                          width="210px"
                          alt="logo product"
                        />
                      </div>
                    </div>

                    <button
                      type="button"
                      className="btn btn-default"
                      onClick={e => this.myInput.click()}
                    >
                      Tải lên từ thư viện
                    </button>
                  </div>
                </div>
              </div>
              <div className="group-btn-pop-up">
                <UI.Button
                  type="submit"
                  name={item && type ? 'Cập nhật' : 'Lưu'}
                  className="btn-pop-up"
                />
                <UI.Button type="button" name="Huỷ" className="btn-pop-up" />
              </div>
            </form>
          )}
        />
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(AddProduct);
