import React, { Component } from 'react';
import { formatCurrency } from '../../commons/utils/format';
import './styles.scss';

class SelectBox extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedItem: '',
      showItems: false
    };
  }

  dropDown = () => {
    this.setState(prevState => ({
      showItems: !prevState.showItems
    }));
  };

  selectItem = value => {
    const { items } = this.props;
    this.setState({
      selectedItem: value,
      showItems: false
    });
    let account = value ? value : items[0];
    this.props.handleChange(account);
  };

  renderItemSelected() {
    const { selectedItem } = this.state;
    const { items, defaultData } = this.props;

    const select = selectedItem
      ? selectedItem
      : defaultData
        ? defaultData
        : items
          ? items[0]
          : [];

    if (select) {
      return (
        <ul>
          <li>{select.name}</li>
          <li>
            {select.value}
            <span>{select.currency}</span>
          </li>
        </ul>
      );
    }
  }
  getState = () => {
    return this.state.selectedItem ? this.state.selectedItem : this.props.items ? this.props.items[0] : '';

  };
  render() {
    const { items } = this.props;
    console.log('items', items)
    return (
      <React.Fragment>
        <div className='select-box--box'>
          <div className='select-box--container'>
            <div className='select-box--selected-item'>
              {this.renderItemSelected()}
            </div>

            <div className='select-box--arrow' onClick={this.dropDown}>
              <span
                className={`${
                  this.state.showItems
                    ? 'select-box--arrow-up'
                    : 'select-box--arrow-down'
                  }`}
              />
            </div>
          </div>

          <div
            className='select-box--items'
            style={{ display: this.state.showItems ? 'block' : 'none' }}
          >
            {items
              ? items.map(item => (
                <div
                  key={item.value}
                  onClick={() => this.selectItem(item)}
                  className={
                    this.state.selectedItem === item ? 'selected' : ''
                  }
                >
                  <ul>
                    <li>{item.name}</li>
                    <li>
                      {item.value}
                      <span>{item.currency}</span>
                      <span className='item-so-du'>
                        {formatCurrency(item.accessibleAssets)}
                      </span>
                    </li>
                  </ul>
                </div>
              ))
              : null}
          </div>
        </div>

        <input type='hidden' name={this.state.name} value='' />
      </React.Fragment>
    );
  }
}

export default SelectBox;
