import { fetch } from '.';
const OrderAPIs = {
  getOrder: () => {
    return fetch('Order', 'get');
  },
  createOrder: (payload: any) => {
    return fetch('Order', 'post', payload);
  },
  editOrder: (payload: any) => {
    return fetch('Order', 'put', payload);
  },
  deleteOrder: (payload: any) => {
    return fetch('Order', 'delete', payload);
  },

  deleteOrderCode: (payload: any) => {
    return fetch(`Order/code/${payload}`, 'delete');
  },

  getOrderById: (payload: any) => {
    return fetch(`Order/${payload}`, 'get');
  },

  getStaticQrCode: () => {
    return fetch('Order/staticQR', 'get');
  }
};

export { OrderAPIs };
