import React, { Component } from 'react';
import Slider from 'react-slick';
import SubMenu from '../../component/menuSell';
import { UI } from '../../../../components';
import { icons, images } from '../../../../assets';
import '../styles/pageOrder.scss';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { productActions, productSelectors } from '../../../../redux/product';
import { categoryActions, categorySelectors } from '../../../../redux/category';
import { AppState } from '../../../../redux/state';
import { StorageKey } from '../../../../commons/constants/storageKeys';
import { formatCurrency } from '../../../../commons/utils';
import _ from 'lodash';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  // login: (payload: any) => dispatch(authActions.login(payload))
  getProduct: () => dispatch(productActions.getProduct()),
  getProductId: (payload: any) => dispatch(productActions.getProductId(payload)),

  getCategory: () => dispatch(categoryActions.getCategory()),
  getCategoryId: (payload: any) => dispatch(categoryActions.getCategoryId(payload))
});

const mapStateToProps = (state: AppState) => ({
  // account: accountSelectors(state).account
  listProduct: productSelectors(state).listProduct,
  productById: productSelectors(state).productById,

  listCategory: categorySelectors(state).listCategory,
  categoryById: categorySelectors(state).categoryById,
  fetch: productSelectors(state).fetchingProduct
});

class CreateOrder extends Component {
  constructor(props) {
    super(props);
    let sumShoppingCarts = JSON.parse(localStorage.getItem(StorageKey.SHOPPING_CARTS));
    let sum = 0;
    if (!_.isEmpty(sumShoppingCarts)) {
      sumShoppingCarts.orderItems.forEach(element => {
        sum = sum + element.quantity;
      });
    }
    this.state = {
      keyword: '',
      shoppingSum: sum
    };
  }
  componentDidMount() {
    console.log('componentDidMount');

    this.props.getProduct();
    this.props.getCategory();
  }
  _onHandleChange(e) {
    var target = e.target;
    var name = target.name;
    var value = target.value;
    this.setState({
      [name]: value
    });
  }
  pushPage = () => {
    this.props.history.push('/detail-order');
  };

  Add = async (item: any) => {
    try {
      let shoppingCarts = {};
      let fullName = '';
      shoppingCarts = await JSON.parse(localStorage.getItem(StorageKey.SHOPPING_CARTS));
      fullName = await JSON.parse(localStorage.getItem(StorageKey.FULL_NAME));
      if (_.isEmpty(shoppingCarts)) {
        shoppingCarts = {
          promoCode: '',
          name: fullName,
          amount: item.price,
          orderItems: [
            {
              productId: item.id,
              quantity: 1,
              productName: item.productName,
              price: item.price,
              thumbnail: images.Budweiser
              // thumbnail:item.thumbnail
            }
          ]
        };
      } else {
        console.log('shoppingCarts else', shoppingCarts);
        shoppingCarts.amount = shoppingCarts.amount + item.price;
        let loop = false;
        if (!_.isEmpty(shoppingCarts.orderItems)) {
          shoppingCarts.orderItems.forEach(element => {
            if (element.productId === item.id) {
              element.quantity = element.quantity + 1;
              loop = true;
              return;
            }
          });
        }

        if (!loop) {
          shoppingCarts.orderItems.push({
            productId: item.id,
            quantity: 1,
            productName: item.productName,
            price: item.price,
            thumbnail: images.Tiger
            // thumbnail:item.thumbnail
          });
        }
      }

      localStorage.setItem(StorageKey.SHOPPING_CARTS, JSON.stringify(shoppingCarts));
      this.setState({ shoppingSum: this.state.shoppingSum + 1 });
    } catch (error) {
      console.log('add shopping cart', error);
    }
  };

  renderProduct = (data: any) => {
    return data.map((item, index) => {
      return (
        <div className="product" key={index}>
          <img src={images.Tiger} className="img-responsive img-product" alt={item.productName} />
          <span className="name-product">{item.productName}</span>
          <br />
          <span className="price-product">{formatCurrency(item.price)}</span>

          <button onClick={() => this.Add(item)} type="button" className="btn btn-default btn-add">
            <i className="fa fa-plus"></i>Thêm
          </button>
        </div>
      );
    });
  };

  changeDataProduct = (data: any) => {
    const _data = data;
    let productCategory = [];

    _data.forEach(element => {
      let push = true;
      productCategory.forEach(category => {
        if (category.category === element.categoryName) {
          push = false;
        }
      });
      if (push) {
        let products = _data.filter(item => {
          return item.categoryName === element.categoryName;
        });
        productCategory.push({
          category: element.categoryName,
          products: products
        });
      }
    });

    return productCategory;
  };

  renderListCategory = () => {
    const { listProduct } = this.props;
    // console.log('change', this.changeDataProduct(listProduct));
    const listProductsCategory = this.changeDataProduct(listProduct);
    return listProductsCategory.map((item, index) => {
      return (
        // <React.Fragment>
        <div className="list-product">
          <div className="caterory-name">
            <span className="name">{item.category}</span>
            <hr />
          </div>
          <div>{this.renderListProduct(item.products)}</div>
        </div>
        // </React.Fragment>
      );
    });
  };
  renderListProduct = (data: any) => {
    var settings = {
      speed: 500,
      infinite: false,
      slidesToShow: 6,
      slidesToScroll: 6,
      responsive: [
        {
          breakpoint: 1600,
          settings: {
            slidesToShow: 6,
            slidesToScroll: 6
          }
        },
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 4
          }
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    return <Slider {...settings}>{this.renderProduct(data)}</Slider>;
    // return <div>{this.renderProduct(data)}</div>;
  };
  render() {
    return (
      <div className="page-order">
        <SubMenu />
        <div className="page-content-order">
          <div className="header-order">
            <div className="header-order-left">
              <span>Tạo đơn hàng</span>
            </div>
            <div className="header-order-right">
              <UI.Formik
                initialValues={{ keyword: '', password: '' }}
                validationSchema={UI.yup.object().shape({
                  keyword: UI.yup.string().required('not null')
                })}
                onSubmit={this.onHandleSubmit}
                render={props => (
                  <form onSubmit={props.handleSubmit}>
                    <div className="box-search">
                      <UI.Field
                        {...props}
                        name="keyword"
                        type="text"
                        placeholder="Tìm kiếm"
                        value={props.values.keyword}
                        onChange={props.handleChange}
                        autoComplete="off"
                        leftImage={icons.icon_search}
                      />
                    </div>
                  </form>
                )}
              />
              <div className="btn-img-list-product">
                <img
                  src={icons.icon_chose_from}
                  className="img-responsive img-chose-list"
                  alt="List product"
                />
              </div>
              <div className="btn-cart">
                <img
                  src={icons.icon_banhang_white}
                  className="img-responsive img-cart"
                  alt="List product"
                />
                <button type="button" className="bt{n btn-default" onClick={this.pushPage}>
                  Giỏ hàng
                </button>
                <div className="box-number-amount">
                  <span className="number-amount">{this.state.shoppingSum}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <hr className="line-header" style={{ marginTop: '-3px' }} />
        {this.renderListCategory()}
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(CreateOrder);
