import React, { Component } from 'react';
import { icons } from '../../../../../../assets';
import { Link } from 'react-router-dom';
import './styles.scss';

const listPopup = [
  {
    id: 1,
    type: 'adlt',
    icon: icons.icon_adult,
    title_left: 'Người lớn',
    detail_title: 'Trên 12 tuổi',
    quantity: 1
  },
  {
    id: 2,
    type: 'child',
    icon: icons.icon_child,
    title_left: 'Trẻ em',
    detail_title: 'Từ 2 đến dưới 12 tuổi',
    quantity: 0
  },
  {
    id: 3,
    type: 'infant',
    icon: icons.icon_infant,
    title_left: 'Trẻ sơ sinh',
    detail_title: 'Dưới 2 tuổi',
    quantity: 0
  }
];

class AmountPerson extends Component {
  constructor(props) {
    super(props);
    this.state = {
      amountBeliever: 1,
      amountChildren: 0,
      amountBabies: 0,
      modalIsOpen: false,
      listSelect: []
    };
  }

  Sub = async (item: any) => {
    if (item.quantity === 0) {
      return;
    } else {
      item.quantity = item.quantity - 1;
    }
  };
  Add = async (item: any) => {
    item.quantity = item.quantity + 1;
  };

  addAmountPerson = () => {
    console.log('list', listPopup);
    this.setState({
      listSelect: listPopup
    });
    this.props.addAmountPerson(listPopup);
    this.props.closeModal();
  };
  renderContent() {
    let result = '';
    result = listPopup.map((item, index) => {
      return (
        <div className="item_pop_up_choose_person" key={index}>
          <div className="item_pop_up_choose_person_left">
            <img src={item.icon} className="img-responsive" alt={item.detail_title} />
            <div className="detail_title">
              <span>{item.title_left}</span>
              <br />
              <span>{item.detail_title}</span>
            </div>
          </div>
          <div className="item_pop_up_choose_person_right">
            <Link to="#" onClick={() => this.Sub(item)}>
              <i className="fa fa-minus"></i>
            </Link>

            <span style={{ fontSize: '1rem' }}>{item.quantity}</span>

            <Link to="#" onClick={() => this.Add(item)}>
              <i className="fa fa-plus"></i>
            </Link>
          </div>
        </div>
      );
    });
    return result;
  }
  render() {
    return (
      <div className="amount_person">
        <div className="viewClose">
          <div className="icon-close">
            <img src={icons.icon_close_popup} alt="" onClick={() => this.props.closeModal()} />
          </div>
        </div>
        <div className="title_pop_up">Hành khách</div>
        <hr className="line_pop_up" />
        <div className="content_pop_up_choose_person">{this.renderContent()}</div>

        <div style={{ textAlign: 'center' }}>
          <button
            type="button"
            className="btn btn-default btn_choose"
            onClick={this.addAmountPerson}
          >
            Đóng
          </button>
        </div>
      </div>
    );
  }
}
export default AmountPerson;
