const IOSSwitch = withStyles(theme => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1)
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: theme.palette.common.white,
      '& + $track': {
        backgroundColor: '#52d869',
        opacity: 1,
        border: 'none'
      }
    },
    '&$focusVisible $thumb': {
      color: '#52d869',
      border: '6px solid #fff'
    }
  },
  thumb: {
    width: 24,
    height: 24
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border'])
  },
  checked: {},
  focusVisible: {}
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked
      }}
      {...props}
    />
  );
});

class AddEmployee extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nameStore: '',
      checkedB: true,
      role: ''
    };
  }

  changeCategoryList = (list: any) => {
    let data = [];

    list.forEach(element => {
      data.push({
        value: element.id,
        label: element.storeName
      });
    });
    return data;
  };
  _onHandleChange = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    this.setState(
      {
        [name]: value
      },
      () => {
        console.log('name', this.state.role);
      }
    );
  };
  handleChangeCheck = name => event => {
    console.log('handleChange');
    this.setState({ checkedB: !this.state.checkedB }, () => {
      console.log('checkB', this.state.checkedB);
    });
  };
  renderDropDown() {
    const classes = useStyles();
    const data = this.changeCategoryList(dataCategory);
    return (
      <TextField
        id="standard-select-currency"
        name="nameStore"
        select
        label=""
        className={classes.textField}
        value={this.state.nameStore}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {data.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  renderDropDownRole() {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="role"
        select
        label=""
        className={classes.textField}
        value={this.state.role}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listRole.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  onHandleSubmit = values => {
    // const { nameStore, catalogCode, descriptionCaterory } = values;
    // console.log('onHandleSubmit', values, this.state.nameStore, this.state.role);
    let role = '';
    switch (this.state.role) {
      case 1:
        role = 'manager';
        break;
      default:
        role = 'staff';
        break;
    }

    if (this.state && this.state.nameStore !== '') {
      this.setState({
        showErrorCombobox: '0'
      });
      console.log('onHandleSubmit', values, this.state.nameStore, role);
    } else {
      this.setState({
        showErrorCombobox: 'Vui lòng chọn danh mục'
      });
    }

    if (this.state && this.state.role !== '') {
      this.setState({
        showErrorComboboxRole: '0'
      });
    } else {
      this.setState({
        showErrorComboboxRole: 'Vui lòng chọn role'
      });
    }
  };
  render() {
    return (
      <div className="add-employee">
        <SubMenu />
        <div className="header-add-employee">
          <span>Tạo nhân viên mới</span>
        </div>
        <hr className="line-header" />
        <UI.Formik
          initialValues={{ nameemployee: '', password: '', phoneNumber: '', email: '' }}
          validationSchema={UI.yup.object().shape({
            nameemployee: UI.yup.string().required('not null'),
            password: UI.yup.string().required('not null'),
            phoneNumber: UI.yup.string().required('not null'),
            email: UI.yup.string().required('not null')
          })}
          onSubmit={this.onHandleSubmit}
          render={props => (
            <form onSubmit={props.handleSubmit}>
              <div className="box-add-product">
                <div className="box-left">
                  <div className="box-input">
                    <div className="title-product-code">Tên nhân viên</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="nameemployee"
                        type="text"
                        placeholder="Tên nhân viên"
                        value={props.values.nameemployee}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Mật khẩu</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="password"
                        type="password"
                        placeholder="Mật khẩu"
                        value={props.values.password}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Số điện thoại</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="phoneNumber"
                        type="text"
                        placeholder="Số điện thoại"
                        value={props.values.phoneNumber}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Email:</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="email"
                        type="email"
                        placeholder="Email"
                        value={props.values.email}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                </div>
                <div className="box-right">
                  <div className="box-input">
                    <div className="title-product-code">Địa điểm bán hàng:</div>
                    <div className="card-cover">{this.renderDropDown()}</div>
                    <div className="err-caterory">
                      {(this.state && this.state.showErrorCombobox) || ''}
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Phân quyền:</div>
                    <div className="card-cover">{this.renderDropDownRole()}</div>
                    <div className="err-caterory">
                      {(this.state && this.state.showErrorComboboxRole) || ''}
                    </div>
                  </div>
                  <div className="condition">
                    <span className="title">Tình trạng hoạt động</span>
                    <FormControlLabel
                      control={
                        <IOSSwitch
                          checked={this.state.checkedB}
                          onChange={this.handleChangeCheck('checkedB')}
                          value="checkedB"
                        />
                      }
                    />
                  </div>
                </div>
              </div>
              <div className="group-btn-pop-up">
                <UI.Button type="submit" name="Lưu" className="btn-pop-up" />
                <UI.Button type="button" name="Huỷ" className="btn-pop-up" />
              </div>
            </form>
          )}
        />
      </div>
    );
  }
}
export default AddEmployee;
