import Immutable from 'seamless-immutable';
import { AppState } from '../state';

import { types } from './actions';

const initialState = Immutable({
  fetchingAccount: 0,
  fetchingAccountMe: 0,
  account: {},
  listAccount: [],
  accountById: {},
  fetchingAccountById: 0,
  actionFetching: 0,
  createSuccess: {},
  editSuccess: {}
});

export const reducer = (
  state = initialState,
  { type, payload }: { type: string, payload?: any }
) => {
  switch (type) {
    case types.ACCOUNT_ME:
      return state.set('fetchingAccountMe', 1);
    case types.ACCOUNT_ME_SUCCESS:
      return state.merge({ fetchingAccountMe: 0, account: payload });
    case types.ACCOUNT_ME_FAILURE:
      return state.set('fetchingAccountMe', -1);
    case types.ACCOUNT_ME_CANCEL:
      return state.set('fetchingAccountMe', 0);

    case types.GET_ACCOUNT:
      return state.set('fetchingAccount', 1);
    case types.GET_ACCOUNT_SUCCESS:
      return state.merge({ fetchingAccount: 0, listAccount: payload });
    case types.GET_ACCOUNT_FAILURE:
      return state.set('fetchingAccount', -1);
    case types.GET_ACCOUNT_CANCEL:
      return state.set('fetchingAccount', 0);

    case types.GET_ACCOUNT_ID:
      return state.set('fetchingAccountById', 1);
    case types.GET_ACCOUNT_ID_SUCCESS:
      return state.merge({ fetchingAccount: 0, accountById: payload });
    case types.GET_ACCOUNT_ID_FAILURE:
      return state.set('fetchingAccountById', -1);
    case types.GET_ACCOUNT_ID_CANCEL:
      return state.set('fetchingAccountById', 0);

    case types.CREATE_ACCOUNT:
      return state.set('actionFetching', 1);
    case types.CREATE_ACCOUNT_SUCCESS:
      return state.merge({ actionFetching: 0, createSuccess: payload });
    case types.CREATE_ACCOUNT_FAILURE:
      return state.set('actionFetching', -1);
    case types.CREATE_ACCOUNT_CANCEL:
      return state.set('actionFetching', 0);

    case types.UPDATE_ACCOUNT:
      return state.set('actionFetching', 1);
    case types.UPDATE_ACCOUNT_SUCCESS:
      return state.merge({ actionFetching: 0, editSuccess: payload });
    case types.UPDATE_ACCOUNT_FAILURE:
      return state.set('actionFetching', -1);
    case types.UPDATE_ACCOUNT_CANCEL:
      return state.set('actionFetching', 0);

    case types.PRELOADING_AFTER_LOGIN:
      return state.set('fetchingPreloading', 1);
    case types.PRELOADING_AFTER_LOGIN_SUCCESS:
      return state.merge({
        fetchingPreloading: 0,
        account: payload[0] ? payload[0] : {},
        listAccount: payload[1] ? payload[1] : []
      });
    case types.PRELOADING_AFTER_LOGIN_FAILURE:
      return state.set('fetchingPreloading', -1);

    default:
      return state;
  }
};

export const selectors = (state: AppState) => ({
  fetchingAccountMe: state.account.getIn(['fetchingAccountMe']),
  fetchingAccount: state.account.getIn(['fetchingAccount']),
  account: state.account.getIn(['account']),
  listAccount: state.account.getIn(['listAccount']),
  fetchingAccountById: state.account.getIn(['fetchingAccountById']),
  accountById: state.account.getIn(['accountById']),
  actionFetching: state.account.getIn(['actionFetching']),
  editSuccess: state.account.getIn(['editSuccess']),
  createSuccess: state.account.getIn(['createSuccess']),
  deleteSuccess: state.account.getIn(['deleteSuccess'])
});
