import Immutable from 'seamless-immutable';
import { AppState } from '../state';

import { types } from './actions';

const initialState = Immutable({
  fetchingLogin: 0,
  fetchingLogout: 0
});

export const reducer = (state = initialState, { type, payload }: { type: string, payload?: any }) => {
  switch (type) {
    case types.LOGIN:
      return state.set('fetchingLogin', 1);
    case types.LOGIN_SUCCESS:
      return state.merge({ fetchingLogin: 0 });
    case types.LOGIN_FAILURE:
      return state.set('fetchingLogin', -1);
    case types.LOGIN_CANCEL:
      return state.set('fetchingLogin', 0);

    case types.LOGOUT:
      return state.set('fetchingLogout', 1);
    case types.LOGOUT_SUCCESS:
    case types.LOGOUT_FAILURE:
      return state.set('fetchingLogout', -1);
    case types.LOGOUT_CANCEL:
      return state.set('fetchingLogout', 0);

    case 'RESET':
      return initialState;

    default:
      return state;
  }
};

export const selectors = (state: AppState) => ({
  fetchingLogin: state.auth.getIn(['fetchingLogin']),
  fetchingLogout: state.auth.getIn(['fetchingLogout'])
});
