import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import './styles/signIn.scss';
import { UI } from '../../../components';
import { icons } from '../../../assets';
import { authActions } from '../../../redux/auth';
import { Dispatch } from 'redux';

class FormLogin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visiblePass: false,
      loading: false,
      messageError: ''
    };
  }

  componentDidMount() {
    this.setState({
      errorMessage: this.props.errorMessage
    });
  }

  showPassword() {
    this.setState({ visiblePass: !this.state.visiblePass });
  }

  onHandleSubmit = values => {
    const { username, password } = values;
    // this.props.dispatchSignIn(`username=${username}&password=${password}&grant_type=password`);
    const payload = `username=${username}&password=${password}&grant_type=password`;

    this.props.login(payload);
  };
  render() {
    return (
      <div className="form-login">
        <UI.Formik
          initialValues={{ username: '', password: '' }}
          validationSchema={UI.yup.object().shape({
            username: UI.yup.string().required('not null'),
            password: UI.yup.string().required('not null')
          })}
          onSubmit={this.onHandleSubmit}
          render={props => (
            <form onSubmit={props.handleSubmit}>
              <ul className="box-login">
                <li>
                  <UI.Field
                    {...props}
                    name="username"
                    type="text"
                    placeholder="Username"
                    value={props.values.username}
                    onChange={props.handleChange}
                    autoComplete="off"
                    leftImage={icons.icon_phone}
                  />
                </li>
                <li>
                  <UI.Field
                    {...props}
                    name="password"
                    type="password"
                    placeholder="Password"
                    value={props.values.password}
                    onChange={props.handleChange}
                    autoComplete="off"
                    leftImage={icons.icon_pass}
                  />
                </li>
              </ul>

              <div className="yd-rememberme-validation-showpass" style={{ paddingTop: 0 }}>
                <Link to="#">Quên mật khẩu</Link>
              </div>

              <UI.Button
                type="submit"
                name="Đăng nhập"
                className="btn-login"
                // loading={loading}
                // error={signInFalse.success}
                // messageError={messageError ? lang('signIn.' + messageError) : null}
                // disabled={loading}
              />
            </form>
          )}
        />
      </div>
    );
  }
}
const mapDispatchToProps = (dispatch: Dispatch) => ({
  login: (payload: any) => dispatch(authActions.login(payload))
});

const mapStateToProps = state => ({
  // signInFalse: state.auth.signInFalse
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FormLogin);
