import { of, from } from 'rxjs';
import { ofType, ActionsObservable } from 'redux-observable';
import { takeUntil, catchError, switchMap } from 'rxjs/operators';
import { combineEpics } from 'redux-observable';
// import { Action } from 'c-redux';
import { types, actions } from './actions';
import { CategoryAPIs } from '../../services';

interface Action {
  type: string;
  payload?: any;
}

const getCategory = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_CATEGORY),
    switchMap(action => {
      return from(CategoryAPIs.getCategory()).pipe(
        switchMap((data: any) => {
          return of(actions.getCategorySuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_CATEGORY_CANCEL))),
        catchError(error => {
          return of(actions.getCategoryFailure(error.message));
        })
      );
    })
  );
};

const getCategoryById = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_CATEGORY_ID),
    switchMap(action => {
      const payload = action.payload;

      return from(CategoryAPIs.getCategoryById(payload)).pipe(
        switchMap((data: any) => {
          console.log('getCategoryById', data);

          return of(actions.getCategoryIdSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_CATEGORY_ID_CANCEL))),
        catchError(error => {
          return of(actions.getCategoryIdFailure(error.message));
        })
      );
    })
  );
};

const createCategory = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.CREATE_CATEGORY),
    switchMap(action => {
      const payload = action.payload;

      const { data, onSuccess } = payload;

      return from(CategoryAPIs.createCategory(data)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }
          return of(actions.createCategorySuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.CREATE_CATEGORY_CANCEL))),
        catchError(error => {
          return of(actions.createCategoryFailure(error.message));
        })
      );
    })
  );
};

const updateCategory = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.UPDATE_CATEGORY),
    switchMap(action => {
      const payload = action.payload;
      const { data, onSuccess } = payload;
      return from(CategoryAPIs.editCategory(data)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }

          return of(actions.updateCategorySuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.UPDATE_CATEGORY_CANCEL))),
        catchError(error => {
          return of(actions.updateCategoryFailure(error.message));
        })
      );
    })
  );
};

const deleteCategory = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.DELETE_CATEGORY),
    switchMap(action => {
      const payload = action.payload;
      const { data, onSuccess } = payload;
      return from(CategoryAPIs.deleteCategory(data)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }
          return of(actions.deleteCategorySuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.DELETE_CATEGORY_CANCEL))),
        catchError(error => {
          return of(actions.deleteCategoryFailure(error.message));
        })
      );
    })
  );
};

export default combineEpics(
  getCategory,
  getCategoryById,
  createCategory,
  updateCategory,
  deleteCategory
);
