import { fetch } from '.';
const productAPIs = {
  getProduct: () => {
    return fetch('Product', 'get');
  },
  createProduct: (payload: any) => {
    return fetch('Product', 'post', payload);
  },
  editProduct: (payload: any) => {
    return fetch('Product', 'put', payload);
  },
  deleteProduct: (payload: any) => {
    return fetch(`Product?id=${payload}`, 'delete', payload);
  },

  getProductById: (payload: any) => {
    return fetch(`Product/${payload}`, 'get');
  }
};

export { productAPIs };
