function formatPhoneNumber(phoneNumber) {
  const prefix = phoneNumber.slice(0, 3);
  const suffix = phoneNumber.slice(-3);
  const middleLength = phoneNumber.length - 6;
  const middleStr = new Array(middleLength).fill('x').join('');
  const str = prefix + middleStr + suffix;
  return str;
}

export { formatCurrency, formatPhoneNumber, formatDateTime, formatDateTimeHours };
