import { status } from '../helper';

export const types = {
  GET_PRODUCT: `${status.start}GET_PRODUCT`,
  GET_PRODUCT_SUCCESS: `${status.success}GET_PRODUCT`,
  GET_PRODUCT_FAILURE: `${status.failure}GET_PRODUCT`,
  GET_PRODUCT_CANCEL: `${status.cancel}GET_PRODUCT`,

  CREATE_PRODUCT: `${status.start}CREATE_PRODUCT`,
  CREATE_PRODUCT_SUCCESS: `${status.success}CREATE_PRODUCT`,
  CREATE_PRODUCT_FAILURE: `${status.failure}CREATE_PRODUCT`,
  CREATE_PRODUCT_CANCEL: `${status.cancel}CREATE_PRODUCT`,

  UPDATE_PRODUCT: `${status.start}UPDATE_PRODUCT`,
  UPDATE_PRODUCT_SUCCESS: `${status.success}UPDATE_PRODUCT`,
  UPDATE_PRODUCT_FAILURE: `${status.failure}UPDATE_PRODUCT`,
  UPDATE_PRODUCT_CANCEL: `${status.cancel}UPDATE_PRODUCT`,

  DELETE_PRODUCT: `${status.start}DELETE_PRODUCT`,
  DELETE_PRODUCT_SUCCESS: `${status.success}DELETE_PRODUCT`,
  DELETE_PRODUCT_FAILURE: `${status.failure}DELETE_PRODUCT`,
  DELETE_PRODUCT_CANCEL: `${status.cancel}DELETE_PRODUCT`,

  GET_PRODUCT_ID: `${status.start}GET_PRODUCT_ID`,
  GET_PRODUCT_ID_SUCCESS: `${status.success}GET_PRODUCT_ID`,
  GET_PRODUCT_ID_FAILURE: `${status.failure}GET_PRODUCT_ID`,
  GET_PRODUCT_ID_CANCEL: `${status.cancel}GET_PRODUCT_ID`
};

export const actions = {
  getProduct: (payload?: any) => ({ type: types.GET_PRODUCT, payload }),
  getProductSuccess: (payload?: any) => ({ type: types.GET_PRODUCT_SUCCESS, payload }),
  getProductFailure: (payload?: any) => ({ type: types.GET_PRODUCT_FAILURE, payload }),
  getProductCancel: () => ({ type: types.GET_PRODUCT_CANCEL }),

  createProduct: (payload?: any) => ({ type: types.CREATE_PRODUCT, payload }),
  createProductSuccess: (payload?: any) => ({ type: types.CREATE_PRODUCT_SUCCESS, payload }),
  createProductFailure: (payload?: any) => ({ type: types.CREATE_PRODUCT_FAILURE, payload }),
  createProductCancel: () => ({ type: types.CREATE_PRODUCT_CANCEL }),

  updateProduct: (payload?: any) => ({ type: types.UPDATE_PRODUCT, payload }),
  updateProductSuccess: (payload?: any) => ({ type: types.UPDATE_PRODUCT_SUCCESS, payload }),
  updateProductFailure: (payload?: any) => ({ type: types.UPDATE_PRODUCT_FAILURE, payload }),
  updateProductCancel: () => ({ type: types.UPDATE_PRODUCT_CANCEL }),

  deleteProduct: (payload?: any) => ({ type: types.DELETE_PRODUCT, payload }),
  deleteProductSuccess: (payload?: any) => ({ type: types.DELETE_PRODUCT_SUCCESS, payload }),
  deleteProductFailure: (payload?: any) => ({ type: types.DELETE_PRODUCT_FAILURE, payload }),
  deleteProductCancel: () => ({ type: types.DELETE_PRODUCT_CANCEL }),

  getProductId: (payload?: any) => ({ type: types.GET_PRODUCT_ID, payload }),
  getProductIdSuccess: (payload?: any) => ({ type: types.GET_PRODUCT_ID_SUCCESS, payload }),
  getProductIdFailure: (payload?: any) => ({ type: types.GET_PRODUCT_ID_FAILURE, payload }),
  getProductIdCancel: () => ({ type: types.GET_PRODUCT_ID_CANCEL })
};
