import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './styles.scss';
// import { icons } from '../../assets';

class index extends Component {
  constructor(props) {
    super(props);
    this.state = { visibleSelect: false, itemSelect: {} };
  }

  formatOptionLabel = (item, index) => {
    return (
      <div onClick={() => this.onSelect(item)} className="item-select">
        <div className="item-select-label">{item.label}</div>
        <div className="item-select-name">{item.value}</div>
      </div>
    );
  };

  onSelect = item => {
    console.log('onSelect', item);
    this.props.onChange(this.props.name, item);
    this.setState({
      visibleSelect: false,
      itemSelect: item
    });
    this.props.handleChange(item);
  };

  onChange = param => {
    this.setState({ visibleSelect: false, itemSelect: param.target.item });

    this.props.onChange(this.props.name, param.target.value);
    console.log('onChange', this.props.name, param.target.value);
  };

  hide = () => this.setState({ visibleSelect: !this.state.visibleSelect });

  render() {
    const { data, touched, errors, name, value } = this.props;
    const { visibleSelect, itemSelect } = this.state;

    return (
      <div onClick={() => this.hide()} className="select-option-container">
        <input
          name={name}
          className="select-option"
          type="text"
          value={value.value}
          item={itemSelect}
          placeholder="Chọn tài khoản"
          onChange={this.onChange}
          autoComplete="off"
          onBlur={this.hide}
        />

        <img
          className="img-select"
          // src={icons.iconDownBlack}
          alt={'icon-select'}
          onClick={() => this.setState({ visibleSelect: !visibleSelect })}
        />
        <div className={this.state.visibleSelect ? 'show-select-item' : 'item-select-visible'}>
          {data.map((item, index) => this.formatOptionLabel(item, index))}
        </div>

        {touched && errors && touched[name] && errors[name] && (
          <div className="errorInput">{errors[name]}</div>
        )}
      </div>
    );
  }
}
index.defaultProps = {
  data: [
    { value: 343333333333, label: 'Nguyễn văn A' },
    { value: 343333333332, label: 'Nguyễn văn B' },
    { value: 343333333331, label: 'Nguyễn văn C' },
    { value: 343333333337, label: 'Nguyễn văn E' },
    { value: 343333333338, label: 'Nguyễn văn A' },
    { value: 343333333339, label: 'Nguyễn văn A' }
  ]
};

index.propTypes = {
  data: PropTypes.array,
  onChange: PropTypes.func
};

export default index;
