import React, { Component } from 'react';
import { history } from '../../redux/router';
import SignIn from '../auth/signIn';
import './styles.scss';

import { StorageKey } from '../../commons/constants/storageKeys';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  renderLogin = () => {
    let token = JSON.parse(localStorage.getItem(StorageKey.USER_TOKEN));
    if (token && token.token) {
      return history.push(`/overview`);
    } else {
      return <SignIn />;
    }
  };

  // componentDidMount() {
  //   let token = JSON.parse(localStorage.getItem(StorageKey.USER_TOKEN));
  //   if (token && token.token) {
  //     this.props.getAccount();
  //   }
  // }

  render() {
    console.log('renderHome');

    return <div className="row container home">{this.renderLogin()}</div>;
  }
}

export default Home;
