import React, { Component } from 'react';
import Header from './header';
import HomePage from '../features/home';
import SignIn from '../features/auth/signIn';
// import Sell from '../features/sell';
import Overview from '../features/overview';
import Report from '../features/report';
import { Route } from 'react-router-dom';
import { connect } from 'react-redux';
import { PrivateRoute } from '../components/PrivateRoute';
import { ToastsStore } from 'react-toasts';
import RightApp from './rightApp/rightApp';
import CreateOrder from '../features/sell/views/createOrder/createOrder';
import QRCode from '../features/sell/views/qrCode';
import WaitingPay from '../features/sell/views/waitingPay/waitingPay';
import HistoryOrder from '../features/sell/views/historyOrder';
import CategoryManagement from '../features/sell/views/cateroryManagement';
import ProductManagement from '../features/sell/views/productManagement';
import AddProduct from '../features/sell/views/productManagement/addProduct';
import EmployeeManager from '../features/sell/views/employeeManagement';
import AddEmployee from '../features/sell/views/employeeManagement/addEmployee';
import StoreManager from '../features/sell/views/storeManager';
import AddStore from '../features/sell/views/storeManager/addStore';
import DetailOrder from '../features/sell/views/createOrder/detailOrder';
import { StorageKey } from '../commons/constants/storageKeys';
import CreateOrderSuccess from '../features/sell/views/createOrder/createOrderSuccess';
import CreateOrderFalse from '../features/sell/views/createOrder/createOrderFalse';
import LoadingModal from '../components/Loadding';

import ChangePassword from '../features/settings/changePassword';
import ChangePinCode from '../features/settings/changePinCode';
import UpdateProfile from '../features/settings/updateProfile';

// Bill
import BillElectric from '../features/collection/views/billElectric';
import BillWater from '../features/collection/views/billWater';
import BillTelevision from '../features/collection/views/billTelevision';
import InfoBillElectric from '../features/collection/views/billElectric/infoBill';
import InfoBillWater from '../features/collection/views/billWater/infoWater';
import InfoBillTelevision from '../features/collection/views/billTelevision/infoTelevision';

import Tuition from '../features/collection/views/tuition';
import ListSearchTution from '../features/collection/views/tuition/listSearch';
import InfoTuition from '../features/collection/views/tuition/infoTuition';

import AirTicket from '../features/collection/views/airTicket';
import FormBooking from '../features/collection/views/airTicket/bookingTicket/bookFlight';
import FlightInformation from '../features/collection/views/airTicket/bookingTicket/flightInformation';
import PeopleInfomation from '../features/collection/views/airTicket/bookingTicket/peopleInfomation';
import ReservationInformation from '../features/collection/views/airTicket/bookingTicket/reservationInformation';

import BookingHistory from '../features/collection/views/airTicket/bookingHistory';
import DetailMyBooking from '../features/collection/views/airTicket/bookingHistory/detail_my_booking';
import MyCustomer from '../features/collection/views/airTicket/myCustomer';
import TicketPurchaseHistory from '../features/collection/views/airTicket/myCustomer/ticketPurchaseHistory';

// import { ToastsContainer, ToastsStore, ToastsContainerPosition } from 'react-toasts';
import './App.scss';

import { productSelectors } from '../redux/product';
import { authSelectors } from '../redux/auth';
import { orderSelectors } from '../redux/order';
import { categorySelectors } from '../redux/category';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      display: false
    };
    this.resetTimeout = this.resetTimeout.bind(this);

    this.setTimeout();
  }

  clearTimeout() {
    if (this.logoutTimeout) clearTimeout(this.logoutTimeout);
  }

  setTimeout() {
    let token = JSON.parse(localStorage.getItem(StorageKey.USER_TOKEN));
    if (token && token.expires_in) {
      let expires_in = token.expires_in;
      this.logoutTimeout = setTimeout(this.logout, expires_in);
    }
  }

  resetTimeout() {
    this.clearTimeout();
    this.setTimeout();
  }

  logout() {
    ToastsStore.error('sdsadas', 10000);
    localStorage.clear();
    window.location.reload();
  }

  getFetchingStatus = () => {
    return (
      this.props.fetchingProduct === 1 ||
      this.props.fetchingLogin === 1 ||
      this.props.fetchOrder === 1 ||
      this.props.fetchCategory === 1
    );
  };

  render() {
    const { display } = this.state;
    console.log('app', this.getFetchingStatus());

    return (
      <div className="App">
        <Header isAuth={this.props.isAuth} />
        <div id="main">
          <Route path="/signin" component={SignIn} />
          <div className="row" style={{ marginLeft: '0px', marginRight: '0px' }}>
            <div className="col-xs-9 col-sm-9 col-md-9 col-lg-9 left-app">
              <Route exact path="/">
                <HomePage display={display} />
              </Route>
              <PrivateRoute path="/change-password" component={ChangePassword}></PrivateRoute>
              <PrivateRoute path="/change-pin-code" component={ChangePinCode}></PrivateRoute>
              <PrivateRoute path="/update-profile" component={UpdateProfile}></PrivateRoute>

              <PrivateRoute path="/overview" component={Overview}></PrivateRoute>

              <PrivateRoute path="/tao-don-hang" component={CreateOrder}></PrivateRoute>
              <PrivateRoute path="/detail-order" component={DetailOrder}></PrivateRoute>
              <PrivateRoute path="/report" component={Report}></PrivateRoute>
              <PrivateRoute path="/ma-qr" component={QRCode}></PrivateRoute>
              <PrivateRoute path="/waiting-pay" component={WaitingPay}></PrivateRoute>
              <PrivateRoute path="/history-order" component={HistoryOrder}></PrivateRoute>
              <PrivateRoute path="/order-false" component={CreateOrderFalse}></PrivateRoute>

              <PrivateRoute path="/order-success" component={CreateOrderSuccess}></PrivateRoute>
              <PrivateRoute
                path="/category-management"
                component={CategoryManagement}
              ></PrivateRoute>
              <PrivateRoute path="/product-management" component={ProductManagement}></PrivateRoute>
              <PrivateRoute path="/add-employee" component={AddEmployee}></PrivateRoute>

              <PrivateRoute path="/add-product" component={AddProduct}></PrivateRoute>
              <PrivateRoute path="/employee-management" component={EmployeeManager}></PrivateRoute>
              <PrivateRoute path="/store-manager" component={StoreManager}></PrivateRoute>
              <PrivateRoute path="/add-store" component={AddStore}></PrivateRoute>
              {/* Bill */}
              <PrivateRoute path="/bill-electric" component={BillElectric}></PrivateRoute>
              <PrivateRoute path="/bill-water" component={BillWater}></PrivateRoute>
              <PrivateRoute path="/bill-television" component={BillTelevision}></PrivateRoute>

              <PrivateRoute path="/info-bill-electric" component={InfoBillElectric}></PrivateRoute>
              <PrivateRoute path="/info-bill-water" component={InfoBillWater}></PrivateRoute>
              <PrivateRoute
                path="/info-bill-television"
                component={InfoBillTelevision}
              ></PrivateRoute>
              <PrivateRoute path="/tuition" component={Tuition}></PrivateRoute>
              <PrivateRoute path="/search-tution" component={ListSearchTution}></PrivateRoute>
              <PrivateRoute path="/info-tuition" component={InfoTuition}></PrivateRoute>

              <PrivateRoute path="/air-ticket" component={AirTicket}></PrivateRoute>
              <PrivateRoute path="/form-booking" component={FormBooking}></PrivateRoute>
              <PrivateRoute path="/flight-information" component={FlightInformation}></PrivateRoute>
              <PrivateRoute path="/people-infomation" component={PeopleInfomation}></PrivateRoute>

              <PrivateRoute path="/booking-history" component={BookingHistory}></PrivateRoute>
              <PrivateRoute path="/detail-my-booking" component={DetailMyBooking}></PrivateRoute>
              <PrivateRoute
                path="/reservation-information"
                component={ReservationInformation}
              ></PrivateRoute>
              <PrivateRoute path="/my-customer" component={MyCustomer}></PrivateRoute>
              <PrivateRoute
                path="/ticket-purchase-history"
                component={TicketPurchaseHistory}
              ></PrivateRoute>
            </div>

            <div className="col-xs-3 col-sm-3 col-md-3 col-lg-3">
              <RightApp />
            </div>
          </div>
        </div>
        <LoadingModal isShow={this.getFetchingStatus()} />
        {/* {LoadingModal(this.props.fetch)} */}
      </div>
    );
  }
}
const mapDispatchToProps = {
  // dispatchInitialApp: () => initialApp(),
  // dispatchSignOut: () => signOut()
};

const mapStateToProps = state => ({
  fetchingProduct: productSelectors(state).fetchingProduct,
  fetchingLogin: authSelectors(state).fetchingLogin,
  fetchOrder: orderSelectors(state).actionFetching,
  fetchCategory: categorySelectors(state).actionFetching
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
