export const actions = {
  getOrder: (payload?: any) => ({ type: types.GET_ORDER, payload }),
  getOrderSuccess: (payload?: any) => ({ type: types.GET_ORDER_SUCCESS, payload }),
  getOrderFailure: (payload?: any) => ({ type: types.GET_ORDER_FAILURE, payload }),
  getOrderCancel: () => ({ type: types.GET_ORDER_CANCEL }),

  createOrder: (payload?: any) => ({ type: types.CREATE_ORDER, payload }),
  createOrderSuccess: (payload?: any) => ({ type: types.CREATE_ORDER_SUCCESS, payload }),
  createOrderFailure: (payload?: any) => ({ type: types.CREATE_ORDER_FAILURE, payload }),
  createOrderCancel: () => ({ type: types.CREATE_ORDER_CANCEL }),

  updateOrder: (payload?: any) => ({ type: types.UPDATE_ORDER, payload }),
  updateOrderSuccess: (payload?: any) => ({ type: types.UPDATE_ORDER_SUCCESS, payload }),
  updateOrderFailure: (payload?: any) => ({ type: types.UPDATE_ORDER_FAILURE, payload }),
  updateOrderCancel: () => ({ type: types.UPDATE_ORDER_CANCEL }),

  deleteOrderCode: (payload?: any) => ({ type: types.DELETE_ORDER_CODE, payload }),
  deleteOrderCodeSuccess: (payload?: any) => ({ type: types.DELETE_ORDER_CODE_SUCCESS, payload }),
  deleteOrderCodeFailure: (payload?: any) => ({ type: types.DELETE_ORDER_CODE_FAILURE, payload }),
  deleteOrderCodeCancel: () => ({ type: types.DELETE_ORDER_CODE_CANCEL }),

  getOrderId: (payload?: any) => ({ type: types.GET_ORDER_ID, payload }),
  getOrderIdSuccess: (payload?: any) => ({ type: types.GET_ORDER_ID_SUCCESS, payload }),
  getOrderIdFailure: (payload?: any) => ({ type: types.GET_ORDER_ID_FAILURE, payload }),
  getOrderIdCancel: () => ({ type: types.GET_ORDER_ID_CANCEL }),

  getStaticQRCode: (payload?: any) => ({ type: types.GET_STATIC_QRCODE, payload }),
  getStaticQRCodeSuccess: (payload?: any) => ({ type: types.GET_STATIC_QRCODE_SUCCESS, payload }),
  getStaticQRCodeFailure: (payload?: any) => ({ type: types.GET_STATIC_QRCODE_FAILURE, payload }),
  getStaticQRCodeCancel: () => ({ type: types.GET_STATIC_QRCODE_CANCEL })
};
