import React, { Component } from 'react';
import MenuCollection from '../../../../component/menuCollection';
import { animations } from 'react-animation';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import moment from 'moment';
import { UI } from '../../../../../../components';
import { formatCurrency } from '../../../../../../commons/utils';
import { icons, images } from '../../../../../../assets';
import BoxInfoRight from '../box_info_right';
import './styles.scss';
import { history } from '../../../../../../redux/router';
// import _ from 'lodash';

const style = {
  animation: animations.fadeInUp
};

const flightList = [
  {
    id: 1,
    airlineName: 'Vietjet Air',
    flightCode: 'VJ-308',
    icon: icons.icon_vietjetair,
    departureTime: '7:30',
    landingTime: '9:45',
    ticketPrice: 1995000,
    ticketType: 'Phổ thông',
    tax: 760000,
    fee: 110000
  },
  {
    id: 2,
    airlineName: 'Vietnam Airlines',
    flightCode: 'VA-303',
    icon: icons.icon_vietnamairlines,
    departureTime: '7:30',
    landingTime: '9:45',
    ticketPrice: 3499000,
    ticketType: 'Phổ thông',
    tax: 760000,
    fee: 110000
  },
  {
    id: 3,
    airlineName: 'BamBoo',
    flightCode: 'BB-304',
    icon: icons.icon_bamboo,
    departureTime: '7:30',
    landingTime: '9:45',
    ticketPrice: 1799000,
    ticketType: 'Phổ thông',
    tax: 760000,
    fee: 110000
  },
  {
    id: 4,
    airlineName: 'Jet Star',
    flightCode: 'JS-310',
    icon: icons.icon_jetstar,
    departureTime: '7:30',
    landingTime: '9:45',
    ticketPrice: 2019000,
    ticketType: 'Phổ thông',
    tax: 760000,
    fee: 110000
  }
];

class FlightInformation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectItems: '',
      value: flightList[0].id,
      itemFlight: flightList[0],
      itemFlightDestination: {},
      display: true,
      priceLuggageFirst: 0,
      priceLuggageLast: 0,
      priceInsurrance: 0
    };
  }
  showInfoFlight = () => {
    this.setState({
      display: false,
      itemFlightDestination: flightList[0]
    });
    console.log('showInfoFlight');
  };

  toggleClass = (id, item) => {
    console.log('toggleClass', item, id);
    if (this.state.display) {
      this.setState(
        {
          value: id,
          itemFlight: item
        },
        () => {
          console.log('set stat', this.state.value);
        }
      );
    } else {
      this.setState(
        {
          value: id,
          itemFlightDestination: item
        },
        () => {
          console.log('set stat', this.state.value);
        }
      );
    }
  };
  renderFlightList() {
    // let { selectItems } = this.state;
    let result = '';
    console.log('value', this.state.value);
    let codeAirportdeparture = this.props.location.state.departure[0].codeAirport;
    let codeDestination = this.props.location.state.destination[0].codeAirport;
    result = flightList.map((item, index) => {
      return (
        <div
          className={this.state.value === item.id ? 'item_fligh active' : 'item_fligh'}
          key={index}
          onClick={() => this.toggleClass(item.id, item)}
        >
          <div className="content_item_fligh">
            <img src={item.icon} className="img-responsive logo_airline" alt={item.airlineName} />
            <div className="flight_time">
              <div className="content_flight_time">
                <div className="flight_time_item">
                  {item.departureTime}
                  <br />
                  <span style={{ color: '#999', fontSize: '1rem' }}>{codeAirportdeparture}</span>
                </div>
                <img src={images.line_flight} className="img-responsive" alt={item.airlineName} />
                <div className="flight_time_item last">
                  {item.landingTime}
                  <br />
                  <span style={{ color: '#999', fontSize: '1rem' }}>{codeDestination}</span>
                </div>
                <span className="flying_time">
                  1h 15m <br /> bay thẳng
                </span>
              </div>
            </div>
            <div className="item_price">
              <p>{formatCurrency(item.ticketPrice)}</p>
              <span className="ticket_type">{item.ticketType}</span>
            </div>
            <FormControlLabel value={item.id} control={<Radio />} />
          </div>
        </div>
      );
    });
    return result;
  }

  pushPage = () => {
    let data = {
      itemFligh: this.state.itemFlight,
      itemFlightDestination: this.state.itemFlightDestination,
      startDate: this.props.location.state.startDate,
      endDate: this.props.location.state.endDate,
      amountBeliever: this.props.location.state.amountBeliever,
      amountChildren: this.props.location.state.amountChildren,
      amountBabies: this.props.location.state.amountBabies,
      display: this.state.display,
      codeAirportdeparture: this.props.location.state.departure[0].codeAirport,
      codeDestination: this.props.location.state.destination[0].codeAirport
    };
    // console.log('push page', data);
    history.push('/people-infomation', data);
  };
  render() {
    console.log(this.state.itemFlightDestination);
    let departure = this.props.location.state.departure[0];
    let destination = this.props.location.state.destination[0];
    return (
      <div className="flight_information">
        <MenuCollection />
        <div className="header_air_ticket">
          <span>Thông tin chuyến bay</span>
        </div>
        <hr className="line-header" />

        <div className="box_info">
          <div className="box_info_left">
            {this.state.display ? (
              <React.Fragment style={style}>
                <div className="title_flight">
                  <div className="title_flight_left">
                    <span>Chuyến bay đi</span>
                  </div>
                  <div className="title_flight_right">
                    <div className="info_city">
                      <div className="content_info_city">
                        {departure.city} ({departure.codeAirport}){' '}
                        <span
                          style={{ color: '#666666', fontWeight: 'normal', fontSize: '0.9rem' }}
                        >
                          đến
                        </span>{' '}
                        <br />
                        {destination.city} ({destination.codeAirport})
                      </div>
                    </div>
                    <div className="date">
                      <div className="content_date">
                        <span className="weekdays">
                          {moment(this.props.location.state.startDate).format('dddd')},
                        </span>
                        <br />
                        <span className="weekdays">
                          {' '}
                          {moment(this.props.location.state.startDate).format('LL')}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <RadioGroup
                  aria-label="flight"
                  name="flight"
                  value={this.state.value}
                  // onChange={this.handleChange}
                  className="list_flight"
                >
                  {this.renderFlightList()}
                </RadioGroup>
              </React.Fragment>
            ) : (
              <React.Fragment style={style}>
                <div className="title_flight">
                  <div className="title_flight_left">
                    <span>Chuyến bay về</span>
                  </div>
                  <div className="title_flight_right">
                    <div className="info_city">
                      <div className="content_info_city">
                        {departure.city} ({departure.codeAirport}){' '}
                        <span
                          style={{ color: '#666666', fontWeight: 'normal', fontSize: '0.9rem' }}
                        >
                          đến
                        </span>{' '}
                        <br />
                        {destination.city} ({destination.codeAirport})
                      </div>
                    </div>
                    <div className="date">
                      <div className="content_date">
                        <span className="weekdays">
                          {moment(this.props.location.state.startDate).format('dddd')},
                        </span>
                        <br />
                        <span className="weekdays">
                          {' '}
                          {moment(this.props.location.state.startDate).format('LL')}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <RadioGroup
                  aria-label="flight"
                  name="flight"
                  value={this.state.value}
                  onChange={this.handleChange}
                  className="list_flight"
                >
                  {this.renderFlightList()}
                </RadioGroup>
              </React.Fragment>
            )}
            {this.state.itemFlightDestination && this.state.itemFlightDestination.id ? (
              <UI.Button
                type="button"
                name="Tiếp tục"
                className="btn_add"
                onClick={this.pushPage}
              />
            ) : (
              <UI.Button
                type="button"
                name="Tiếp tục"
                className="btn_add"
                onClick={this.showInfoFlight}
              />
            )}
          </div>
          <BoxInfoRight
            itemFlight={this.state.itemFlight}
            itemFlightDestination={this.state.itemFlightDestination}
            startDate={this.props.location.state.startDate}
            amountBeliever={this.props.location.state.amountBeliever}
            amountChildren={this.props.location.state.amountChildren}
            amountBabies={this.props.location.state.amountBabies}
            display={this.state.display}
            priceLuggageFirst={this.state.priceLuggageFirst}
            priceLuggageLast={this.state.priceLuggageLast}
            priceInsurrance={this.state.priceInsurrance}
          />
        </div>
      </div>
    );
  }
}
export default FlightInformation;
