class FormRegister extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visiblePass: false,
      loading: false,
      display: true,
      displayAll: false,
      messageError: '',
      district: [],
      listQuanHuyen: [],
      OTP: '',
      setOTP: '',
      disableBtnRegister: true,
      infoData: {}
    };
  }

  componentDidMount() {
    this.setState({
      errorMessage: this.props.errorMessage
    });
  }

  showPassword() {
    this.setState({ visiblePass: !this.state.visiblePass });
  }
  changeListCityToShowCBBox = (list: any) => {
    let data = [];

    list.forEach(element => {
      let push = false;
      data.forEach(item => {
        if (item.value === element.cityId) {
          push = true;
        }
      });

      if (!push) {
        data.push({
          value: element.cityId,
          label: element.cityName
        });
      }
    });
    return data;
  };
  onSuccess = data => {
    console.log('onSuccess', data);
    this.setState({
      infoData: data
    });
  };

  onHandleSubmitInfo = values => {
    const { nameCompany, phoneNumber, taxCode } = values;
    console.log(
      'onHandleSubmit Register',
      values,
      this.state.city,
      this.state.district,
      nameCompany,
      phoneNumber,
      taxCode
    );
    this.setState({
      display: false
    });
    let data = {
      info_user: values,
      city: this.state.city,
      district: this.state.district
    };
    this.onSuccess(data);
  };
  onHandleSubmitEnterPass = values => {
    const { password, rePassword } = values;
    let onSuccess = this.onSuccess;
    if (password !== rePassword) {
      console.log('onHandleSubmit Enter pass', password, rePassword);
      alert('mat khau khong khop');
    } else {
      let data = {
        infoData: this.state.infoData,
        password: password
      };
      this.setState({
        displayAll: true
      });
      console.log('onHandleSubmitEnterPass', data);
    }
  };
  validateRegister = (validate: FormikValues) => {
    let { nameCompany, phoneNumber, taxCode, address } = validate;
    let disableBtnRegister = true;
    console.log('validateRegister', validate, this.state.city, this.state.district);
    if (
      !nameCompany ||
      !phoneNumber ||
      !taxCode ||
      !address ||
      !this.state.city ||
      !this.state.district
    ) {
      disableBtnRegister = false;
    }
    this.setState({
      disableBtnRegister
    });
  };

  renderDropDownCity() {
    const classes = useStyles();
    const data = this.changeListCityToShowCBBox(listDistrict);
    return (
      <TextField
        id="standard-select-currency"
        name="city"
        select
        label="Tỉnh / Thành phố"
        className={classes.textField}
        value={this.state.city}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {data.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  changeListDistrictToShowCBBox = (list: any) => {
    let data = [];
    list.forEach(element => {
      data.push({
        value: element.id,
        label: element.name
      });
    });
    return data;
  };
  _onHandleChangeDistrict = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;

    this.setState(
      {
        [name]: value
      },
      () => {
        console.log('name', this.state.district);
      }
    );
  };
  _onHandleChange = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    let district = listDistrict.filter(item => {
      return item.cityId === value;
    });
    this.setState(
      {
        [name]: value,
        listQuanHuyen: this.changeListDistrictToShowCBBox(district)
      },
      () => {
        console.log('name', this.state.listQuanHuyen);
      }
    );
  };
  renderDropDownDistrict() {
    const classes = useStyles();
    const { listQuanHuyen } = this.state;
    return (
      <TextField
        id="standard-select-currency"
        name="district"
        label="Quận/ Huyện"
        select
        className={classes.textField}
        value={this.state.district}
        onChange={this._onHandleChangeDistrict}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listQuanHuyen.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  display = () => {
    this.setState({
      display: false
    });
  };
  displayAll = () => {
    this.setState(
      {
        displayAll: true
      },
      console.log('displayAll', this.state.displayAll)
    );
  };
  render() {
    return (
      <div className="form-login">
        <div style={{ display: this.state.displayAll ? 'none' : 'block' }}>
          <UI.Formik
            initialValues={{
              nameCompany: '',
              phoneNumber: '',
              taxCode: '',
              address: ''
            }}
            validate={this.validateRegister}
            validationSchema={UI.yup.object().shape({
              nameCompany: UI.yup.string().required('not null'),
              phoneNumber: UI.yup.string().required('not null'),
              taxCode: UI.yup.string().required('not null'),
              address: UI.yup.string().required('not null')
            })}
            onSubmit={this.onHandleSubmitInfo}
            render={props => (
              <form onSubmit={props.handleSubmit}>
                <div
                  className="info_user"
                  style={{ display: this.state.display ? 'block' : 'none' }}
                >
                  <ul className="box-login">
                    <li>
                      <UI.Field
                        {...props}
                        name="nameCompany"
                        type="text"
                        placeholder="Tên doanh nghiệp"
                        value={props.values.nameCompany}
                        onChange={props.handleChange}
                        autoComplete="off"
                        leftImage={icons.icon_company_name}
                      />
                    </li>
                    <li>
                      <UI.Field
                        {...props}
                        name="phoneNumber"
                        type="text"
                        placeholder="Số điện thoại"
                        value={props.values.phoneNumber}
                        onChange={props.handleChange}
                        autoComplete="off"
                        leftImage={icons.icon_phone}
                      />
                    </li>
                    <li>
                      <UI.Field
                        {...props}
                        name="taxCode"
                        type="text"
                        placeholder="Mã số thuế doanh nghiệp"
                        value={props.values.taxCode}
                        onChange={props.handleChange}
                        autoComplete="off"
                        leftImage={icons.icon_cmnd}
                      />
                    </li>
                  </ul>
                  <ul className="box-login input_dropdown">
                    <li>
                      <img src={icons.icon_location} className="img-responsive" alt="icon city" />
                      {this.renderDropDownCity()}
                    </li>
                    <li>
                      <img
                        src={icons.icon_location}
                        className="img-responsive"
                        alt="icon District"
                      />
                      {this.renderDropDownDistrict()}
                    </li>
                    <li>
                      <UI.Field
                        {...props}
                        name="address"
                        type="text"
                        placeholder="Địa chỉ"
                        value={props.values.address}
                        onChange={props.handleChange}
                        autoComplete="off"
                        leftImage={icons.icon_home}
                      />
                    </li>
                  </ul>
                  <UI.Button
                    type="submit"
                    name="Tiếp tục"
                    className="btn-login"
                    disabled={this.state.disableBtnRegister}
                  />
                </div>
              </form>
            )}
          />
        </div>
        <div style={{ display: this.state.displayAll ? 'none' : 'block' }}>
          <UI.Formik
            initialValues={{
              password: '',
              rePassword: ''
            }}
            validationSchema={UI.yup.object().shape({
              password: UI.yup.string().required('not null'),
              rePassword: UI.yup.string().required('not null')
            })}
            onSubmit={this.onHandleSubmitEnterPass}
            render={props => (
              <form onSubmit={props.handleSubmit}>
                <div
                  className="box_pass"
                  style={{ display: this.state.display ? 'none' : 'block' }}
                >
                  <ul className="box-login">
                    <li>
                      <UI.Field
                        {...props}
                        name="password"
                        type="password"
                        placeholder="Nhập mật khẩu"
                        value={props.values.password}
                        onChange={props.handleChange}
                        autoComplete="off"
                        leftImage={icons.icon_pass}
                      />
                    </li>
                    <li>
                      <UI.Field
                        {...props}
                        name="rePassword"
                        type="password"
                        placeholder="Nhập lại mật khẩu"
                        value={props.values.rePassword}
                        onChange={props.handleChange}
                        autoComplete="off"
                        leftImage={icons.icon_pass}
                      />
                    </li>
                  </ul>
                  <UI.Button
                    type="submit"
                    name="Tiếp tục"
                    className="btn-login"
                    // onClick={this.displayAll}
                  />
                </div>
              </form>
            )}
          />
        </div>
        <div className="box_otp" style={{ display: this.state.displayAll ? 'block' : 'none' }}>
          <p>Nhập mã OTP để xác nhận</p>
          <OTPInput
            value={this.state.OTP}
            onChange={item => {
              this.setState({ OTP: item }, () => {
                console.log('set otp', this.state.OTP, this.state.OTP.length);
              });
            }}
            autoFocus
            OTPLength={6}
            otpType="number"
            // disabled={true}
            secure
          />
          <Link className="btn_resent_otp">Không nhận được mã OTP</Link>
        </div>
      </div>
    );
  }
}
const mapDispatchToProps = (dispatch: Dispatch) => ({
  login: (payload: any) => dispatch(authActions.login(payload))
});

const mapStateToProps = state => ({
  signInFalse: state.auth.signInFalse
});

export default connect(mapStateToProps, mapDispatchToProps)(FormRegister);
