export const status = {
  start: '@@FETCH_START/',
  success: '@@FETCH_SUCCESS/',
  failure: '@@FETCH_FAILURE/',
  cancel: '@@CANCEL/'
};

export const removeDuplicates = (myArr: Array<any>, key: string) => {
  return myArr.filter((obj, pos, arr) => {
    return arr.map(mapObj => mapObj[key]).indexOf(obj[key]) === pos;
  });
};
