import { of, from } from 'rxjs';
import { ofType, ActionsObservable } from 'redux-observable';
import { takeUntil, catchError, switchMap } from 'rxjs/operators';
import { combineEpics } from 'redux-observable';
// import { Action } from 'c-redux';
import { types, actions } from './actions';
import { productAPIs } from '../../services';

interface Action {
  type: string;
  payload?: any;
}

const getProduct = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_PRODUCT),
    switchMap(action => {
      return from(productAPIs.getProduct()).pipe(
        switchMap((data: any) => {
          return of(actions.getProductSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_PRODUCT_CANCEL))),
        catchError(error => {
          console.log('getProduct', error);

          return of(actions.getProductFailure(error.message));
        })
      );
    })
  );
};

const getProductById = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_PRODUCT_ID),
    switchMap(action => {
      const payload = action.payload;

      return from(productAPIs.getProductById(payload)).pipe(
        switchMap((data: any) => {
          return of(actions.getProductIdSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_PRODUCT_ID_CANCEL))),
        catchError(error => {
          return of(actions.getProductIdFailure(error.message));
        })
      );
    })
  );
};

const createProduct = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.CREATE_PRODUCT),
    switchMap(action => {
      const payload = action.payload;
      const { data, onSuccess } = payload;
      return from(productAPIs.createProduct(data)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }
          return of(actions.createProductSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.CREATE_PRODUCT_CANCEL))),
        catchError(error => {
          return of(actions.createProductFailure(error.message));
        })
      );
    })
  );
};

const updateProduct = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.UPDATE_PRODUCT),
    switchMap(action => {
      const payload = action.payload;

      return from(productAPIs.editProduct(payload)).pipe(
        switchMap((data: any) => {
          console.log('data', data);

          return of(actions.updateProductSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.UPDATE_PRODUCT_CANCEL))),
        catchError(error => {
          return of(actions.updateProductFailure(error.message));
        })
      );
    })
  );
};

const deleteProduct = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.DELETE_PRODUCT),
    switchMap(action => {
      const payload = action.payload;
      const { data, onSuccess } = payload;
      return from(productAPIs.deleteProduct(data)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }
          return of(actions.deleteProductSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.DELETE_PRODUCT_CANCEL))),
        catchError(error => {
          return of(actions.deleteProductFailure(error.message));
        })
      );
    })
  );
};

export default combineEpics(
  getProduct,
  getProductById,
  createProduct,
  updateProduct,
  deleteProduct
);
