import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import SubMenu from '../../component/menuSell';
import { formatCurrency } from '../../../../commons/utils';
import { icons } from '../../../../assets';
import { history } from '../../../../redux/router';

import '../styles/CreateOrderSuccess.scss';
import { formatDateTimeHours } from '../../../../commons/utils/format';

class CreateOrderSuccess extends Component {
  makeid(length) {
    var result = '';
    var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return 'EC-' + result;
  }

  componentWillMount() {
    if (this.props.location.state) {
    } else {
      history.push('/');
    }
  }
  render() {
    const { item } = this.props.location.state;
    console.log('renderOn', this.state, this.props);

    return (
      <div className="create_order_success">
        <SubMenu />
        <div className="view_create_order">
          <div className="content_create_order">
            <img
              src={icons.logo_ecpay}
              className="img-responsive logo-success"
              alt="logo-success"
            />
            <p>Hoá đơn đã được thanh toán thành công</p>
            <div className="box-info-order">
              <div className="row-box-info-order">
                <div className="item-left">Mã giao dịch</div>
                <div className="item-right">
                  {item && item.transId ? item.transId : this.makeid(9)}
                </div>
              </div>
              <div className="row-box-info-order">
                <div className="item-left">Mã hoá đơn</div>
                <div className="item-right">#{item && item.code ? item.code : '1234'}</div>
              </div>
              <div className="row-box-info-order">
                <div className="item-left">Thời gian</div>
                <div className="item-right">
                  {item && item.expire ? formatDateTimeHours(item.expire) : '22/10/2019 - 10:31:32'}
                </div>
              </div>
              <div className="row-box-info-order">
                <div className="item-left">Loại giao dịch</div>
                <div className="item-right">
                  Thanh toán hoá đơn #{item && item.code ? item.code : '1234'}
                </div>
              </div>
              <div className="row-box-info-order">
                <div className="item-left">Số tiền</div>
                <div className="item-right">
                  {formatCurrency(item && item.amount ? item.amount : 0)}
                </div>
              </div>
            </div>

            <Link to="/" className="back-home">
              Về trang chủ
            </Link>
          </div>
        </div>
      </div>
    );
  }
}
export default CreateOrderSuccess;
