const listSchool = [
  {
    value: 1,
    label: 'THPT Ngôi Nhà Xanh'
  },
  {
    value: 2,
    label: 'THPT chuyên Lê Hồng Phong'
  }
];
const listTime = [
  {
    value: 1,
    label: '1989'
  },
  {
    value: 2,
    label: '1990'
  }
];

class FromInfoStudent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nameSchool: '',
      displayPopup: false,
      modalIsOpen: false,
      time: ''
    };
  }

  changeCategoryList = (list: any) => {
    let data = [];

    list.forEach(element => {
      data.push({
        value: element.id,
        label: element.storeName
      });
    });
    return data;
  };
  openModal = () => {
    this.setState({
      modalIsOpen: true
    });
  };
  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };
  _onHandleChange = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    this.setState(
      {
        [name]: value
      },
      () => {
        console.log('name', this.state.role);
      }
    );
  };
  handleChangeCheck = name => event => {
    console.log('handleChange');
    this.setState({ checkedB: !this.state.checkedB }, () => {
      console.log('checkB', this.state.checkedB);
    });
  };

  renderDropDownSchool() {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="nameSchool"
        select
        label=""
        className={classes.textField}
        value={this.state.nameSchool}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listSchool.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  renderDropDownTime() {
    const classes = useStyles();
    return (
      <TextField
        id="standard-select-currency"
        name="time"
        select
        label=""
        className={classes.textField}
        value={this.state.time}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listTime.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  onHandleSubmit = values => {
    console.log('onHandleSubmitInfoStudent', values, this.state);
    if (this.state.displayPopup) {
      this.setState({
        modalIsOpen: true,
        titlePopupNotification:
          'Thông tin học sinh/ sinh viên vừa nhập không đúng. Xin vui lòng kiểm tra lại'
      });
    } else {
      history.push('/search-tution', data);
    }
  };
  render() {
    return (
      <div className="from_info_student" style={style}>
        <UI.Formik
          initialValues={{ nameStudent: '' }}
          validationSchema={UI.yup.object().shape({
            nameStudent: UI.yup.string().required('not null')
          })}
          onSubmit={this.onHandleSubmit}
          render={props => (
            <form onSubmit={props.handleSubmit}>
              <div className="box-add-product">
                <div className="box-input">
                  <div className="title-product-code">Tìm theo trường:</div>
                  <div className="card-cover">{this.renderDropDownSchool()}</div>
                  <div className="err-caterory">
                    {(this.state && this.state.showErrorComboboxRole) || ''}
                  </div>
                </div>
                <div className="box-input">
                  <div className="title-product-code">Họ tên HS/SV</div>
                  <div className="card-cover">
                    <UI.Field
                      {...props}
                      name="nameStudent"
                      type="text"
                      placeholder="Họ tên HS/SV"
                      value={props.values.nameStudent}
                      onChange={props.handleChange}
                      autoComplete="off"
                    />
                  </div>
                </div>
              </div>
              <div className="box-input">
                <div className="title-product-code">Năm sinh:</div>
                <div className="card-cover">{this.renderDropDownTime()}</div>
                <div className="err-caterory">
                  {(this.state && this.state.showErrorComboboxRole) || ''}
                </div>
              </div>
              <UI.Button type="submit" name="Tiếp tục" className="btn-continue" />
            </form>
          )}
        />
        <Modal
          isOpen={this.state.modalIsOpen}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Example Modal"
          ariaHideApp={false}
        >
          <PopupNotification
            title={this.state.titlePopupNotification}
            onRequestClose={this.closeModal}
          />
        </Modal>
      </div>
    );
  }
}
export default FromInfoStudent;
