import { of, from } from 'rxjs';
import { ofType, ActionsObservable } from 'redux-observable';
import { takeUntil, catchError, switchMap } from 'rxjs/operators';
import { combineEpics } from 'redux-observable';
import { types, actions } from './actions';
import { DistrictAPIs } from '../../services';

interface Action {
  type: string;
  payload?: any;
}

const getDistrict = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_DISTRICT),
    switchMap(action => {
      return from(DistrictAPIs.getDistrict()).pipe(
        switchMap((data: any) => {
          return of(actions.getDistrictSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_DISTRICT_CANCEL))),
        catchError(error => {
          return of(actions.getDistrictFailure(error.message));
        })
      );
    })
  );
};

export default combineEpics(getDistrict);
