import { Route, Link } from 'react-router-dom';
import React from 'react';
import './CustomCss.css';

const CustomLink = ({ label, to, className, muiltipleTo, activeOnlyWhenExact, imgAlt, imgTop }) => (
  <Route
    path={muiltipleTo ? muiltipleTo : to}
    exact={activeOnlyWhenExact}
    children={({ match }) => (
      <li
        className={match ? `${className ? className : 'nav-menu active'}` : 'nav-menu'}
        style={{ listStyleType: 'none' }}
      >
        {imgAlt ? <img src={imgAlt} alt="logo-menu" className="logo-left" /> : null}
        {imgTop ? (
          <div>
            <img src={imgTop} alt="logo-menu" className="logo-top" />
            <br />
          </div>
        ) : null}
        <Link to={to} className={imgTop ? 'title-link' : ''}>
          {label}
        </Link>
      </li>
    )}
  />
);

export { CustomLink };
