const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200
  },
  menu: {
    width: 200
  }
}));
class UpdateProfile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      district: [],
      listQuanHuyen: [],
      logo: images.none_image
    };
  }
  changeListCityToShowCBBox = (list: any) => {
    let data = [];

    list.forEach(element => {
      let push = false;
      data.forEach(item => {
        if (item.value === element.cityId) {
          push = true;
        }
      });

      if (!push) {
        data.push({
          value: element.cityId,
          label: element.cityName
        });
      }
    });
    return data;
  };
  onSetState = (data: any) => {
    this.setState({ logo: data });
  };
  _fileChangedHandler = e => {
    e.stopPropagation();
    e.preventDefault();

    // FileList object.
    var files = e.target.files;

    var file = files[0];

    var fileReader = new FileReader();

    fileReader.onload = progressEvent => {
      var url = fileReader.result;

      // Something like: data:image/png;base64,iVBORw...Ym57Ad6m6uHj96js
      this.onSetState(url);

      var myImg = document.getElementById('myimage');
      myImg.src = url ? url : images.none_image;
    };

    // Read file asynchronously.
    fileReader.readAsDataURL(file); // fileReader.result -> URL.
  };

  renderDropDownCity() {
    const classes = useStyles();
    const data = this.changeListCityToShowCBBox(listDistrict);
    return (
      <TextField
        id="standard-select-currency"
        name="city"
        select
        className={classes.textField}
        value={this.state.city}
        onChange={this._onHandleChange}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {data.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }
  changeListDistrictToShowCBBox = (list: any) => {
    let data = [];
    list.forEach(element => {
      data.push({
        value: element.id,
        label: element.name
      });
    });
    return data;
  };
  _onHandleChangeDistrict = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;

    this.setState(
      {
        [name]: value
      },
      () => {
        console.log('name', this.state.district);
      }
    );
  };
  _onHandleChange = e => {
    const target = e.target;
    console.log('target', target);
    const name = target.name;
    const value = target.value;
    let district = listDistrict.filter(item => {
      return item.cityId === value;
    });
    this.setState(
      {
        [name]: value,
        listQuanHuyen: this.changeListDistrictToShowCBBox(district)
      },
      () => {
        console.log('name', this.state.listQuanHuyen);
      }
    );
  };
  renderDropDownDistrict() {
    const classes = useStyles();
    const { listQuanHuyen } = this.state;
    return (
      <TextField
        id="standard-select-currency"
        name="district"
        select
        className={classes.textField}
        value={this.state.district}
        onChange={this._onHandleChangeDistrict}
        SelectProps={{
          MenuProps: {
            className: classes.menu
          }
        }}
        margin="normal"
      >
        {listQuanHuyen.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    );
  }

  onHandleSubmit = values => {
    console.log('onHandleSubmit', values, this.state.city, this.state.district);
  };
  render() {
    return (
      <div className="update_profile">
        <div className="header_update_profile">
          <span>Thông tin cá nhân</span>
        </div>
        <hr className="line-header" />
        <UI.Formik
          initialValues={{
            nameProfile: '',
            nameCompany: '',
            phoneNumberProfile: '0975835662',
            emailProfile: '',
            phoneNumberCompany: '',
            address: '',
            taxCode: ''
          }}
          validationSchema={UI.yup.object().shape({
            nameCompany: UI.yup.string().required('not null'),
            emailProfile: UI.yup.string().required('not null'),
            nameProfile: UI.yup.string().required('not null'),
            phoneNumberCompany: UI.yup.string().required('not null'),
            address: UI.yup.string().required('not null'),
            taxCode: UI.yup.string().required('not null')
          })}
          onSubmit={this.onHandleSubmit}
          render={props => (
            <form onSubmit={props.handleSubmit}>
              <div className="titile-info-profile">
                Thông tin cá nhân
                <hr className="hr-info-company" />
                <div className="row-first">
                  <div className="box-input">
                    <div className="title-product-code">Họ tên:</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="nameProfile"
                        type="text"
                        placeholder="Họ tên"
                        value={props.values.nameProfile}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Email:</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="emailProfile"
                        type="email"
                        placeholder="Email"
                        value={props.values.emailProfile}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                </div>
                <div className="box-input number_phone_profile">
                  <div className="title-product-code">Số điện thoại</div>
                  <div className="card-cover">
                    <UI.Field
                      {...props}
                      name="phoneNumberProfile"
                      type="text"
                      placeholder="Số điện thoại"
                      value={props.values.phoneNumberProfile}
                      onChange={props.handleChange}
                      autoComplete="off"
                      disabled={true}
                    />
                  </div>
                </div>
              </div>
              <div className="titile-info-company">
                Thông tin doanh nghiệp
                <hr className="hr-info-company" />
              </div>
              <div className="box-info-company">
                <div className="box-left">
                  <div className="box-input">
                    <div className="title-product-code">Tên doanh nghiệp</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="nameCompany"
                        type="text"
                        placeholder="Tên doanh nghiệp"
                        value={props.values.nameCompany}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Số điện thoại</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="phoneNumberCompany"
                        type="text"
                        placeholder="Số điện thoại"
                        value={props.values.phoneNumberCompany}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>

                  <div className="box_address">
                    <div className="box-input item_address">
                      <div className="title-product-code">Tỉnh/ Thành phố:</div>
                      <div className="card-cover"> {this.renderDropDownCity()}</div>
                      <div className="err-caterory">
                        {(this.state && this.state.showErrorCombobox) || ''}
                      </div>
                    </div>
                    <div className="box-input item_address">
                      <div className="title-product-code">Quận/ Huyện:</div>
                      <div className="card-cover"> {this.renderDropDownDistrict()}</div>
                      <div className="err-caterory">
                        {(this.state && this.state.showErrorCombobox) || ''}
                      </div>
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Địa chỉ</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="address"
                        type="text"
                        placeholder="Địa chỉ"
                        value={props.values.address}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                  <div className="box-input">
                    <div className="title-product-code">Mã số thuế doanh nghiệp</div>
                    <div className="card-cover">
                      <UI.Field
                        {...props}
                        name="taxCode"
                        type="text"
                        placeholder="Mã số thuế doanh nghiệp"
                        value={props.values.taxCode}
                        onChange={props.handleChange}
                        autoComplete="off"
                      />
                    </div>
                  </div>
                </div>
                <div className="box-right">
                  <div className="content-box-right">
                    <p>Hình ảnh thương hiệu</p>

                    <div className="box-image">
                      <input
                        name="logo"
                        id="logo"
                        type="file"
                        style={{ display: 'none' }}
                        ref={ref => (this.myInput = ref)}
                        onChange={this._fileChangedHandler}
                      />
                      <div className="media-photo media-round" aria-hidden="true">
                        <img
                          id="myimage"
                          src={images.none_image}
                          width="210px"
                          alt="logo product"
                        />
                      </div>
                    </div>

                    <button
                      type="button"
                      className="btn btn-default"
                      onClick={e => this.myInput.click()}
                    >
                      Tải lên từ thư viện
                    </button>
                  </div>
                </div>
              </div>
              <div className="group-btn-pop-up">
                <UI.Button type="submit" name="Lưu" className="btn-pop-up" />
                <UI.Button type="button" name="Huỷ" className="btn-pop-up" />
              </div>
            </form>
          )}
        />
      </div>
    );
  }
}
export default UpdateProfile;
