import { of, from } from 'rxjs';
import { ofType, ActionsObservable } from 'redux-observable';
import { takeUntil, catchError, switchMap } from 'rxjs/operators';
import { combineEpics } from 'redux-observable';
// import { Action } from 'c-redux';
import { types, actions } from './actions';
import { accountAPIs } from '../../services';
import { history } from '../router';

import * as localStorage from '../../commons/localStorage';
import { StorageKey } from '../../commons/constants/storageKeys';
// import { showDialog } from '../../emitter';
// import { t } from '../../i18n';

interface Action {
  type: string;
  payload?: any;
}

const getAccountMe = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.ACCOUNT_ME),
    switchMap(action => {
      return from(accountAPIs.getAccountMe()).pipe(
        switchMap((data: any) => {
          localStorage.setStorageKey(StorageKey.FULL_NAME, data ? data.fullName : null);

          return of(actions.accountMeSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.ACCOUNT_ME_CANCEL))),
        catchError(error => {
          return of(actions.accountMeFailure(error.message));
        })
      );
    })
  );
};

const getAccount = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_ACCOUNT),
    switchMap(action => {
      return from(accountAPIs.getAccount()).pipe(
        switchMap((data: any) => {
          return of(actions.getAccountSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_ACCOUNT_CANCEL))),
        catchError(error => {
          return of(actions.getAccountFailure(error.message));
        })
      );
    })
  );
};

const getAccountById = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.GET_CATEGORY_ID),
    switchMap(action => {
      const payload = action.payload;

      return from(accountAPIs.getAccountId(payload)).pipe(
        switchMap((data: any) => {
          console.log('getAccountById', data);

          return of(actions.getAccountIdSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.GET_CATEGORY_ID_CANCEL))),
        catchError(error => {
          return of(actions.getAccountIdFailure(error.message));
        })
      );
    })
  );
};

const createAccount = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.CREATE_CATEGORY),
    switchMap(action => {
      const payload = action.payload;

      const { data, onSuccess } = payload;

      return from(accountAPIs.createAccount(data)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }
          return of(actions.createAccountSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.CREATE_CATEGORY_CANCEL))),
        catchError(error => {
          return of(actions.createAccountFailure(error.message));
        })
      );
    })
  );
};

const updateAccount = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.UPDATE_CATEGORY),
    switchMap(action => {
      const payload = action.payload;
      const { data, onSuccess } = payload;
      return from(accountAPIs.editAccount(data)).pipe(
        switchMap((data: any) => {
          console.log('data', data);
          if (onSuccess) {
            onSuccess(data);
          }

          return of(actions.updateAccountSuccess(data));
        }),
        takeUntil(action$.pipe(ofType(types.UPDATE_CATEGORY_CANCEL))),
        catchError(error => {
          return of(actions.updateAccountFailure(error.message));
        })
      );
    })
  );
};

