const preloadingAfterLogin = (action$: ActionsObservable<Action>) => {
  return action$.pipe(
    ofType(types.PRELOADING_AFTER_LOGIN),
    switchMap(action => {
      return from(Promise.all([accountAPIs.getAccountMe()])).pipe(
        switchMap(data => {
          localStorage.setStorageKey(StorageKey.FULL_NAME, data[0] ? data[0].fullName : null);

          history.push(`/overview`);

          return of(actions.preloadingAfterLoginSuccess(data));
        }),
        catchError(error => {
          return of(actions.preloadingAfterLoginFailure(error.message));
        })
      );
    })
  );
};

export default combineEpics(
  getAccountMe,
  preloadingAfterLogin,
  getAccount,
  getAccountById,
  createAccount,
  updateAccount,
  accountReset,
  accountChangePassword
);
