import { status } from '../helper';

export const types = {
  ACCOUNT_ME: `${status.start}ACCOUNT_ME`,
  ACCOUNT_ME_SUCCESS: `${status.success}ACCOUNT_ME`,
  ACCOUNT_ME_FAILURE: `${status.failure}ACCOUNT_ME`,
  ACCOUNT_ME_CANCEL: `${status.cancel}ACCOUNT_ME`,

  GET_ACCOUNT: `${status.start}GET_ACCOUNT`,
  GET_ACCOUNT_SUCCESS: `${status.success}GET_ACCOUNT`,
  GET_ACCOUNT_FAILURE: `${status.failure}GET_ACCOUNT`,
  GET_ACCOUNT_CANCEL: `${status.cancel}GET_ACCOUNT`,

  GET_ACCOUNT_ID: `${status.start}GET_ACCOUNT_ID`,
  GET_ACCOUNT_ID_SUCCESS: `${status.success}GET_ACCOUNT_ID`,
  GET_ACCOUNT_ID_FAILURE: `${status.failure}GET_ACCOUNT_ID`,
  GET_ACCOUNT_ID_CANCEL: `${status.cancel}GET_ACCOUNT_ID`,

  ACCOUNT_RESET: `${status.start}ACCOUNT_RESET`,
  ACCOUNT_RESET_SUCCESS: `${status.success}ACCOUNT_RESET`,
  ACCOUNT_RESET_FAILURE: `${status.failure}ACCOUNT_RESET`,
  ACCOUNT_RESET_CANCEL: `${status.cancel}ACCOUNT_RESET`,

  ACCOUNT_CHANGE_PASSWORD: `${status.start}ACCOUNT_CHANGE_PASSWORD`,
  ACCOUNT_CHANGE_PASSWORD_SUCCESS: `${status.success}ACCOUNT_CHANGE_PASSWORD`,
  ACCOUNT_CHANGE_PASSWORD_FAILURE: `${status.failure}ACCOUNT_CHANGE_PASSWORD`,
  ACCOUNT_CHANGE_PASSWORD_CANCEL: `${status.cancel}ACCOUNT_CHANGE_PASSWORD`,

  CREATE_ACCOUNT: `${status.start}CREATE_ACCOUNT`,
  CREATE_ACCOUNT_SUCCESS: `${status.success}CREATE_ACCOUNT`,
  CREATE_ACCOUNT_FAILURE: `${status.failure}CREATE_ACCOUNT`,
  CREATE_ACCOUNT_CANCEL: `${status.cancel}CREATE_ACCOUNT`,

  UPDATE_ACCOUNT: `${status.start}UPDATE_ACCOUNT`,
  UPDATE_ACCOUNT_SUCCESS: `${status.success}UPDATE_ACCOUNT`,
  UPDATE_ACCOUNT_FAILURE: `${status.failure}UPDATE_ACCOUNT`,
  UPDATE_ACCOUNT_CANCEL: `${status.cancel}UPDATE_ACCOUNT`,

  PRELOADING_AFTER_LOGIN: `${status.start}PRELOADING_AFTER_LOGIN`,
  PRELOADING_AFTER_LOGIN_SUCCESS: `${status.success}PRELOADING_AFTER_LOGIN`,
  PRELOADING_AFTER_LOGIN_FAILURE: `${status.failure}PRELOADING_AFTER_LOGIN`
};

export const actions = {
  accountMe: (payload?: any) => ({ type: types.ACCOUNT_ME, payload }),
  accountMeSuccess: (payload?: any) => ({ type: types.ACCOUNT_ME_SUCCESS, payload }),
  accountMeFailure: (payload?: any) => ({ type: types.ACCOUNT_ME_FAILURE, payload }),
  accountMeCancel: () => ({ type: types.ACCOUNT_ME_CANCEL }),

  getAccount: (payload?: any) => ({ type: types.GET_ACCOUNT, payload }),
  getAccountSuccess: (payload?: any) => ({ type: types.GET_ACCOUNT_SUCCESS, payload }),
  getAccountFailure: (payload?: any) => ({ type: types.GET_ACCOUNT_FAILURE, payload }),
  getAccountCancel: () => ({ type: types.GET_ACCOUNT_CANCEL }),

  getAccountId: (payload?: any) => ({ type: types.GET_ACCOUNT_ID, payload }),
  getAccountIdSuccess: (payload?: any) => ({ type: types.GET_ACCOUNT_ID_SUCCESS, payload }),
  getAccountIdFailure: (payload?: any) => ({ type: types.GET_ACCOUNT_ID_FAILURE, payload }),
  getAccountIdCancel: () => ({ type: types.GET_ACCOUNT_ID_CANCEL }),

  accountReset: (payload?: any) => ({ type: types.ACCOUNT_RESET, payload }),
  accountResetSuccess: (payload?: any) => ({ type: types.ACCOUNT_RESET_SUCCESS, payload }),
  accountResetFailure: (payload?: any) => ({ type: types.ACCOUNT_RESET_FAILURE, payload }),
  accountResetCancel: () => ({ type: types.ACCOUNT_RESET_CANCEL }),

  accountChangePassword: (payload?: any) => ({ type: types.ACCOUNT_CHANGE_PASSWORD, payload }),
  accountChangePasswordSuccess: (payload?: any) => ({
    type: types.ACCOUNT_CHANGE_PASSWORD_SUCCESS,
    payload
  }),
  accountChangePasswordFailure: (payload?: any) => ({
    type: types.ACCOUNT_CHANGE_PASSWORD_FAILURE,
    payload
  }),
  accountChangePasswordCancel: () => ({ type: types.ACCOUNT_CHANGE_PASSWORD_CANCEL }),

  createAccount: (payload?: any) => ({ type: types.CREATE_CATEGORY, payload }),
  createAccountSuccess: (payload?: any) => ({ type: types.CREATE_CATEGORY_SUCCESS, payload }),
  createAccountFailure: (payload?: any) => ({ type: types.CREATE_CATEGORY_FAILURE, payload }),
  createAccountCancel: () => ({ type: types.CREATE_CATEGORY_CANCEL }),

  updateAccount: (payload?: any) => ({ type: types.UPDATE_CATEGORY, payload }),
  updateAccountSuccess: (payload?: any) => ({ type: types.UPDATE_CATEGORY_SUCCESS, payload }),
  updateAccountFailure: (payload?: any) => ({ type: types.UPDATE_CATEGORY_FAILURE, payload }),
  updateAccountCancel: () => ({ type: types.UPDATE_CATEGORY_CANCEL }),

  preloadingAfterLogin: () => ({ type: types.PRELOADING_AFTER_LOGIN }),
  preloadingAfterLoginSuccess: (payload: any) => ({
    type: types.PRELOADING_AFTER_LOGIN_SUCCESS,
    payload
  }),
  preloadingAfterLoginFailure: (payload: any) => ({
    type: types.PRELOADING_AFTER_LOGIN_FAILURE,
    payload
  })
};
