import Immutable from 'seamless-immutable';
import { AppState } from '../state';

import { types } from './actions';

const initialState = Immutable({
  fetchingDistrict: 0,
  listDistrict: []
});

export const reducer = (
  state = initialState,
  { type, payload }: { type: string, payload?: any }
) => {
  switch (type) {
    case types.GET_DISTRICT:
      return state.set('fetchingDistrict', 1);
    case types.GET_DISTRICT_SUCCESS:
      return state.merge({ fetchingDistrict: 0, listDistrict: payload });
    case types.GET_DISTRICT_FAILURE:
      return state.set('fetchingDistrict', -1);
    case types.GET_DISTRICT_CANCEL:
      return state.set('fetchingDistrict', 0);

    default:
      return state;
  }
};

export const selectors = (state: AppState) => ({
  fetchingDistrict: state.district.getIn(['fetchingDistrict']),
  listDistrict: state.district.getIn(['listDistrict'])
});
