import React, { Component } from 'react';
import MenuCollection from '../../component/menuCollection';
import { formatCurrency } from '../../../../commons/utils';
import TransactionSuccess from '../../component/transactionSuccessful';
import { UI } from '../../../../components';
import { history } from '../../../../redux/router';
import './styles/bill_info_electric.scss';

const listPrepay = [
  {
    id: 1,
    name: 'Gói tiêu chuẩn',
    price: 110000,
    time: '3 tháng'
  },
  {
    id: 2,
    name: 'Gói nâng cao',
    price: 250000,
    time: '3 tháng'
  },
  {
    id: 3,
    name: 'Gói Ultimate',
    price: 395000,
    time: '3 tháng'
  }
];
class InfoBillTelevision extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectItems: [],
      display: true,
      itemPrepayId: listPrepay[0].id,
      selectItemPrepay: {},
      displayPrepay: false
    };
  }
  toggleClassPrepay = (id, item) => {
    this.setState({
      itemPrepayId: id,
      selectItemPrepay: item ? item : listPrepay[0]
    });
  };
  renderBill() {
    let { selectItems } = this.state;
    let result = '';
    if (this.props.location && this.props.location.state) {
      const listBill = this.props.location.state.dataBill.billEVN;
      result = listBill.map((item, index) => {
        return (
          <div className="item_bill" key={index}>
            <div className="content_item_bill">
              <div className="content_item_bill_left">
                <UI.CheckBox
                  // className="check_employee"
                  checked={selectItems.indexOf(item) !== -1}
                  label=""
                  value={item}
                  onChange={e => this.onchangeSelect(e, item)}
                />
                <span style={{ paddingLeft: '40px' }}>Kỳ {item.term}</span>
              </div>
              <span className="role_employee">{formatCurrency(item.amount)}</span>
            </div>
          </div>
        );
      });
      return result;
    }
  }
  renderBillPrepay() {
    let result = '';
    result = listPrepay.map((item, index) => {
      return (
        <div
          onClick={() => this.toggleClassPrepay(item.id, item)}
          className={
            this.state.itemPrepayId === item.id ? 'item_bill_prepay active' : 'item_bill_prepay'
          }
          key={index}
        >
          <div className="title_item_bill_prepay">{item.name}</div>
          <div className="content_item_bill_prepay">
            <div className="price_item_bill_prepay">{formatCurrency(item.price)}</div>
            <div className="time_item_bill_prepay">{item.time}</div>
          </div>
        </div>
      );
    });
    return result;
  }
  onchangeSelect = (select, data) => {
    const { count, selectItems } = this.state;
    if (select.target.checked) {
      selectItems.push(data);
      console.log(selectItems);
      this.setState({
        count: count + 1,
        selectItems
      });
    } else {
      selectItems.splice(selectItems.indexOf(data), 1);

      this.setState(
        {
          count: count - 1,
          checkAll: false,
          selectItems
        },
        () => {
          console.log('selectItems sau', selectItems);
        }
      );
    }
  };
  renderInfoCustomer() {
    let totalAmount = null;
    if (this.state.selectItems) {
      totalAmount = this.state.selectItems.map((item, index) => {
        return (totalAmount += item.amount);
      });
      console.log('totalAmount', totalAmount);
    }
    if (this.state.selectItemPrepay) {
      // totalAmount == this.state.selectItemPrepay.price;
      console.log('totalAmount', this.state.selectItemPrepay.price);
    }

    if (this.props.location && this.props.location.state) {
      const infoBill = this.props.location.state.dataBill;
      const infoProvider = this.props.location.state.infoProvider;

      return (
        <div className="box_info_customer">
          <div className="title_info_customer">
            <p>Thông tin khách hàng</p>
            <div className="item_info_customer">
              <div className="item_info_customer_left">Nhà cung cấp:</div>
              <div className="item_info_customer_right">{infoProvider.name}</div>
            </div>
            <div className="item_info_customer">
              <div className="item_info_customer_left">Mã khách hàng:</div>
              <div className="item_info_customer_right">
                {this.props.location.state.customerCode}
              </div>
            </div>
            <div className="item_info_customer">
              <div className="item_info_customer_left">Tên khách hàng:</div>
              <div className="item_info_customer_right">{infoBill.name}</div>
            </div>
            <div className="item_info_customer">
              <div className="item_info_customer_left">Số điện thoại:</div>
              <div className="item_info_customer_right">{infoBill.phoneByProvider}</div>
            </div>
            <div className="item_info_customer">
              <div className="item_info_customer_left">Địa chỉ:</div>
              <div className="item_info_customer_right">{infoBill.address}</div>
            </div>
          </div>
          <hr className="line-header" style={{ marginTop: '1rem', height: '2px' }} />
          {this.state.displayPrepay ? (
            <React.Fragment>
              <p>Vui lòng chọn gói cước</p>
              <div className="list_prepay">{this.renderBillPrepay()}</div>
            </React.Fragment>
          ) : (
            <React.Fragment>
              <p>Danh sách hoá đơn</p>
              <div className="list_bill">{this.renderBill()}</div>
            </React.Fragment>
          )}
          <hr className="line-header" style={{ marginTop: '1rem', height: '2px' }} />
          <div className="fees">
            <div className="fees_left">Phí:</div>
            <div className="fees_right">0</div>
          </div>
          <div className="fees">
            <div className="fees_left">Tổng số tiền cần thanh toán:</div>
            <div
              className="fees_right"
              style={{ color: '#f25366', fontSize: '1.2rem', fontWeight: 'bold' }}
            >
              {formatCurrency(totalAmount)}
            </div>
          </div>
        </div>
      );
    }
  }
  comeBack = () => {
    history.goBack();
  };
  transaction = () => {
    if (this.state.selectItems.length === 0) {
      alert('Vui lòng chọn kỳ thanh toán');
    } else {
      this.setState(
        {
          display: false
        },
        () => {
          console.log('onHandleSubmit', this.state.display);
        }
      );
      console.log('onHandleSubmit');
      // history.push('/transaction-success');
    }
  };

  renderUI() {
    if (this.state.display === true) {
      return (
        <React.Fragment>
          <div className="header_bill_electric">
            <span>Thông tin hoá đơn</span>
          </div>
          <hr className="line-header " />
          <div className="box_info_bill">{this.renderInfoCustomer()}</div>
          <div className="group-btn-pop-up">
            <UI.Button
              type="button"
              name="Quay lại"
              className="btn-pop-up come-back"
              onClick={this.comeBack}
            />
            <UI.Button
              type="submit"
              name="Xác nhận"
              className="btn-pop-up"
              onClick={this.transaction}
            />
          </div>
        </React.Fragment>
      );
    } else {
      return <TransactionSuccess title="Thanh toán hoá đơn truyền hình" />;
    }
  }

  render() {
    // console.log('info bill', this.props.location.state);

    return (
      <div className="info-bill-electric">
        <MenuCollection />
        {this.renderUI()}
      </div>
    );
  }
}
export default InfoBillTelevision;
