export const getFetchingStatus = () => {
  const state: any = store.getState();
  const fetchingLogin = authSelectors(state).fetchingLogin;
  const fetchingLogout = authSelectors(state).fetchingLogout;
  return fetchingLogin === 1 || fetchingLogout === 1;
};

