import React, { Component } from 'react';
import { icons } from '../../../../assets';
import { Link } from 'react-router-dom';
import Modal from 'react-modal';
import AddCaterory from './addCaterory';
import SubMenu from '../../component/menuSell';
import '../styles/categoryManagement.scss';

import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { categoryActions, categorySelectors } from '../../../../redux/category';
import { AppState } from '../../../../redux/state';
import { formatDateTime } from '../../../../commons/utils/format';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  // login: (payload: any) => dispatch(authActions.login(payload))
  getCategory: () => dispatch(categoryActions.getCategory()),
  getCategoryId: (payload: any) => dispatch(categoryActions.getCategoryId(payload)),
  createCategory: (payload: any) => dispatch(categoryActions.createCategory(payload)),
  editCategory: (payload: any) => dispatch(categoryActions.updateCategory(payload)),
  deleteCategory: (payload: any) => dispatch(categoryActions.deleteCategory(payload))
});

const mapStateToProps = (state: AppState) => ({
  // account: accountSelectors(state).account
  listCategory: categorySelectors(state).listCategory,
  categoryById: categorySelectors(state).categoryById
});

const customStyles = {
  content: {
    top: '53%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    padding: '0px'
  }
};
class CategoryManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalIsOpen: false,
      type: false,
      itemEdit: {}
    };
  }
  onDel = item => {
    console.log('item', item);
    this.onDeleteCategory(item);
  };
  onEdit = item => {
    console.log('onEdit', item);
    this.setState({
      itemEdit: item,
      type: true,
      modalIsOpen: true
    });
  };

  componentDidMount() {
    this.fetchData();
  }

  fetchData = () => {
    this.props.getCategory();
  };

  openModal = () => {
    this.setState({
      modalIsOpen: true
    });
  };
  closeModal = () => {
    this.setState({ modalIsOpen: false }, () => {
      console.log('closeModal');
    });
  };
  renderCategory() {
    let result = '';
    const { listCategory } = this.props;
    result = listCategory.map((item, index) => {
      return (
        <div className="box-category" key={index}>
          <div
            className="content-box-category"
            style={{ backgroundColor: index % 2 === 0 ? '#fbfcff' : 'white' }}
          >
            <div className="item-detail-caterory">
              <span>{item.id}</span>
            </div>
            <div className="item-detail-caterory name-caterory">
              <span>{item.categoryName}</span>
            </div>
            <div className="item-detail-caterory description-category">{item.categoryName}</div>
            <div className="item-detail-caterory created-time">
              {formatDateTime(item.createdTime)}
            </div>
            <div className="item-detail-caterory">
              <Link
                className="icon"
                to="#"
                onClick={() => {
                  this.onEdit(item);
                }}
              >
                <img
                  src={icons.icon_edit}
                  className="img-responsive"
                  alt="icon_edit"
                  style={{ height: '35px' }}
                />
              </Link>
              <Link
                className="icon"
                to="#"
                onClick={() => {
                  this.onDel(item);
                }}
              >
                <img
                  src={icons.icon_del}
                  className="img-responsive"
                  alt="icon_edit"
                  style={{ height: '35px' }}
                />
              </Link>
            </div>
          </div>
        </div>
      );
    });
    return result;
  }

  onCreateCategory = (name: string) => {
    const onSuccess = (data: any) => {
      this.closeModal();
      this.fetchData();
    };
    this.props.createCategory({ data: { categoryName: name }, onSuccess });
  };

  onEditCategory = (item: any) => {
    const onSuccess = (data: any) => {
      this.closeModal();
      this.fetchData();
    };
    console.log('onEdit', item);

    this.props.editCategory({ data: item, onSuccess });
  };

  onDeleteCategory = (item: any) => {
    const onSuccess = (data: any) => {
      this.closeModal();
      this.fetchData();
    };
    this.props.deleteCategory({ data: item.id, onSuccess });
  };

  render() {
    return (
      <div className="category-management">
        <SubMenu />
        <div className="content-category-management">
          <div className="header-category-management">
            <div className="header-category-management-left">
              <span>Quản lý nghành hàng</span>
            </div>
            <div className="header-category-management-right">
              <button
                type="button"
                className="btn btn-default add-category"
                onClick={() => this.openModal()}
              >
                <span className="title-add">Thêm danh mục mới</span>
                <div className="icon-add">
                  <i className="fa fa-plus icon_plus"></i>
                </div>
              </button>
            </div>
          </div>
          <hr className="line-header" />
          <div className="caterory-products">
            <div className="title-caterory-products">
              <div className="item-title-caterory">Mã danh mục</div>
              <div className="item-title-caterory name-caterory">Tên danh mục</div>
              <div className="item-title-caterory description-category">Mô tả</div>
              <div className="item-title-caterory created-time">Ngày tạo</div>
              <div className="item-title-caterory">Tuỳ chỉnh</div>
            </div>
          </div>
          <div className="list-category">{this.renderCategory()}</div>
        </div>
        <Modal
          isOpen={this.state.modalIsOpen}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Example Modal"
          ariaHideApp={false}
        >
          <AddCaterory
            closeModal={this.closeModal}
            onCreateCategory={this.onCreateCategory}
            onEditCategory={this.onEditCategory}
            itemEdit={this.state.itemEdit}
            type={this.state.type}
          />
        </Modal>
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(CategoryManagement);
