class ChooseEmployee extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectItems: []
    };
  }
  renderEmployee() {
    let { selectItems } = this.state;
    let result = '';
    result = listEmployee.map((item, index) => {
      let role = '';
      switch (item.role) {
        case 'manager':
          role = 'Quản lý';
          break;

        default:
          role = 'Nhân viên';
          break;
      }
      return (
        <div className="item_employee" key={index}>
          <div className="content_item_employee">
            <span>{item.fullName}</span>
            <span className="role_employee">{role}</span>
            <UI.CheckBox
              // className="check_employee"
              checked={selectItems.indexOf(item) !== -1}
              label=""
              value={item}
              onChange={e => this.onchangeSelect(e, item)}
            />
          </div>
        </div>
      );
    });
    return result;
  }
  addEmployee = () => {
    this.props.addEmployee(this.state.selectItems);
    this.props.closeModal();
  };

  onchangeSelect = (select, data) => {
    const { count, selectItems } = this.state;
    if (select.target.checked) {
      selectItems.push(data);
      console.log(selectItems);
      this.setState(
        {
          count: count + 1,
          selectItems
        },
        () => {
          console.log(this.state.count);
        }
      );
    } else {
      selectItems.splice(selectItems.indexOf(data), 1);

      this.setState(
        {
          count: count - 1,
          checkAll: false,
          selectItems
        },
        () => {
          console.log('selectItems sau', selectItems);
        }
      );
    }
  };
  render() {
    return (
      <div className="choose_employee">
        <div className="viewClose">
          <div className="icon-close">
            <img src={icons.icon_close} alt="" onClick={() => this.props.closeModal()} />
          </div>
        </div>
        <div className="title_add_employee">
          <span>Chọn nhân viên tại điểm bán</span>
        </div>
        <hr className="line-header" />
        <div className="list_employee">{this.renderEmployee()}</div>

        <button
          type="button"
          className="btn btn-default btn_choose_employee"
          onClick={this.addEmployee}
        >
          Chọn
        </button>
      </div>
    );
  }
}
export default ChooseEmployee;
