import Immutable from 'seamless-immutable';
import { AppState } from '../state';

import { types } from './actions';

const initialState = Immutable({
  fetchingBill: 0,
  listBill: [],
  BillById: {},
  fetchingBillById: 0,
  actionFetching: 0,
  createSuccess: {}
});

export const reducer = (
  state = initialState,
  { type, payload }: { type: string, payload?: any }
) => {
  switch (type) {
    case types.GET_BILL_ALL:
      return state.set('fetchingBill', 1);
    case types.GET_BILL_ALL_SUCCESS:
      return state.merge({ fetchingBill: 0, listBill: payload });
    case types.GET_BILL_ALL_FAILURE:
      return state.set('fetchingBill', -1);
    case types.GET_BILL_ALL_CANCEL:
      return state.set('fetchingBill', 0);

    case types.GET_BILL_PAGE:
      return state.set('fetchingBill', 1);
    case types.GET_BILL_PAGE_SUCCESS:
      return state.merge({ fetchingBill: 0, listBill: payload });
    case types.GET_BILL_PAGE_FAILURE:
      return state.set('fetchingBill', -1);
    case types.GET_BILL_PAGE_CANCEL:
      return state.set('fetchingBill', 0);

    case types.GET_BILL_SEARCH:
      return state.set('fetchingBill', 1);
    case types.GET_BILL_SEARCH_SUCCESS:
      return state.merge({ fetchingBill: 0, listBill: payload });
    case types.GET_BILL_SEARCH_FAILURE:
      return state.set('fetchingBill', -1);
    case types.GET_BILL_SEARCH_CANCEL:
      return state.set('fetchingBill', 0);

    case types.GET_BILL_CODE:
      return state.set('fetchingBill', 1);
    case types.GET_BILL_CODE_SUCCESS:
      return state.merge({ fetchingBill: 0, listBill: payload });
    case types.GET_BILL_CODE_FAILURE:
      return state.set('fetchingBill', -1);
    case types.GET_BILL_CODE_CANCEL:
      return state.set('fetchingBill', 0);

    case types.GET_BILL_ID:
      return state.set('fetchingBillById', 1);
    case types.GET_BILL_ID_SUCCESS:
      return state.merge({ fetchingBillById: 0, BillById: payload });
    case types.GET_BILL_ID_FAILURE:
      return state.set('fetchingBillById', -1);
    case types.GET_BILL_ID_CANCEL:
      return state.set('fetchingBillById', 0);

    case types.POST_BILL_ID:
      return state.set('actionFetching', 1);
    case types.POST_BILL_ID_SUCCESS:
      return state.merge({ actionFetching: 0, createSuccess: payload });
    case types.POST_BILL_ID_FAILURE:
      return state.set('actionFetching', -1);
    case types.POST_BILL_ID_CANCEL:
      return state.set('actionFetching', 0);

    default:
      return state;
  }
};

export const selectors = (state: AppState) => ({
  fetchingBill: state.bill.getIn(['fetchingBill']),
  fetchingBillById: state.bill.getIn(['fetchingBillById']),
  actionFetching: state.bill.getIn(['actionFetching']),
  listBill: state.bill.getIn(['listBill']),
  BillById: state.bill.getIn(['BillById']),
  createSuccess: state.bill.getIn(['createSuccess'])
});
