import { combineReducers } from 'redux';

import { reducer as authReducer } from './auth/reducer';
import { reducer as accountReducer } from './account/reducer';
import { reducer as productReducer } from './product/reducer';
import { reducer as categoryReducer } from './category/reducer';
import { reducer as orderReducer } from './order/reducer';
import { reducer as billReducer } from './bill/reducer';
import { reducer as districtReducer } from './district/reducer';
import { reducer as appReducer } from './app/reducer';
export default combineReducers({
  auth: authReducer,
  account: accountReducer,
  product: productReducer,
  category: categoryReducer,
  order: orderReducer,
  bill: billReducer,
  district: districtReducer,
  app: appReducer
});
